import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { DOM, format, sendXhr } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
Existing code: yes
Remove code: yes
Stackable: yes
Free gift support: no
Free shipping: no
'isValid' implemented: no
Codes can be replaced by another code: no
Manually apply code: no
Is driver SPA and add the condition to show popup: yes
     - is on cart url or is on checkout(payment)
     - is total element
Pages, where extension works: `Cart` and `Checkout(payment, review)`
Gather codes on site pages: yes
sessionTimer: 20min
Special case about the driver works:
On bag page we need to calculate the total price with tax to get correct discount (because the discount applies to the tax)
Credentials (joq@gmail.com, 12345qwerty)
we cannot implement code testing at all steps of the checkout due to data loss (we cannot correctly apply)
*/

const CART_PAGE_URL = 'https://www.rockport.com/cart';
const BASE_URL = 'https://www.rockport.com/on/demandware.store/Sites-RockportUS-Site/default';
const CART_FORM_SELECTOR = '#cart-items-form';
const isCartPage = () => window.location.href.startsWith(CART_PAGE_URL) && document.querySelector(CART_FORM_SELECTOR);

const isOnBillingPage = document.querySelector('.billing-coupon-code') || !document.querySelector('#cart-items-form');
const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = isCartPage() ? '.cart-right .order-value' : '.order-totals-table .order-value';
		this.SUB_TOTAL =
			'.cart-order-totals .order-subtotal td:last-child, .place-order-totals .order-subtotal td:last-child';
		this.isStackable = true;
		this.APPLY_REQUEST_HEADERS = this.BILLING_COUPON_SELECTOR
			? { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' }
			: '';

		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = isOnBillingPage
			? `${BASE_URL}/COSummary-Start?onepageentry=true&format=ajax`
			: CART_PAGE_URL;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.MERCHANT_CODES_SELECTOR = '.promo-link .promo-text';
		this.MERCHANT_CODES_REGEX = /code\s(\w+)/i;

		this.INPUT_SELECTOR = '.cart-coupon-code #dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '.cart-coupon-code #add-coupon';

		this.CHECKOUT_PAGE_SELECTOR = '.pt_onepage_checkout';
		this.CART_PAGE_SELECTOR = '#cart-items-form';
		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.CODES_REGEXP = /couponcode':\s"(\w+)/gi;
		this.ACTIVE_BILLING = '.onepage-billing:not(.disabled)';

		sessionTimer.initialize({ timeout: 20 });
	}

	checkCondition() {
		const isCart = document.querySelector(this.CART_PAGE_SELECTOR);
		const isCheckout = document.querySelector(this.CHECKOUT_PAGE_SELECTOR);
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const isTotal = totalElem && this.parseTotal();
		const isPayment = isCheckout && document.querySelector(this.ACTIVE_BILLING);
		return (isCart || isPayment) && isTotal;
	}

	getMerchantCodes() {
		return (this._isOnCartPage() ? Promise.resolve(null) : this.getCartPage()).then(responseHTML => {
			this.lastResponse = responseHTML;
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
						.map(elem => elem.innerText.match(this.MERCHANT_CODES_REGEX))
						.filter(Boolean)
						.map(code => code[1]),
				),
			];
		});
	}

	getExistingCodes() {
		return (this.lastResponse ? Promise.resolve(this.lastResponse) : this.getCartPage()).then(res => {
			const codes = [...new Set([...res.matchAll(this.CODES_REGEXP)].map(code => code[1]))];
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			return elemToFloat(extractElement(this.SUB_TOTAL, response));
		});
	}

	_isOnCartPage() {
		return isCartPage();
	}

	_sendRequest(method, requestURL, headers, queryParams, bodyData) {
		const finalRequestURL =
			queryParams && queryParams.toString() ? `${requestURL}?${queryParams.toString()}` : requestURL;
		return sendXhr(finalRequestURL, {
			method,
			credentials: 'include',
			headers,
			body: bodyData,
		}).then(response => response.text());
	}
};

export default {
	name: 'Rockport',
	domain: 'rockport.com',
	stackable: true,
	maxCoupons: 10,
	Driver,
	cartContinueUrl: isOnBillingPage ? `${BASE_URL}/COCustomer-Start` : CART_PAGE_URL,
	isSinglePageApp: true,
};
