import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';
import { extractElement } from '../../../utils/DOM';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Don't support Checkout due to clear data, can't avoid Cart
+ Is driver SPA: no
+ Gather codes on site pages: no
+ credentials: josephq@gmail.com, 12345qwerty
*/

const CART_PAGE_URL = 'https://www.ylighting.com/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.ordertotal .value';
		this.APPLIED_CODES_SELECTOR = '.submitedcoupon, .coupon-applied-block li';
		this.PRODUCT_ITEM_SELECTOR = 'tr.cartproductrow, .ordersummary';
		this.isStackable = true;

		const BASE_URL = 'https://www.ylighting.com/on/demandware.store/Sites-YLighting-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCLI`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.PROPOSED_CODES_URL = `${BASE_URL}/Page-GetPromosOnly`;

		this.CART_CODE_REGEX = /Promo Code \w+: ((\w+))?/;

		this.FREE_GIFT_MSG_SELECTOR = '.cartproductrow .promo';
		this.FREE_GIFT_MSG_REGEX = /applied for a free gift/;

		this.PROPOSED_CODES_SELECTOR = '.promoDetailContainer p strong';

		this.CART_FORM_SELECTOR = '#dwfrm_cart';

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'coupon';
	}

	getMerchantCodes() {
		return this._getProposedCoupons().then(responseText => {
			if (!responseText) {
				return Promise.resolve();
			}
			return [...extractElement(this.PROPOSED_CODES_SELECTOR, responseText, true)].map(el =>
				el.textContent.trim(),
			);
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(codeElement => codeElement.innerText.match(this.CART_CODE_REGEX))
			.filter(Boolean)
			.map(match => match[1]);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	parseItemsCount(html) {
		return extractElement(this.PRODUCT_ITEM_SELECTOR, html, true).length + this._parseFreeGiftMsg(html).length;
	}

	// there are some codes which don't increase item count only add msg that code with free gift is applied
	_parseFreeGiftMsg(html) {
		return [...extractElement(this.FREE_GIFT_MSG_SELECTOR, html, true)]
			.map(el => el.textContent.match(this.FREE_GIFT_MSG_REGEX))
			.filter(Boolean);
	}

	_getProposedCoupons() {
		return this._sendRequest('GET', this.PROPOSED_CODES_URL);
	}
};

export default {
	name: 'Ylighting',
	domain: 'ylighting.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cartempty'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_PAGE_URL) && document.querySelector('.cart-content');
	},
};
