import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { DOM, format, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML, extractElement, extractFormValues } = DOM;

export default class DemandwareAJAXDriver extends BaseDriver {
	// This class could be used if merchant uses AJAX `GET` requests to manage promo codes
	// Could be used too if on cart page POST request is used to remove code
	constructor() {
		super();

		// Should be defined in driver
		this.APPLY_CODE_URL = '';
		this.REMOVE_CODE_URL = '';
		this.UPDATE_REQUEST_URL = '';

		this.APPLIED_CODES_SELECTOR = null;
		this.PRODUCT_ITEM_SELECTOR = null;

		// Redefine in driver in case it differs
		this.isStackable = false;
		this.TOTAL_SELECTOR = '.order-total td:last-child';

		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		// Keep empty if no code is passed in remove query params
		this.REMOVE_CODE_PARAM_NAME = '';
		this.REMOVE_COUPON_PARAM_VALUE = 'Remove';

		this.CODES_ELEMENTS_SELECTOR = '.rowcoupons';
		this.CODE_VALUE_SELECTOR = '.cartcoupon .value';
		this.CART_FORM_SELECTOR = '#cart-items-form';

		this.DEFAULT_REMOVE_CODE_VALUE = 'dwfrm_cart_coupons_i0_deleteCoupon';
		this.REMOVE_CODE_SELECTOR = '[name*="_deleteCoupon"]';

		this.CART_PAGE_URL = '';

		// Redefine in driver in case they are needed
		this.APPLY_REQUEST_HEADERS = {};
		this.REMOVE_REQUEST_HEADERS = {};
		this.UPDATE_REQUEST_HEADERS = {};

		// Set required static params in driver in case they are present
		this.applyQueryParams = new URLSearchParams();
		this.removeQueryParams = new URLSearchParams();
		this.updateQueryParams = new URLSearchParams();

		// Will try to check if code was applied if JSON response received,
		// to avoid sending update request if code was not applied.
		// If sequence of keys were matched to compare value or keys were not found,
		// update request will be send

		// Sequence corresponds to responseJSON.<key1>.<key2>...
		// Default to `responseJSON.success`
		this.APPLY_RESPONSE_KEY = 'success';

		// Value extracted from JSON will be compared with this value
		this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE = true;

		// In case codes may replace each other
		// we should reapply the best codes after codes removal to correctly stack next one
		this.REAPPLY_CODES_WHILE_STACKING = false;
	}

	// Base Driver methods
	applyCodes(codes) {
		return this.isStackable ? Promise.resolve() : this.applyCode(codes[0]);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	getExistingCodes() {
		return Promise.resolve()
			.then(() => {
				if (this._isOnCartPage()) {
					return document.documentElement.innerHTML;
				}
				return this.getCartPage();
			})
			.then(html => {
				const appliedCodesSelector = this.APPLIED_CODES_SELECTOR
					? this.APPLIED_CODES_SELECTOR
					: this.CODE_VALUE_SELECTOR;
				const codes = [...extractElementFromHTML(html, appliedCodesSelector, true)].map(element =>
					element.innerText.trim(),
				);

				return {
					codes,
					codeCount: codes.length,
				};
			});
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => this.getOrderSummary())
			.then(response => ({
				originalPrice: this.parseTotal(response),
				originalItemsCount: this.parseItemsCount(response),
			}));
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice, originalItemsCount } = validationInfo;

		// Sort by discount and free gifts count
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code)
				.then(() => this.getOrderSummary())
				.then(responseHTML => {
					const finalPrice = this.parseTotal(responseHTML);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(responseHTML) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});

					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this.removeCode(discount.code).then(() => {
						if (this.REAPPLY_CODES_WHILE_STACKING) {
							return Promise.each(successfulCodes, code => this.applyCode(code));
						}
						return Promise.resolve();
					});
				}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	// Demandware AJAX Driver methods
	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);

		return this._sendRequestGET(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	removeCode(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	removeCodeOnCart(code) {
		// Need to get cart page to retrieve form data with a token each time a code was applied
		return this.getCartPage().then(responseHTML => {
			const removeParamName = this._getRemoveValueName(code, responseHTML);
			if (!removeParamName) {
				// No need in the reuqest, as parameter name was not found for the code
				return Promise.resolve();
			}

			const removeRequestData = extractFormValues(this.CART_FORM_SELECTOR, responseHTML);
			removeRequestData.params.set(removeParamName, this.REMOVE_COUPON_PARAM_VALUE);

			return this._sendRequestPOST(removeRequestData.action, {}, null, removeRequestData.params);
		});
	}

	getOrderSummary() {
		return this._sendRequestGET(this.UPDATE_REQUEST_URL, this.UPDATE_REQUEST_HEADERS, this.updateQueryParams);
	}

	getCartPage() {
		return this._sendRequestGET(this.CART_PAGE_URL, {});
	}

	parseTotal(html) {
		const totalElement = extractElement(this.TOTAL_SELECTOR, html);
		return elemToFloat(totalElement);
	}

	parseItemsCount(html) {
		return this.PRODUCT_ITEM_SELECTOR ? extractElement(this.PRODUCT_ITEM_SELECTOR, html, true).length : 0;
	}

	_sendRequestGET(requestURL, headers, queryParams) {
		return this._sendRequest('GET', requestURL, headers, queryParams);
	}

	_sendRequestPOST(requestURL, headers, queryParams, bodyData) {
		return this._sendRequest('POST', requestURL, headers, queryParams, bodyData);
	}

	_sendRequest(method, requestURL, headers, queryParams, bodyData) {
		const finalRequestURL =
			queryParams && queryParams.toString() ? `${requestURL}?${queryParams.toString()}` : requestURL;

		return fetch(finalRequestURL, {
			method,
			credentials: 'include',
			headers,
			body: bodyData,
		})
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}

	_getValueFromJSON(json) {
		if (!this.APPLY_RESPONSE_KEY) {
			return null;
		}

		return Object.prototype.hasOwnProperty.call(json, this.APPLY_RESPONSE_KEY)
			? json[this.APPLY_RESPONSE_KEY]
			: null;
	}

	_getRemoveValueName(code, html) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, html, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.trim().toLowerCase() === code.toLowerCase();
		});

		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).name
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL);
	}
}
