import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

const CART_PAGE_URL = 'https://www.southmoonunder.com/view-cart';
const CHECKOUT_URL_CONDITIONS = ['shipping', 'checkout'];
const TOTAL_SELECTOR = '.cartTotals.cartTotalsLast .cartSubTotal.cartVal, .ordertotal .value';

const onCart = () => window.location.href.toLowerCase().startsWith(CART_PAGE_URL);
const onCheckout = () => CHECKOUT_URL_CONDITIONS.some(urlPart => window.location.href.toLowerCase().includes(urlPart));

/*
Driver behavior:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: yes
•   Free gift support: no
•   isValid implemented: no
•   Show message about free shipping: no
•   Driver is SPA: no
•   Gather merchant codes: no
•   Special case: driver work on cart & checkout
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = TOTAL_SELECTOR;
		this.APPLIED_CODES_SELECTOR = '.coupon .value';
		this.isStackable = true;

		const BASE_URL = 'https://www.southmoonunder.com/on/demandware.store/Sites-SMU-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = 'https://www.southmoonunder.com/view-cart';
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.CART_FORM_SELECTOR = '#dwfrm_cart';
		this.APPLIED_COUPONS_SELECTOR = '.coupon .value';
		this.CODES_ELEMENTS_SELECTOR = '.coupon';
		this.CODE_VALUE_SELECTOR = '.coupon .value';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('dwfrm_cart_coupons_i0_deleteCoupon', 'Remove');
		this.REAPPLY_CODES_WHILE_STACKING = true;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_COUPONS_SELECTOR)].map(element =>
			element.textContent.trim(),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		// total doesn't always update on the page
		return this._sendRequestGET(window.location.href).then(responseTxt => this.parseTotal(responseTxt));
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'South Moon Under',
	domain: 'southmoonunder.com',
	stackable: true,
	completionAction: completionActions.revisit, // avoid alert on cart
	Driver,
	isCartEmpty() {
		return !!document.querySelector('.emptycart');
	},
	isOnCartUrl() {
		return (onCart() || onCheckout()) && [...document.querySelectorAll(TOTAL_SELECTOR)].some(el => el.offsetParent);
	},
};
