import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { completionActions, log } from '../../../utils';

const CHECKOUT_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

/* Behaviour description:
-    Existing code: yes
-    Remove code: yes
-    Stackable: no
-    Free gift support: only on the cart page
-    ‘isValid’ implemented: yes
Optional:
-    Is driver SPA and add the condition to show popup: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_PAGE_URL = 'https://www.vince.com/cart';

		this.BASE_URL = 'https://www.vince.com/on/demandware.store/Sites-vince-Site/default';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-Start`;

		this.APPLY_CODE_BTN_SELECTOR = '#add-coupon';

		this.PRODUCT_ITEM_SELECTOR = '.cart-row';

		this.applyQueryParams.set('format', 'ajax');

		this.CART_FORM_SELECTOR = '#cart-items-form';

		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.isStackable = true;
	}

	beforeTestCodes() {
		this.checkoutInput = document.querySelector(CHECKOUT_INPUT_SELECTOR);

		return Promise.resolve();
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: false,
			};
			return this.applyCode(code).then(applyCodeResponse => {
				if (applyCodeResponse.success) {
					result.isValid = true;
				}

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		}).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'Vince',
	domain: 'vince.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl() {
		// there can be different urls
		return document.querySelector('#dwfrm_cart_couponCode') || document.querySelector(CHECKOUT_INPUT_SELECTOR);
	},
};
