import DemandwareAJAXDriver from './_base';
import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: cart and checkout
+ Is driver SPA: yes
+ Gather codes on site pages: yes
*/

const CART_FORM_SELECTOR = 'form[name="dwfrm_cart"]';
const CHECKOUT_FORM_SELECTOR = '#dwfrm_billing';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .orderTotal';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping  td:last-child';

		this.isStackable = true;

		this.BASE_URL = 'https://www.hottopic.com/on/demandware.store/Sites-hottopic-Site/default/';
		this.APPLY_CODE_URL = `${this.BASE_URL}Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}COBilling-UpdateSummary`;
		this.CART_PRODUCT_ITEM_SELECTOR = '.cart-row, .mini-cart-product';
		this.ITEM_PRICE_SELECTOR = '.price-total, .price-original';
		this.DONATION_SELECTOR = '[title*=Donation]';
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.MERCHANT_PROMO_SELECTOR = '.html-slot-container .text--bold';
		this.MERCHANT_PROMO_REGEX = /use code: (\w+)/i;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	getMerchantCodes() {
		return [document.querySelector(this.MERCHANT_PROMO_SELECTOR)]
			.filter(code => Boolean(code) && code.textContent.match(this.MERCHANT_PROMO_REGEX))
			.map(code => code.textContent.match(this.MERCHANT_PROMO_REGEX)[1].trim());
	}

	getExistingCodes() {
		const ON_CART_SELECTOR = '.coupon-applied > span';
		const ON_CHECKOUT_SELECTOR = '.promo-li > span:not([class*="promo-text"])';
		const selector = document.querySelector(CART_FORM_SELECTOR) ? ON_CART_SELECTOR : ON_CHECKOUT_SELECTOR;

		const codes = Array.from(document.querySelectorAll(selector)).map(element => element.innerText.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const donation = [...document.querySelectorAll(this.CART_PRODUCT_ITEM_SELECTOR)]
				.filter(itemEl => itemEl.querySelector(this.DONATION_SELECTOR))
				.map(donationEl => (donationEl.querySelector(this.ITEM_PRICE_SELECTOR) || {}).offsetParent)
				.filter(Boolean)
				.map(el => elemToFloat(el))
				.reduce((cur, prev) => cur + prev, 0);
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			return this.parseTotal(responseHTML) - shipping - tax - donation;
		});
	}
};

export default {
	name: 'Hot Topic',
	domain: 'hottopic.com',
	stackable: true,
	Driver,
	isOnCartUrl() {
		// For this merchant it is safer to check form existence
		return document.querySelector(CART_FORM_SELECTOR) || document.querySelector(CHECKOUT_FORM_SELECTOR);
	},
};
