import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';

const { toFloat } = format;
const { extractElement } = DOM;

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: no
•    Manually apply code: no
•    Active pages: cart, checkout (payment -- only when checkbox is checked and review steps)
Optional:
•    Show message about free shipping on the cart page: no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: yes
•    Credentials: ypa@mail.com / Temporary_1 */

const BASE_URL = 'https://www.pier1.com/on/demandware.store/Sites-pier1_us-Site/default';
const CART_PAGE_URL = `${BASE_URL}/Checkout-Start`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODE_VALUE_SELECTOR = '.coupon-code span';
		this.TOTAL_SELECTOR = '.order-total .orderdetails-pricing';

		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Checkout-Basket`;
		this.CART_UPDATED_URL = `${BASE_URL}/Checkout-Start`;

		this.PROPOSED_CODE_REGEX = /code\s+(\w+)/;

		this.isStackable = true;
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.applyQueryParams.set('imgrole', 'productLine');

		this.PAYMENT_CHECKBOX_SELECTOR = '#use-ship-box-in-page';

		this.INPUT_CODES_SELECTOR = '[name="coupon_code"]';
		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.PROPOSED_CODE_SELECTOR = '.checkout-banner-text-row strong, .checkout-fs-banner .message';
	}

	getMerchantCodes() {
		return this._getUpdatedCart().then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.PROPOSED_CODE_SELECTOR, responseHTML, true)]
						.map(el => el.textContent.match(this.PROPOSED_CODE_REGEX))
						.filter(Boolean)
						.map(el => el[1]),
				),
			];
		});
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCart = document.querySelector(this.CART_EMPTY_SELECTOR);
		const promoField = document.querySelector(this.INPUT_CODES_SELECTOR);
		const paymentCheckbox = document.querySelector(this.PAYMENT_CHECKBOX_SELECTOR);
		const isCheckboxChecked = href.includes('/Checkout-Start#c_2')
			? paymentCheckbox && paymentCheckbox.checked
			: true;
		const promoURL =
			href.startsWith(BASE_URL) &&
			(href.includes('/Checkout-Start#c_0') ||
				href.includes('/Checkout-Start#c_2') ||
				href.includes('/Checkout-Start#c_3'));

		return !emptyCart && promoURL && promoField && isCheckboxChecked;
	}

	parseTotal(data) {
		if (data) {
			return toFloat(data.order_total.value);
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).innerText);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseJSON => {
			const shipping = responseJSON.shipping_total.value;
			const taxes = responseJSON.tax_total.value;
			return this.parseTotal(responseJSON) - toFloat(taxes || 0) - toFloat(shipping || 0);
		});
	}

	_getUpdatedCart() {
		return this._sendRequestGET(this.CART_UPDATED_URL, this.UPDATE_REQUEST_HEADERS);
	}
};

export default {
	name: 'Pier1',
	domain: 'pier1.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
};
