import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { extractElement } from '../../../utils/DOM';
import { log } from '../../../utils';
import { elemToFloat } from '../../../utils/format';

/**
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support:  no
+ Free shipping: no
+ ‘isValid’ implemented:  no
+ Codes can be replaced by another code: no
+ Pages, where extension works:
   - cart
   - billing page
+ Is driver SPA: no
+ Gather codes on site pages:  no
+ Merchant has a bug. On some codes, merchant can show a bigger discount than we have on the page.
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '.cart-v2-coupon-block div span.v2-small.v2-semi-bold';

		this.TOTAL_SELECTOR =
			'.order-total .order-value, #cart-v2-order-totals-body .cart-v2-actions span:last-of-type';

		const BASE_URL = 'https://www.claires.com/on/demandware.store/Sites-clairesNA-Site/en_US/';
		this.APPLY_CODE_URL = `${BASE_URL}Cart-CheckCoupon`;

		this.CART_PAGE_URL = 'https://www.claires.com/us/shopping-cart/';
		this.REMOVE_CODE_URL = 'https://www.claires.com/us/shopping-cart-coupon/';
		this.UPDATE_REQUEST_URL = this.REMOVE_CODE_URL;

		this.APPLY_RESPONSE_KEY = null;

		this.applyQueryParams.set('format', 'ajax');

		this.APPLY_REQUEST_HEADERS = {
			accept: 'application/json, text/javascript, */*; q=0.01',
			'x-requested-with': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
		this.UPDATE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.CHECKOUT_INPUT_SELECTOR = '#dwfrm_billing_couponCode';
		this.CHECKOUT_BUTTON_SELECTOR = '#add-coupon';

		this.REAPPLY_CODES_WHILE_STACKING = true;
		this.MERCHANT_CODES_SELECTOR = '#homepage-promotion-banners span b';
		this.MERCHANT_CODES_REGEX = /:\s([\d\w]+)/;
		this.CHARGES_SELECTOR = '#cart-v2-order-totals-body span:last-child, .order-totals-table td:last-child';
		this.DELIVERY_REGEX = /delivery|shipping/i;
		this.CHARGES_REGEX = /delivery|tax|shipping|subtotal/i;
	}

	getMerchantCodes() {
		return this._sendRequestGET('https://www.claires.com/us/').then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
						.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
						.filter(Boolean)
						.map(el => el[1]),
				),
			];
		});
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => this.getCartPage())
			.then(response => ({
				originalPrice: this.parseTotal(response),
				originalItemsCount: this.parseItemsCount(response),
			}));
	}

	removeCode(code) {
		return this.getCartPage().then(responseHTML => {
			const removeParamName = this._getRemoveValueName(code, responseHTML);
			if (!removeParamName) {
				// No need in the reuqest, as parameter name was not found for the code
				return Promise.resolve();
			}

			this.removeQueryParams.set(removeParamName, 'Remove');

			return this._sendRequestPOST(
				this.REMOVE_CODE_URL,
				{ 'x-requested-with': 'XMLHttpRequest' },
				null,
				this.removeQueryParams,
			);
		});
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: false,
				};

				if (!applyCodeResponse.success) {
					// Code was not applied
					return bus.$emit('code-checked', result);
				}

				return this.getCartPage().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						result.isValid = true;
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this.getCartPage();
		const total = this.parseTotal(cartStr);
		const chargesElem = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)];

		const allTaxes = chargesElem
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping might not be counted on cart
		const delivery = chargesElem
			.filter(el => el.parentElement.textContent.match(this.DELIVERY_REGEX))
			.map(elemToFloat)
			.filter(Boolean);
		// when user test codes on the checkout page we can't send request to this page and get all charges due to the very big body in the request.
		// also on the cart page tax is not present in the page but it is counted to the total price
		// so we send request to the cart page, get from there all subtotal, shipping and subtract from the total it to determine tax value
		const tax = total - allTaxes;
		return total - tax - delivery;
	}
};

export default {
	name: "Claire's",
	domain: 'claires.com',
	Driver,
	isCartEmpty() {
		const emptyCart = !document.querySelector(
			'.order-total .order-value, #cart-v2-order-totals-body .cart-v2-actions span:last-of-type',
		);
		const header = document.querySelector('#primary h1');

		const path = window.location.pathname.toLowerCase();
		const errorPage =
			header &&
			header.innerText === 'An Error Occurred!' &&
			(path.includes('/shopping-cart-coupon') || path.includes('/us/checkout-billing/'));

		return errorPage || emptyCart;
	},
	isOnCartUrl(href) {
		const hrefLowerCase = href.toLowerCase();
		return (
			hrefLowerCase.startsWith('https://www.claires.com/us/shopping-cart') ||
			hrefLowerCase.startsWith('https://www.claires.com/us/checkout-billing')
		);
	},
};
