import DemandwareAJAXDriver from './_base';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - valid code can't be replaced with another valid or invalid code
 * - can't apply multiple codes (isn't stackable)
 * */

const BASE_URL = 'https://www.store.crossfit.com/on/demandware.store/Sites-Reebok-CF-Site/en_CY';
const CART_PAGE_URL = `${BASE_URL}/Cart-Show`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.ordertotal.bg .summary_value';
		this.APPLIED_CODES_SELECTOR = '[name="dwfrm_cart_coupons_i0_deleteCoupon"]';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-Submits`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Cart-Show`;
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.isStackable = false;

		this.REMOVE_CODE_PARAM_NAME = 'dwfrm_cart_coupons_i0_deleteCoupon';
		this.APPLY_CODE_PARAM_NAME = 'dwfrm_cart_couponCode';
		this.applyQueryParams.set('dwfrm_cart_addCoupon', 'Apply');
		this.applyQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(codeElement =>
			codeElement.value.replace('Remove', '').trim(),
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, `Remove ${code}`);
		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'CrossFit Store',
	domain: 'store.crossfit.com',
	Driver,
	isOnCartUrl(href) {
		return (
			(href.startsWith(BASE_URL) && href.includes('CODelivery-Start')) ||
			href.includes('COSummary2-Start') ||
			href.includes('COPayment-HostedPaymentContinue') ||
			href.includes('Cart-Show')
		);
	},
	isCartEmpty() {
		const TITLE_ELEMENT = document.querySelector('.checkout-title');
		return TITLE_ELEMENT ? TITLE_ELEMENT.innerText.includes('IS EMPTY') : false;
	},
};
