import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Driver behavior:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: yes
•   Free gift support: no
•   isValid implemented: no
•   Show message about free shipping: no
•   Driver is SPA: yes, when genie is on right cart or checkout url and cart is not empty
•   Gather merchant codes: no
======== Special case ========
• pages:
- cart urls
- /checkout (payment)
- /expresscheckout (can't get existing and remove code when we have applied code)
*/

const BASE_CART_URL = 'https://www.marmot.com';
const CART_PAGE_URL = `${BASE_CART_URL}/cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.PAGE_TOTAL_SELECTOR = '[class*=footer] .order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value, .redemption.coupon [data-couponcode]';

		this.MERCHANT_CODES_SELECTOR = '.header-promotion span';
		this.MERCHANT_CODES_REGEX = /with\scode\s(\w+)/i;

		this.isStackable = true;

		const BASE_URL = 'https://www.marmot.com/on/demandware.store/Sites-Marmot_US-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/ExpressCheckout-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		// Activate extension popup only when the code input is visible and we're on checkout
		const { href } = window.location;
		const isInput = (extractElement('#dwfrm_cart_couponCode, .promo-codes-fieldset.code-payment-fieldset') || {})
			.offsetParent;
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const onCart = href.startsWith(CART_PAGE_URL);
		const onCheckout = href.startsWith(BASE_CART_URL) && href.match(/cart-submitform|cart-addtowishlist|checkout/i);
		const secureCheckoutLink = href.includes('/expresscheckout');
		const secureCheckoutForm = (extractElement('.express-checkout-form') || {}).offsetParent;
		return (((onCart || onCheckout) && isInput) || (secureCheckoutLink && secureCheckoutForm)) && !emptyCart;
	}

	getMerchantCodes() {
		return this._getHomePageHTML().then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
						.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
						.filter(Boolean)
						.map(el => el[1]),
				),
			];
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(codeElement =>
				codeElement && codeElement.getAttribute('data-couponcode')
					? codeElement.getAttribute('data-couponcode')
					: codeElement.innerText.trim(),
			)
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeApplyCodes() {
		// replace destructive URL
		if (window.location.href.includes('Cart-SubmitForm')) {
			window.history.replaceState(window.history.state, window.document.title, this.CART_PAGE_URL);
		}
		return Promise.resolve();
	}

	parseTotal(html) {
		// total selector on the page differs from the one in the response
		return html ? super.parseTotal(html) : elemToFloat(extractElement(this.PAGE_TOTAL_SELECTOR));
	}

	_getHomePageHTML() {
		return fetch(`${BASE_CART_URL}/`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Marmot',
	domain: 'marmot.com',
	completionAction: completionActions.revisit,
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
