/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_isOnCartPage", "applyCodes", "_codeAppliedCheck", "watchForCartUrl"] }] */
import Promise from 'bluebird';

import DemandwareDriver from './_base';
import { DOM, completionActions } from '../../../utils';
import { elemToFloat } from '../../../utils/format';

const { extractData, extractFormValues, extractElement } = DOM;

const MERCHANT_URL = 'https://www.christopherandbanks.com';
const CART_PAGE_URL = `${MERCHANT_URL}/cart`;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
    - Cart page
    - Checkout page (all steps except 'Shipping and Address' step)
+ Is driver SPA yes/no
    - right URL
	- not empty
	- right step on the checkout
+ Gather codes on site pages: no
+ Credentials (for signing in)
*/

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.REMOVE_PARAMS = extractFormValues('#cart-items-form');

		this.TOTAL_SELECTOR = '.order-value ';

		this.isStackable = true;

		this.DEFAULT_REMOVE_CODE_VALUE = '';

		const BASE_URL = `${MERCHANT_URL}/on/demandware.store/Sites-CBKB2C-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');

		this.REMOVE_COUPON_PARAM_VALUE = 'delete';

		this.APPLY_RESPONSE_KEY = null;

		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.SHIPPING_STEP_SELECTOR = '#dwfrm_singleshipping_shippingAddress';
		this.isOnCartPage = () => window.location.href.startsWith(CART_PAGE_URL);

		this.CHARGES_REGEX = /(shipping|tax)/i;
		this.CHARGES_SELECTOR = '.order-totals-table .value';

		this.CSRF_TOKEN_SELECTOR = '[name*=csrf_token]';
	}

	checkCondition() {
		const isOnCartUrl = this.isOnCartPage() || window.location.href.startsWith(`${MERCHANT_URL}/checkout`);
		const isShippingStep = (document.querySelector(this.SHIPPING_STEP_SELECTOR) || {}).offsetParent;
		const emptyCart = document.querySelector(this.CART_EMPTY_SELECTOR);
		return isOnCartUrl && !emptyCart && !isShippingStep;
	}

	beforeTestCodes() {
		return this.getCartPage().then(response => {
			const csrfToken = [...extractElement(this.CSRF_TOKEN_SELECTOR, response, true)].reduce(item => item.value)
				.value;

			return this.applyQueryParams.set('csrf_token', csrfToken);
		});
	}

	getExistingCodes() {
		return (this.isOnCartPage() ? Promise.resolve(null) : this._sendRequestGET(this.CART_PAGE_URL, {})).then(
			responseHTML => {
				const codes = extractData(this.CODE_VALUE_SELECTOR, responseHTML, true).map(code => code.trim());

				return {
					codes,
					codeCount: codes.length,
				};
			},
		);
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this.getOrderSummary();
		const total = this.parseTotal(cartStr);

		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(el => (el.textContent.toLowerCase().trim() !== 'free' ? elemToFloat(el) : 0))
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0); // shipping & tax might not be counted on cart
		return total - taxes;
	}

	_getRemoveValueName(code, html) {
		const matchingCodesElement = [...extractElement(this.CODES_ELEMENTS_SELECTOR, html, true)].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.trim().toLowerCase() === code.toLowerCase();
		});

		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).name
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}
};

export default {
	name: 'Christopher & Banks',
	domain: 'christopherandbanks.com',
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
};
