import DemandwareAJAXDriver from './_base';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - can apply multiple codes (stackable)
 * - isValid - yes
 *
 * ======== Special case ========
 * have to reload checkout page to apply code, can't avoid loss of data
 * apply codes only on Cart and wish list page
 * */

const CART_PAGE_URL = 'https://www.slumberland.com/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.isStackable = true;

		const BASE_URL = 'https://www.slumberland.com/on/demandware.store/Sites-slumberland-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'Slumberland Furniture',
	domain: 'slumberland.com',
	stackable: true,
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return (
			href.startsWith(CART_PAGE_URL) ||
			href.startsWith(
				'https://www.slumberland.com/on/demandware.store/Sites-slumberland-Site/default/Cart-AddToWishlist',
			)
		);
	},
};
