import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { log, request } from '../../../utils';
import bus from '../../bus';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support: yes
•    ‘isValid’ implemented: yes
•    can run code testing only on Cart page
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no */

const CART_PAGE_URL = 'https://www.paulaschoice.com/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = false;

		this.BASE_URL = 'https://www.paulaschoice.com/on/demandware.store/Sites-paulaschoice_us-Site/en_US';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Bag-AddCoupon`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/Bag-RemoveCoupon`;
		this.CODE_VALUE_SELECTOR = '.normal.uppercase';
		this.UPDATE_REQUEST_URL =
			'https://www.paulaschoice.com/on/demandware.store/Sites-paulaschoice_us-Site/en_US/Bag-Get';

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.EMPTY_CART_SELECTOR = '.large3.normalcase';

		this.REQUEST_HEADERS = {
			Accept: 'application/json, text/plain, */*',
			'Content-Type': 'application/json;charset=UTF-8',
		};
	}

	checkCondition() {
		const onCart = window.location.href.startsWith('https://www.paulaschoice.com/cart');
		const emptyElem = document.querySelector(this.EMPTY_CART_SELECTOR);
		const cartEmpty = emptyElem && emptyElem.textContent.includes('no items');
		return onCart && !cartEmpty;
	}

	async beforeTestCodes() {
		const responseJSON = await this.sendRequest(
			'https://www.paulaschoice.com/on/demandware.store/Sites-paulaschoice_us-Site/en_US/Customer-GetCSRFToken',
			JSON.stringify({}),
		);
		this.csrfToken = responseJSON.data.csrfToken;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
			.filter(Boolean)
			.map(codeElement => codeElement.innerText);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code));
	}

	beforeCheckCodes() {
		return this._sendGETRequest().then(responseJSON => {
			return {
				originalPrice: this._parseTotal(responseJSON),
				originalItemsCount: this._parseItemsCount(responseJSON),
			};
		});
	}

	checkCodes(codes, originalPrice, originalItemsCount) {
		log(`Original price: ${originalPrice}`);
		log('originalItemsCount: %s', originalItemsCount);
		const workingDiscounts = [];

		return Promise.each(codes, code =>
			this.applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};
				if (responseJSON.success === false) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this._parseItemsCount(responseJSON) - originalItemsCount;
				if (result.finalDiscount > 0 || result.freeGifts > 0) {
					workingDiscounts.push(result);
				}

				return this.removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { codes, discounts, originalPrice, originalItemsCount } = validationInfo;

		discounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentGifts = 0;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(codes, code =>
			this.applyCode(code).then(response => {
				const finalPrice = this._parseTotal(response);
				const finalDiscount = originalPrice - finalPrice;
				const freeGifts = this._parseItemsCount(response) - originalItemsCount;

				bus.$emit('code-stacked', { code, finalPrice, finalDiscount, freeGifts });
				if (finalPrice < currentPrice || freeGifts > currentGifts) {
					// we can worsen the price with the code that gives the gift with less discount
					const priceIncreased = finalPrice > currentPrice;
					if (!priceIncreased) {
						successfulCodes.push(code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentGifts = freeGifts;
					}
					return Promise.resolve();
				}
				return this.removeCode(code);
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentGifts,
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this._sendGETRequest().then(responseJSON => {
			const taxValue = responseJSON.bag.tax ? responseJSON.bag.tax.value : 0;
			const shippingValue = responseJSON.bag.shippingTotal ? responseJSON.bag.shippingTotal.value : 0;
			const totalValue = this._parseTotal(responseJSON);
			return totalValue - taxValue - shippingValue;
		});
	}

	applyCode(code) {
		return this._manageCode(code, this.APPLY_CODE_URL);
	}

	removeCode(code) {
		return this._manageCode(code, this.REMOVE_CODE_URL);
	}

	_manageCode(code, url) {
		const bodyData = JSON.stringify({
			couponCode: code,
			csrfToken: this.csrfToken,
		});
		return this.sendRequest(url, bodyData);
	}

	_sendGETRequest() {
		return request.get('https://www.paulaschoice.com/cart?customer=true').then(response => response.json());
	}

	sendRequest(url, bodyData) {
		return request
			.post(url, bodyData, {
				headers: this.REQUEST_HEADERS,
				withCredentials: false,
			})
			.then(response => response.json());
	}

	_parseItemsCount(json) {
		if (json.data) {
			return json.data.items.length;
		}
		return json.bag.items.length;
	}

	_parseTotal(json) {
		if (json.data) {
			return json.data.amountDue.value;
		}
		return json.bag.amountDue.value;
	}
};

export default {
	name: 'Paulas Choice',
	domain: 'paulaschoice.com',
	Driver,
	isSinglePageApp: true,
	stackable: true,
};
