import DemandwareAJAXDriver from './_base';

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - can apply multiple codes (stackable)
 * */

const CART_PAGE_URL = 'https://www.parkerny.com/bag';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.isStackable = true;

		const BASE_URL = 'https://www.parkerny.com/on/demandware.store/Sites-parkerny-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');

		this.APPLY_BUTTON_SELECTOR = '#add-coupon';

		this.CART_EMPTY_SELECTOR = '.cart-empty';
	}

	checkCondition() {
		const emptyCartElement = document.querySelector(this.CART_EMPTY_SELECTOR);
		const addCouponButton = document.querySelector(this.APPLY_BUTTON_SELECTOR);
		const correctPagesParts = ['shipping', 'billing', 'Cart-SubmitForm'];
		const { href } = window.location;
		const correctPage = href.startsWith(CART_PAGE_URL) || correctPagesParts.some(urlPart => href.includes(urlPart));
		const currentState = addCouponButton && !emptyCartElement && correctPage;
		return currentState;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'Parker',
	domain: 'parkerny.com',
	stackable: true,
	Driver,
	isSinglePageApp: true,
	isOnCartUrl: () => false,
};
