import DemandwareAJAXDriver from './_base';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.wolverine.com/on/demandware.store/Sites-wolverine_us-Site/default';

		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/Cart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/Cart-UpdateBasketTotals`;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.isStackable = true;
	}

	getExistingCodes() {
		let codes = [];
		if (window.location.pathname.includes('/cart')) {
			codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(elem => elem.innerText.trim());
		} else {
			const codesElements = [...document.querySelectorAll('[data-couponcode]')].map(el =>
				el.getAttribute('data-couponcode'),
			);
			codes = [...new Set(codesElements)];
		}

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Wolverine',
	domain: 'wolverine.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.wolverine.com/US/en/cart') || href.includes('billing');
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
