import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';
import { DOM } from '../../../utils';
import { elemToFloat } from '../../../utils/format';

const { extractElement } = DOM;

const CART_PAGE_URL = 'https://www.modells.com/cart';
const BILLING_PAGE_URL = 'https://www.modells.com/billing';
const isOnCartPage = window.location.pathname.toLowerCase().includes('/cart');

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Billing
+ Is driver SPA: yes
+ Gather codes on site pages: no
 * */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLY_CODES_SELECTOR = isOnCartPage ? '.cartcoupon .value' : '.coupon .success';

		this.CODE_REGEX_CART = /\S(\w+)/;
		this.CODE_REGEX_CHECKOUT = /Coupon code \S(\w+)/;

		this.TOTAL_SELECTOR = '.value.order-value';
		this.SHIPPING_SELECTOR = '.order-shipping .value';
		this.TAX_SELECTOR = '.order-sales-tax .value';

		const BASE_URL = 'https://www.modells.com/on/demandware.store/Sites-Modells-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BILLING_PAGE_URL}page`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.EMPTY_CART_SELECTOR = '.cart__empty';
		this.CODE_INPUT_SELECTOR = '[name="dwfrm_cart_couponCode"], [name="dwfrm_billing_couponCode"]';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'old');
	}

	checkCondition() {
		const isCodeInputElem = (document.querySelector(this.CODE_INPUT_SELECTOR) || {}).offsetParent;
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		return isCodeInputElem && !emptyCart;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLY_CODES_SELECTOR)].map(el => {
			const codeRegex = isOnCartPage ? this.CODE_REGEX_CART : this.CODE_REGEX_CHECKOUT;
			return el.textContent.match(codeRegex)[1];
		});

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeApplyCodes() {
		window.history.pushState(window.history.state, window.document.title, CART_PAGE_URL);
		return Promise.resolve();
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}
};

export default {
	name: 'Modells',
	domain: 'modells.com',
	Driver,
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
	cartContinueUrl: isOnCartPage ? window.location.href : 'https://www.modells.com/billing',
};
