import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import { DOM, format, log } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractElement, extractElementFromHTML } = DOM;
const { elemToFloat } = format;
/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Payment
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from this url: https://www.jessicalondon.com/offers-coupons
*/

const BASE_URL = 'https://www.jessicalondon.com';
const CART_URL = `${BASE_URL}/cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = false;
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';

		this.CHARGES_REGEX = /(handling:|tax)/i;
		this.CHARGES_SELECTOR = '.order-totals-table .order-detail span.value:last-child';

		this.BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-oss-Site/default`;
		this.APPLY_CODE_URL = `${this.BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.BASE_REQUEST_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.ERROR_MSG_SELECTOR = '.error.coupon-code-error';
		this.REMOVE_ATTRIBUTE_SELECTOR = '#remove-coupon';
		this.MERCHANT_CODE_SELECTOR = '.promo-code b, .oc-coupon-code';
		this.COUPONS_PAGE_URL = `${BASE_URL}/offers-coupons`;

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
		this.UPDATE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		this.MAX_WAIT_TIME = 15;
	}

	watchForCartUrl() {
		sessionTimer.initialize({
			actionInterval: super.watchForCartUrl(),
			timeout: this.MAX_WAIT_TIME,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl =
			href.includes('/cart') ||
			href.includes('/Cart-SubmitForm') ||
			href.includes('/checkout') ||
			href.includes('/shipping') ||
			href.includes('/billing') ||
			href.startsWith(`${this.BASE_REQUEST_URL}/Cart-AddToWishlist`);
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return isRightUrl && isCodeInput;
	}

	getMerchantCodes() {
		return this._getCouponsPage().then(responseHTML => [
			...new Set(
				[...extractElement(this.MERCHANT_CODE_SELECTOR, responseHTML, true)]
					.map(el => el.textContent)
					.filter(el => Boolean(el) && el.toLowerCase() !== 'no code needed')
					.map(code => code.toUpperCase()),
			),
		]);
	}

	getExistingCodes() {
		return Promise.resolve()
			.then(() => {
				if (this._isOnCartPage()) {
					return document.documentElement.innerHTML;
				}
				return this.getCartPage();
			})
			.then(html => {
				const appliedCodesSelector = this.APPLIED_CODES_SELECTOR
					? this.APPLIED_CODES_SELECTOR
					: this.CODE_VALUE_SELECTOR;
				const codes = [...extractElementFromHTML(html, appliedCodesSelector, true)].map(element =>
					element.innerText.trim(),
				);

				return {
					codes: codes.map(code => code.toUpperCase()),
					codeCount: codes.length,
				};
			});
	}

	removeCodes(codes) {
		return Promise.each(codes, async code => {
			const responseSammary = await this._getUpdatedCart();
			return this.removeCode(code, responseSammary);
		})
			.then(() => this.getOrderSummary())
			.then(response => ({
				originalPrice: this.parseTotal(response),
			}));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};

				if (this._codeAppliedCheck(applyCodeResponse)) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(async orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					const cartResponse = await this._getUpdatedCart();
					const idElement = [...extractElement('.cartcoupon span', cartResponse, true)].find(
						el => el.textContent.trim().toLowerCase() === code.toLowerCase(),
					);
					// if no idElement - code is not applied
					if (!idElement) {
						result.isValid = false;
						return bus.$emit('code-checked', result);
					}

					return this.removeCode(code, cartResponse).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	async getPreTaxShippingTotal() {
		const cartStr = await this.getOrderSummary();
		const total = this.parseTotal(cartStr);
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0);
		return total - taxes;
	}

	removeCode(code, cartResponse) {
		const couponId = [...extractElement('.cartcoupon span', cartResponse, true)].find(
			el => el.textContent.trim().toLowerCase() === code.toLowerCase(),
		).nextElementSibling.name;
		return fetch(`${this.BASE_REQUEST_URL}/Cart-SubmitForm`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'content-type': 'application/x-www-form-urlencoded',
			},
			body: `dwfrm_cart_couponCode=&${couponId}=Remove`,
		}).then(response => response.text());
	}

	_getUpdatedCart() {
		return fetch(CART_URL, {
			method: 'GET',
		}).then(response => response.text());
	}

	_getCouponsPage() {
		return fetch(this.COUPONS_PAGE_URL, {
			method: 'GET',
		}).then(response => response.text());
	}

	_codeAppliedCheck(data) {
		const errorMsg = extractElement(this.ERROR_MSG_SELECTOR, data);
		return errorMsg && errorMsg.textContent ? errorMsg.textContent.match(/invalid or expired/i) : false;
	}
};

export default {
	name: 'Jessica London',
	domain: 'jessicalondon.com',
	isSinglePageApp: true,
	Driver,
};
