import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';
import { handleDriverError } from '../../lib';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: yes
•   Free gift support: no
•   isValid implemented: no
•   Show message about free shipping: no
•   Driver is SPA: yes, on cart & checkout-payment
•   Gather merchant codes: no
•   Special case:
•		- after visiting PayPal checkout page, coupon codes can be neither applied nor removed.
•		As a result, once user returns back to cart from the PP checkout, all codes would be
•		marked as not valid
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.APPLY_CODE_BUTTON_SELECTOR = '#csc-checkout-btn-apply-promo-code';
		this.EMPTY_CART_SELECTOR = '.cart-empty:not([class*="d-none"])';
		this.CART_CODE_INPUT_SELECTOR = '#csc-coupon-code';
		this.CHECKOUT_CODE_INPUT_SELECTOR = '#csc-checkout-promo-code';
		this.CODE_VALUE_SELECTOR = '#csc-table-coupon-codes [data-transaction]';
		this.PAYMENT_STEP_SELECTOR = '#csc-collapse-payment.show';
		this.TOTAL_SELECTOR =
			'#csc-amount-total, #csc-amount-paypal, #csc-checkout-order-summary-total, #csc-amount_due';
		this.CONTAINER_SELECTOR = '#csc-checkout-order-summary-child';
		this.SHIPPING_SELECTOR = `${this.CONTAINER_SELECTOR} #csc-checkout-order-summary-shipping`;
		this.TAX_SELECTOR = `${this.CONTAINER_SELECTOR} .summary-container.summary .text-left`;
		this.isStackable = true;

		const BASE_URL = 'https://www.columbia.com/on/demandware.store/Sites-Columbia_US-Site/en_US/SinglePageCheckout';
		this.APPLY_CODE_URL = `${BASE_URL}-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}-UpdateOrderSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'removeCoupon';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		this.APPLY_RESPONSE_KEY = null;
	}

	checkCondition() {
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const promoInputVisible =
			document.querySelector(this.CART_CODE_INPUT_SELECTOR) ||
			(document.querySelector(this.CHECKOUT_CODE_INPUT_SELECTOR) &&
				document.querySelector(this.PAYMENT_STEP_SELECTOR));

		return !isCartEmpty && promoInputVisible;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(
			element => element.dataset.transaction,
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			let total;
			// seems like sometimes response might not contain total selector
			try {
				total = this.parseTotal(responseHTML);
			} catch (e) {
				handleDriverError(new Error('Invalid response string. Cannot parse total'), {
					errorMessage: e,
					response: responseHTML,
				});
				return null;
			}
			// shipping may be free
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			const taxElem = [...extractElement(this.TAX_SELECTOR, responseHTML, true)]
				.filter(elem => elem.innerText.trim().toLowerCase() === 'sales tax')
				.map(elem => elem.nextElementSibling.innerText);
			const tax = toFloat(taxElem[0]) || 0;
			return total - shipping - tax;
		});
	}
};

export default {
	name: 'Columbia',
	domain: 'columbia.com',
	isSinglePageApp: true,
	stackable: true,
	Driver,
};
