import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { completionActions, DOM, log } from '../../../utils';

const { extractElementFromHTML } = DOM;

const CART_PAGE_URL = 'https://www.redcross.org/take-a-class/cart';

/* Behavior description
 * We can apply not only one code(i guess)
 * We can get existing code
 * We can remove code
 * We can apply code on cart page
 * We can't save user data on checkout page so that we not support that page
 */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon, .coupon-promo';
		this.UUID_SELECTOR = '.removeCoupon, [id*="remove-coupon-"]';
		this.isStackable = true;

		const BASE_URL = 'https://www.redcross.org/on/demandware.store/Sites-RedCross-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCouponJson`;
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.removeQueryParams.set('format', 'ajax');
		this.APPLIED_CODE_REGEX = /code\s(.+)\sapplied/i;
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(element => element.innerText.trim().match(this.APPLIED_CODE_REGEX)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		this.UU_ID = document.querySelector(this.UUID_SELECTOR).dataset.uuid;
		return Promise.each(codes, code => this.removeCode(code, this.UU_ID))
			.then(() => this.getOrderSummary())
			.then(response => this.parseTotal(response));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(responseText => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
				};

				if (extractElementFromHTML(responseText, this.UUID_SELECTOR)) {
					this.UU_ID = extractElementFromHTML(responseText, this.UUID_SELECTOR).dataset.uuid;

					return this.getOrderSummary().then(response => {
						result.finalPrice = this.parseTotal(response);
						result.finalDiscount = originalPrice - result.finalPrice;
						if (result.finalPrice < originalPrice) {
							workingDiscounts.push(result);
						}

						return this.removeCode(code, this.UU_ID).then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
				});
			}
			return Promise.resolve();
		});
	}

	removeCode(code, uuId) {
		this.removeQueryParams.set('uuid', uuId);

		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'RedCross',
	domain: 'redcross.org',
	Driver,
	completionAction: completionActions.revisit,
	stackable: true,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) || href.endsWith('Cart-SubmitForm')) &&
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
};
