import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { browserUtils, completionActions, DOM, format } from '../../../utils';

const { wait } = browserUtils;
const { extractElement, runEmbeddedScript, setValue } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ ‘isValid’ implemented: yes
	+ Replace code: no
	+ Manually apply code: yes(checkout page)
	+ Pages, where extension works: Cart, Payment
	+ Is driver SPA: yes
	+ Gather codes on site pages: yes, from https://www.womanwithin.com/offers-coupons page
====Spesial case====
	+ Session timer: 20min
*/

const BASE_URL = 'https://www.womanwithin.com';
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-womanwithin-Site/default`;

const isOnCheckout = () => window.location.href.includes('checkout');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_ELEMENT_CONFIG = {
			selector: '.order-total .order-value',
		};
		this.TOTAL_SELECTOR = this.TOTAL_ELEMENT_CONFIG.selector;
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.PRODUCT_REMOVE_SELECTOR = '.remove-btn, .add-to-wishlist';

		this.CSRF_TOKEN_SELECTOR = '[name="csrf_token"]';

		this.CART_PAGE_URL = `${BASE_URL}/cart`;
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-SubmitForm`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/Cart-SubmitForm`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'dwfrm_cart_couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'dwfrm_cart_coupons_i0_deleteCoupon';

		this.REMOVE_CODE_BTN_SELECTOR = '#remove-coupon';

		this.APPLIED_CODE_BUTTON_SELECTOR = '#add-coupon';
		this.APPLIED_CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

		this.COUPONS_PAGE_URL = `${BASE_URL}/offers-coupons`;
		this.MERCHANT_CODE_SELECTOR = '.promo-code b, .oc-coupon-code';

		this.TAX_VALUE_SELECTOR = '.order-sales-tax .value';
		this.SHIPPING_VALUE_SELECTOR = '.order-shipping .value';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount .value';

		this.APPLY_REQUEST_HEADERS = {
			'Content-Type': 'application/x-www-form-urlencoded',
			'upgrade-insecure-requests': 1,
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
		this.UPDATE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.isListenerAdded = false;
	}

	watchForCartUrl() {
		if (navigator.cookieEnabled) {
			localStorage.setItem('lastProductRemove', 'false');
		}
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 20,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const cookiesOn = navigator.cookieEnabled;
		const isRightUrl =
			href.includes('/cart') ||
			href.includes('/Cart-SubmitForm') ||
			href.includes('/checkout') ||
			href.includes('/shipping');
		const total = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		if (!this.isListenerAdded && cookiesOn) {
			this.isListenerAdded = true;
			runEmbeddedScript(`
					[...document.querySelectorAll('${this.PRODUCT_REMOVE_SELECTOR}')]
					.map(el => el.addEventListener('click', () => localStorage.setItem('lastProductRemove', true)));
				`);
		}
		return (
			cookiesOn && (isRightUrl || total) && isCodeInput && localStorage.getItem('lastProductRemove') === 'false'
		);
	}

	beforeTestCodes() {
		const tokenElem = document.querySelector(this.CSRF_TOKEN_SELECTOR);
		this.applyQueryParams.set('csrf_token', tokenElem ? tokenElem.value : false);
		this.removeQueryParams.set('csrf_token', tokenElem ? tokenElem.value : false);
		return Promise.resolve();
	}

	applyCodes(codes) {
		return isOnCheckout() ? this.applyCodeManually(codes[0]) : this.applyCode(codes[0]);
	}

	removeCodes(codes) {
		return isOnCheckout() ? this.removeCodeManually() : this.removeCode(codes[0]);
	}

	getMerchantCodes() {
		return fetch(this.COUPONS_PAGE_URL, {
			method: 'GET',
		})
			.then(response => response.text())
			.then(responseHTML => [
				...new Set(
					[...extractElement(this.MERCHANT_CODE_SELECTOR, responseHTML, true)]
						.map(el => el.textContent)
						.filter(el => Boolean(el) && el.toLowerCase() !== 'no code needed'),
				),
			]);
	}

	applyCode(code) {
		this.applyQueryParams.set('dwfrm_cart_addCoupon', 'dwfrm_cart_addCoupon');
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, false, this.applyQueryParams);
	}

	applyCodeManually(code) {
		const codeInputElm = document.querySelector(this.APPLIED_CODE_INPUT_SELECTOR);
		const codeApplyBtn = document.querySelector(this.APPLIED_CODE_BUTTON_SELECTOR);
		setValue(codeInputElm, code);
		codeApplyBtn.click();
		const codeWasApplied = () => document.querySelector(this.REMOVE_CODE_BTN_SELECTOR);
		return wait(codeWasApplied, 10000);
	}

	removeCodeManually() {
		document.querySelector(this.REMOVE_CODE_BTN_SELECTOR).click();
		const codeWasRemoved = () => !document.querySelector(this.REMOVE_CODE_BTN_SELECTOR);
		return wait(codeWasRemoved, 10000)
			.then(() => this.getOrderSummary())
			.then(res => this.parseTotal(res));
	}

	removeCode(code) {
		this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, false, this.removeQueryParams)
			.then(() => this.getOrderSummary())
			.then(res => this.parseTotal(res));
	}

	getPreTaxShippingTotal() {
		return this.getCartPage().then(responseHTML => {
			const taxValue = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML)) || 0;
			const shippingValue = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			return this.parseTotal(responseHTML) - taxValue - shippingValue + shippingDiscount;
		});
	}
};

export default {
	name: 'womanwithin',
	domain: 'womanwithin.com',
	isSinglePageApp: true,
	Driver,
	completionAction: isOnCheckout() ? completionActions.none : completionActions.default,
};
