import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { log, format, DOM, completionActions } from '../../../utils';
import { handleDriverError, sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

/* Behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Replace code: yes
	+ Stackable: yes
	+ Free gift support: no
	+ Free shipping: no
	+ ‘isValid’ implemented: yes
	+ Pages, where extension works: side cart, checkout
	+ Is driver SPA: no
	+ Gather codes on site pages: no
	+ Session timer: 15min
*/

const MERCHANT_DOMAIN = 'https://www.gnc.com';
const CART_PAGE_URL = `${MERCHANT_DOMAIN}/cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.TOTAL_ELEMENT_CONFIG = { selector: this.TOTAL_SELECTOR };

		const BASE_URL = `${MERCHANT_DOMAIN}/on/demandware.store/Sites-GNC2-Site/default/`;
		this.APPLY_CODE_URL = `${BASE_URL}Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.applyQueryParams.set('format', 'ajax');
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-of-type';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.REMOVE_REQUEST_HEADERS = {
			'content-type': 'application/x-www-form-urlencoded',
			'upgrade-insecure-requests': 1,
			accept:
				'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3',
		};

		this.SIDE_CART_SELECTOR = '.flyout-active #flyoutcart-content';
		this.OPEN_SIDE_CART_BTN_SELECTOR = '#mini-cart .mini-cart-total';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isSideCartOpen = document.querySelector(this.SIDE_CART_SELECTOR);
		const codeInputSelector = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnCheckout = href.startsWith(`${MERCHANT_DOMAIN}/shipping`);
		return (isSideCartOpen || isOnCheckout) && codeInputSelector;
	}

	checkRestoreDataCondition() {
		return document.querySelector(this.OPEN_SIDE_CART_BTN_SELECTOR);
	}

	getUserData() {
		return { sideCartCodeTesting: !!document.querySelector(this.SIDE_CART_SELECTOR) };
	}

	setUserData(userData) {
		if (userData.sideCartCodeTesting && document.querySelector(this.OPEN_SIDE_CART_BTN_SELECTOR)) {
			document.querySelector(this.OPEN_SIDE_CART_BTN_SELECTOR).click();
		}
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						result.isValid = true;
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const taxEl = extractElement(this.TAX_SELECTOR, response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, response);
			const shipping = shippingEl ? elemToFloat(shippingEl) || 0 : 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			try {
				return this.parseTotal(response) - shipping - tax + shippingDiscount;
			} catch (e) {
				handleDriverError(new Error(`Can't get total in getPreTaxShippingTotal method.`), {
					response,
					url: window.location.href,
					tax,
					shipping,
					shippingDiscount,
				});
				return null;
			}
		});
	}

	_isOnCartPage() {
		const href = window.location.href.toLowerCase();
		return href.startsWith(this.CART_PAGE_URL);
	}
};

export default {
	name: 'GNC',
	domain: 'gnc.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
	restoreUserData: true,
};
