import DemandwareAJAXDriver from './_base';
import { DOM, format, completionActions } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_SITE_URL = 'https://www.talbots.com';
const CART_PAGE_URL = `${BASE_SITE_URL}/cart`;
const ITEM_SELECTOR = '.cart-row, .mini-summary-products .mini-cart-product';
const isBilling = window.location.href.startsWith(`${BASE_SITE_URL}/billing`);

/**
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes 
+ Codes can be replaced by another code: no
+ Pages, where extension works:
   - cart
   - billing
+ Is driver SPA: no
+ Gather codes on site pages: no
+ Special case about the driver works.
On the checkout page we will be redirected on the shipping page after codes testing 
 */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-value';
		this.APPLIED_CODES_SELECTOR = '';
		this.PRODUCT_ITEM_SELECTOR = ITEM_SELECTOR;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-tax td:last-child, .order-sales-tax td:last-child';

		this.MERCHANT_CODES_SELECTOR = '.promo-bar-header span';
		this.MERCHANT_CODES_REGEXP = /code:\s(\w+)/i;

		this.isStackable = true;

		const BASE_URL = 'https://www.talbots.com/on/demandware.store/Sites-talbotsus-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	getMerchantCodes() {
		return (isBilling ? this.getCartPage() : Promise.resolve(null)).then(responseHTML => {
			const codes = [...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
				.map(elem => elem.innerText.match(this.MERCHANT_CODES_REGEXP))
				.filter(Boolean)
				.map(elem => elem[1]);
			return [...new Set(codes)];
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			return total - tax - shipping;
		});
	}
};

export default {
	name: 'Talbots',
	domain: 'talbots.com',
	stackable: true,
	Driver,
	cartContinueUrl: isBilling ? `${BASE_SITE_URL}/shipping` : window.location.href,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		return Boolean(document.querySelector('.mini-cart-empty'));
	},
	isOnCartUrl(href) {
		return (href.startsWith(CART_PAGE_URL) || isBilling) && document.querySelector(ITEM_SELECTOR);
	},
};
