import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement, extractFormValues } = DOM;
const { elemToFloat, toFloat } = format;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
    - the cart page
    - the checkout page (payment). user info is removed after code testing (default site behavior)
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const BASE_URL = 'https://www.rag-bone.com';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.IS_ON_CHECKOUT = window.location.href.includes('checkout');
		this.API_URL = `${BASE_URL}/on/demandware.store/Sites-ragandbone-Site/en_US`;
		this.APPLY_CODE_URL = `${this.API_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.API_URL}/Cart-DeleteCouponJson`;
		this.UPDATE_REQUEST_URL = this.IS_ON_CHECKOUT ? `${BASE_URL}/checkout-billing` : `${BASE_URL}/cart`;

		this.REMOVE_CODE_PARAM_NAME = 'couponCode';
		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.TOTAL_SELECTOR = '.order-total td strong';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.EXISTING_CODES_SELECTOR = '.promo-remove p strong, .coupon-redemption-message b';
		this.CART_FORM_SELECTOR = 'form[name=dwfrm_cart]';

		this.isStackable = true;
		this.codesToApply = null;
		this.formValues = null;
	}

	beforeTestCodes() {
		this.formValues = extractFormValues(this.CART_FORM_SELECTOR);
		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.cartHeaders = {
			'content-type': 'application/x-www-form-urlencoded',
			'sec-fetch-user': '?1',
			'upgrade-insecure-requests': '1',
		};
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.EXISTING_CODES_SELECTOR)]
			.filter(el => el.textContent && !el.textContent.match(/promo error/i))
			.map(el => el.textContent);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const taxElement = extractElement(this.TAX_SELECTOR, responseHTML);
			const shippingElement = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const shippingDiscountElement = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML);
			const shippingDiscount = shippingDiscountElement ? elemToFloat(shippingDiscountElement) : 0;
			const tax = taxElement && !taxElement.textContent.includes('N/A') ? taxElement.textContent : 0;
			const shipping =
				shippingElement && !shippingElement.textContent.includes('N/A') ? shippingElement.textContent : 0;
			return toFloat(total) - toFloat(tax) - toFloat(shipping) + shippingDiscount;
		});
	}

	applyCode(code) {
		if (this.IS_ON_CHECKOUT) {
			return this._applyCodeOnCheckout(code);
		}
		return this._applyCodeOnCart(code);
	}

	removeCode(code) {
		return this.IS_ON_CHECKOUT ? this._removeCodeOnCheckout(code) : this._removeCodeOnCart(code);
	}

	_removeCodeOnCart() {
		this.formValues.params.set('dwfrm_cart_couponCode', '');
		this.formValues.params.set('dwfrm_cart_coupons_i0_deleteCoupon', 'Remove');
		this.formValues.params.delete('dwfrm_cart_addCoupon');
		return this._sendFetch(this.formValues.action, this.formValues.params);
	}

	_removeCodeOnCheckout(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	_applyCodeOnCart(code) {
		this.formValues.params.set('dwfrm_cart_couponCode', code);
		this.formValues.params.set('dwfrm_cart_addCoupon', 'dwfrm_cart_addCoupon');
		this.formValues.params.delete('dwfrm_cart_coupons_i0_deleteCoupon');
		return this._sendFetch(this.formValues.action, this.formValues.params);
	}

	_sendFetch(url, body) {
		return fetch(url, {
			credentials: 'include',
			headers: this.cartHeaders,
			body,
			method: 'POST',
		}).then(res => res.text());
	}

	_applyCodeOnCheckout(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);

		return this._sendRequestGET(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	parseTotal(data = false) {
		if (typeof data === 'string' || data === false) {
			return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
		}
		return data.baskettotal;
	}
};

export default {
	name: 'rag & bone',
	domain: 'rag-bone.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith(BASE_URL) &&
			(document.querySelector('input[name=dwfrm_cart_couponCode]') ||
				document.querySelector('#dwfrm_billing_couponCode'))
		);
	},
	showDifferentDiscountMessage: true,
};
