import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';

/*
Driver behavior description:
-    Existing code: yes
-    Remove code: yes
-    Stackable:no
-    Free gift support: no
-    ‘isValid’ implemented: yes
Optional:
-    Show message about free shipping on the cart page (without discount info): no
-    Is driver SPA and add the condition to show popup: no
-	 We can apply code on Cart page and on Billing
 */

const MAIN_URL = 'https://www.mrcoffee.com';
const CART_PAGE_URL = `${MAIN_URL}/cart`;
const BILLING_PAGE_URL = `${MAIN_URL}/billing`;
// it is safer to check code input, because sometimes we can move from Cart to Checkout without url change
const isOnCartPage = () => document.querySelector('#dwfrm_cart_couponCode');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';

		const BASE_URL = 'https://www.mrcoffee.com/on/demandware.store/Sites-mr-coffee-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.PROMO_INPUT_SELECTOR_CART = '#dwfrm_cart_couponCode';
		this.PROMO_INPUT_SELECTOR_CHECKOUT = '#dwfrm_billing_couponCode';
		this.PROMO_BTN_SELECTOR = '#add-coupon';

		this.applyQueryParams.set('format', 'ajax');
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return Boolean(document.querySelector(this.PROMO_INPUT_SELECTOR_CART));
	}
};

export default {
	name: 'MrCoffee',
	domain: 'mrcoffee.com',
	Driver,
	completionAction: completionActions.revisit,
	cartContinueUrl: isOnCartPage() ? CART_PAGE_URL : BILLING_PAGE_URL,
	isOnCartUrl() {
		return document.querySelector('#dwfrm_cart_couponCode, #dwfrm_billing_couponCode');
	},
};
