import DemandwareAJAXDriver from './_base';
import { DOM, completionActions, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractElement } = DOM;
const { elemToFloat } = format;

const CART_URL = 'https://www.michaels.com/cart';
const EMPTY_CART_SELECTOR = '.cart-empty';

/*
Driver behavior description:
+ Existing code: yes (also caught valid, but not working codes)
+ Remove code: yes
+ Stackable: yes
+ Free gift support:  no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
    - Cart page
+ Is driver SPA no
+ Gather codes on site pages: yes
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		const BASE_URL = 'https://www.michaels.com/on/demandware.store/Sites-MichaelsUS-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COSinglePageCheckout-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.isStackable = true;

		this.TOTAL_SELECTOR = '.order-total-value .value';

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.CODES_ELEMENTS_SELECTOR = '.cartcoupon';
		this.CODE_VALUE_SELECTOR = '.bonus-item';
		this.CODE_REGEX = /[pP]romo.*( [A-Z\d]+[\s$]?)/;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.COUPON_BOX_SELECTOR = '.coupon-bottom-box';
		this.COUPON_BOX_VALUE = 'data-barcode-value';
		sessionTimer.initialize({
			actionInterval: null,
			timeout: 30,
		});
	}

	getMerchantCodes() {
		return this._getMerchantCoupons().then(responseHTML => {
			const promoCodes = [...extractElement(this.COUPON_BOX_SELECTOR, responseHTML, true)]
				.filter(Boolean)
				.map(elem => elem.getAttribute(this.COUPON_BOX_VALUE))
				.filter(Boolean);
			return promoCodes;
		});
	}

	_getMerchantCoupons() {
		return fetch('https://www.michaels.com/coupons', {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
			.filter(element => element.innerText.match(this.CODE_REGEX))
			.map(element => element.innerText.match(this.CODE_REGEX)[1].trim())
			.filter(Boolean);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = this._findValueByLabel(response, 'Estimated Shipping') || 0;
			const tax = this._findValueByLabel(response, 'Estimated Tax') || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	_findValueByLabel(response, label) {
		const element = [...extractElement('span.label', response, true)].find(el => el.textContent.includes(label));
		return element ? elemToFloat(element.nextElementSibling) : 0;
	}
};

export default {
	name: 'Michael`s',
	domain: 'michaels.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isCartEmpty() {
		return (document.querySelector(EMPTY_CART_SELECTOR) || {}).offsetParent;
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL) && document.querySelector('[name="dwfrm_cart_checkoutCart"]');
	},
};
