import DemandwareAJAXDriver from './_base';

const BASE_URL = 'https://www.donaldpliner.com';

const INPUT_CODE_FLD_SELECTOR = '#dwfrm_cart_couponCode';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no
•    Can apply code only on `Cart` page

======== Special case ==========
Not support `Checkout`. Site applies code and shows
successful message but doesn't change total. */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.CART_PAGE_URL = `${BASE_URL}/cart`;

		this.APPLY_CODE_URL = `${BASE_URL}/on/demandware.store/Sites-donaldpliner-Site/default/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = this.CART_PAGE_URL;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.applyQueryParams.set('format', 'ajax');
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'Donald Pliner',
	domain: 'donaldpliner.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			(href.startsWith(`${BASE_URL}/cart`) ||
				href.startsWith(`${BASE_URL}/on/demandware.store/Sites-donaldpliner-Site/default/Cart-SubmitForm`)) &&
			Boolean(document.querySelector(INPUT_CODE_FLD_SELECTOR))
		);
	},
};
