import DemandwareAJAXDriver from './_base';
import { toFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

/* Diver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: billing
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_SUMMARY_SELECTOR = '.js-checkout-order-totals';
		this.TOTAL_SELECTOR = `${this.CART_SUMMARY_SELECTOR} .order-total .order-value`;
		this.SHIPPING_SELECTOR = `${this.CART_SUMMARY_SELECTOR} .order-shipping td:last-child`;
		this.SHIPPING_DISCOUNT_SELECTOR = `${this.CART_SUMMARY_SELECTOR} .discounts-delivery-row`;
		this.isStackable = false;

		const BASE_URL = 'https://www.boohooman.com/on/demandware.store/Sites-boohooman-US-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'couponID';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.APPLIED_CODES_SELECTOR = '.js-couponcode [data-coupon]';
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(element =>
			element.getAttribute('data-coupon'),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shippingDiscountElem = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingElem = extractElement(this.SHIPPING_SELECTOR, response);
			const shipping = shippingElem ? toFloat(shippingElem.innerText) : 0;
			const shippingDiscount = shippingDiscountElem ? toFloat(shippingDiscountElem.innerText) : 0;
			return this.parseTotal(response) - shipping + shippingDiscount;
		});
	}
};

export default {
	name: 'Boohoo Man',
	domain: 'boohooman.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.boohooman.com/us/billing-continue');
	},
};
