import DemandwareAJAXDriver from './_base';
import { toFloat, elemToFloat } from '../../../utils/format';

/**
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart
+ Is driver SPA yes (total and url) 
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
sometimes we don’t have the total on the page, in this case we take the price from the subtotal
*/

// need this because url can be different
const BASE_SITE_URL = window.location.href.split('Cart-AjaxCartView')[0];
const CART_PAGE_URL = `${BASE_SITE_URL}Cart-AjaxCartView`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.amount.order-total';
		this.SUBTOTAL_SELECTOR = '.totalPrice';
		this.APPLIED_CODES_SELECTOR = '#RemovePromoCodeButton';
		this.isStackable = false;

		const BASE_URL = BASE_SITE_URL;
		this.APPLY_CODE_URL = `${BASE_URL}Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}Cart-CartSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		const isOnHref = window.location.href.startsWith(CART_PAGE_URL);
		const isTotal = document.querySelector(`${this.TOTAL_SELECTOR}, ${this.SUBTOTAL_SELECTOR}`);
		return isOnHref && isTotal;
	}

	beforeTestCodes() {
		const total = document.querySelector(this.TOTAL_SELECTOR);
		this.TOTAL_SELECTOR = total ? this.TOTAL_SELECTOR : this.SUBTOTAL_SELECTOR;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(elem => elem.dataset && elem.dataset.couponid)
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseJSON => {
			const { orderTax, shippingCharges, shippingDiscount } = responseJSON;
			const tax = toFloat(orderTax) || 0;
			const shipping = toFloat(shippingCharges) || 0;
			const shippingDisc = toFloat(shippingDiscount) || 0;
			return this.parseTotal(responseJSON) - tax - shipping + shippingDisc;
		});
	}

	parseTotal(data) {
		if (data) {
			return toFloat(data.totalOrderPrice);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Scholastic Store',
	domain: 'shop.scholastic.com',
	Driver,
	isSinglePageApp: true,
};
