import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';

/* Driver behavior description:
+ 	Existing code: yes
+ 	Remove code: yes
+ 	Stackable: yes
+ 	Free gift support: no
+ 	Free shipping: no
+ 	‘isValid’ implemented: yes
+ 	Codes can be replaced by another code: no
+ 	Manually apply code: no
+ 	Pages, where extension works:
		- Cart page
		- Wishlist
+ 	Is driver SPA yes
    	- On cart page when total is rendered, on checkout there's a high risk of getting a broken page/alert message after reload, no support there.
+ 	Gather codes on site pages: no
+  	Special case: no
+  	Credentials: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		const BASE_URL = 'https://www.artvan.com';
		const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-artvan-Site/default`;
		const FIRST_CART_URL = `${BASE_URL}/cart`;
		const SECOND_CART_URL = `${BASE_REQUEST_URL}/Cart-SubmitForm`;
		const WISHLIST_URL = `${BASE_URL}/on/demandware.store/Sites-artvan-Site/default/Cart-AddToWishlist`;

		this.PAGES = [FIRST_CART_URL, SECOND_CART_URL, WISHLIST_URL];
		this.isStackable = true;
		this.TOTAL_SELECTOR = '.order-value';
		this.EMPTY_SELECTOR = '.cart-empty';
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = FIRST_CART_URL;

		this.UPDATE_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
			accept: 'text/html, */*; q=0.01',
		};

		this.CART_PAGE_URL = FIRST_CART_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.SUCCESS_MESSAGE_SELECTOR = '.redemption .success';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isEmpty = document.querySelector(this.EMPTY_SELECTOR);
		const isInput = document.querySelector(this.CODE_INPUT_SELECTOR);

		return this._isOnCartPage() && isTotal && isInput && !isEmpty;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return this.PAGES.some(page => window.location.href.includes(page));
	}
};

export default {
	name: 'Art Van',
	domain: 'artvan.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
