import DemandwareAJAXDriver from './_base';

const LOGIN_USER_SELECTOR = '.logout.nav-ico-after.js-logout';
/*
Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support: no
•    ‘isValid’ implemented: no
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no

•   Can apply code on `Cart` page

======== Special case ==========
Not support login users due to behavior of driver.
Site applies code and shows successful message but doesn't change total.
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total td:last-child, .cart-price-total span:last-child';

		const BASE_URL = 'https://www.lacoste.com/on/demandware.store/Sites-FlagShip-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/CartV2-RemoveCouponLineItem`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');

		this.APPLY_RESPONSE_KEY = null;

		this.APPLIED_CODE_SELECTOR = '[data-coupon-code]';
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(el =>
			el.getAttribute('data-coupon-code'),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'Lacoste',
	domain: 'lacoste.com',
	Driver,
	isCartEmpty() {
		return !!document.querySelector('.cart-empty-title');
	},
	isOnCartUrl(href) {
		return href.startsWith('https://www.lacoste.com/us/cart') && !document.querySelector(LOGIN_USER_SELECTOR);
	},
};
