import DemandwareAJAXDriver from './_base';

/*
 Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable:  no
 -    Free gift support: yes
 -    ‘isValid’ implemented: yes
 Optional:
 -    Is driver SPA and add the condition to show popup: no
 -	Show popup on the Cart and Checkout (2 step, after reloadind user will be redirected
	 to the first step without without erasing adress data) pages
  */

const BASE_URL = 'https://www.motherhood.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const BILLING_PAGE_URL = `${BASE_URL}/billing`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-mh-us-Site/default`;
const CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
const isOnCart = () => Boolean(document.querySelector(CODE_INPUT_SELECTOR));

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return isOnCart();
	}
};

export default {
	name: 'Motherhood Maternity',
	domain: 'motherhood.com',
	Driver,
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) ||
				href.startsWith(BILLING_PAGE_URL) ||
				href.includes('/shipping') ||
				href.includes('/checkout') ||
				href.includes('/Cart-AddToWishlist')) &&
			document.querySelector(`#dwfrm_billing_couponCode, ${CODE_INPUT_SELECTOR}`)
		);
	},
	isCartEmpty() {
		return document.querySelector('.mini-cart-empty');
	},
};
