import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

const DOMAIN_URL = 'https://www.truereligion.com';
const CART_PAGE_URL = `${DOMAIN_URL}/cart`;
const BASE_URL = `${DOMAIN_URL}/on/demandware.store/Sites-TrueReligion-Site/default`;

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support:  no
•    ‘isValid’ implemented: no
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page: no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: yes
•    Active pages: cart, checkout(billing)
•    sessionTimer: 15min */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLIED_CODES_SELECTOR = '.coupon-applied';
		this.CODE_REGEX = /Promo Number: (\w+)/;
		this.APPLIED_ELEM_SELECTOR = '.promo-coupon-style';
		this.INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.MERCHANT_CODE_SELECTOR = '.bx-row-coupon div';
		this.MERCHANT_CODES_REG = /Use Code: (\w+)/;

		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponAjax`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-DeleteCoupon`;
		this.UPDATE_REQUEST_URL = window.location.href.startsWith(CART_PAGE_URL)
			? `${BASE_URL}/COBilling-UpdateSummary`
			: `${BASE_URL}/COBilling-Start`;

		this.APPLY_CODE_PARAM_NAME = 'dwfrm_cart_couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('gcPromoSubmitted', 'true');
		sessionTimer.initialize();
	}

	checkCondition() {
		const codeInput = document.querySelector(this.INPUT_SELECTOR);
		const emptyCartSelector = document.querySelector(this.EMPTY_CART_SELECTOR);
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const rightCurrency = totalElem ? totalElem.textContent.includes('$') : false;
		return codeInput && !emptyCartSelector && rightCurrency;
	}

	getMerchantCodes() {
		const merchantCodes = [];
		return this._getPage().then(responseHTML => {
			const merchantCode = responseHTML.match(this.MERCHANT_CODES_REG);
			if (merchantCode) {
				merchantCodes.push(merchantCode[1]);
			}
			const codeFromPage = document.querySelector(this.MERCHANT_CODE_SELECTOR);
			if (codeFromPage) {
				merchantCodes.push(codeFromPage.textContent);
			}
			return merchantCodes;
		});
	}

	getExistingCodes() {
		const codes = [];
		const codeElement = document.querySelector(this.APPLIED_CODES_SELECTOR);
		if (codeElement && codeElement.innerText.match(this.CODE_REGEX)) {
			codes.push(codeElement.innerText.match(this.CODE_REGEX)[1]);
		} else if (document.querySelector(this.APPLIED_ELEM_SELECTOR)) {
			codes.push(document.querySelector(this.APPLIED_ELEM_SELECTOR).textContent.trim());
		} else if (codeElement && codeElement.innerText.includes('Applied')) {
			codes.push(codeElement.textContent.split(' ')[2]);
		}
		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}

	_getPage() {
		return fetch(`${DOMAIN_URL}/`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'True Religion',
	domain: 'truereligion.com',
	Driver,
	isSinglePageApp: true,
	cartContinueUrl:
		window.location.href.includes('COBilling-DeleteCoupon') || window.location.href.includes('shipping')
			? `${BASE_URL}/COBilling-Start`
			: window.location.href,
};
