import DemandwareAJAXDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

const BASE_URL = 'https://www.itcosmetics.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const CHECKOUT_PAGE_URL = `${BASE_URL}/checkout`;
const IS_ON_CHECKOUT = window.location.href.startsWith(CHECKOUT_PAGE_URL);
/*
Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support: yes
•    ‘isValid’ implemented: no
•    active pages: cart, checkout
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR =
			'.cart_order_totals .order_total td:last-child, .checkout_order_totals .order_total td:last-child';
		this.APPLIED_CODES_SELECTOR = '.remove_coupon';
		this.INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.PRODUCT_ITEM_SELECTOR = '#cart_table tbody tr, .checkout_minicart_product';
		this.REVIEW_SELECTOR = '.chsection_preview.current';
		this.ICON_SELECTOR = '.b-minicart_quantity span';
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_URL =
			'https://www.itcosmetics.com/on/demandware.store/Sites-itcosmetics-us-Site/default/Cart-AddRemoveCoupon';
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.UPDATE_REQUEST_URL = this._isOnCartPage() ? this.CART_PAGE_URL : CHECKOUT_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.applyQueryParams.set('format', 'ajax');
		this.applyQueryParams.set('type', 'add');
		this.removeQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('type', 'remove');

		this.PRODUCT_ITEM_SELECTOR = '.cart_row, .bonus_row, .b-checkout_minicart_product';
		this.FIRST_STEP_SELECTOR = '#form_emailonly';

		this.TAX_VALUE_SELECTOR = '.estimated_tax_amount';
		this.HIDDEN_TAX_SELECTOR = '.estimated_tax_result_box';
		this.SHIPPING_VALUE_SELECTOR = '.order_shipping td:last-child';
		this.CART_PAGE_URL = 'https://www.itcosmetics.com/cart';
	}

	checkCondition() {
		const cartPage = window.location.href.startsWith(this.CART_PAGE_URL);
		const isInput = document.querySelector(`${this.INPUT_SELECTOR}, ${this.APPLIED_CODES_SELECTOR}`);
		const isEmpty = document.querySelector(this.TOTAL_SELECTOR) && this.parseTotal() === 0;
		const isOnFirstStep = document.querySelector(this.FIRST_STEP_SELECTOR);
		return (cartPage && !isEmpty && isInput) || (IS_ON_CHECKOUT && isOnFirstStep && !isEmpty && isInput);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			codeElement => codeElement.dataset.coupon,
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCodes(codes) {
		return this.applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		// Tax and shipping are included on cart and checkout pages
		return this.getCartPage().then(responseHTML => {
			const taxValue = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML));
			const hiddenTax = extractElement(this.HIDDEN_TAX_SELECTOR, responseHTML).classList.contains('hidden');
			const isTaxValue = hiddenTax ? 0 : taxValue;
			const shippingValue = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML));
			const isShippingValue = Number.isNaN(shippingValue) ? 0 : shippingValue;
			const totalValue = this.parseTotal(responseHTML);
			return totalValue - isTaxValue - isShippingValue;
		});
	}
};

export default {
	name: 'It Cosmetics',
	domain: 'itcosmetics.com',
	Driver,
	isSinglePageApp: true,
	enableFreeGiftsWarning: true,
	completionAction: completionActions.revisit,
};
