import DemandwareDriver from './_base';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: no */

const BASE_URL = 'https://www.otterbox.com';
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-otterbox_us_v2-Site/default`;
const CART_PAGE_URL = `${BASE_URL}/en-us/cart`;
const DELIVERY_PAGE_URL = `${BASE_REQUEST_URL}/BasicShipping-Delivery`;
const BILLING_PAGE_URL = `${BASE_REQUEST_URL}/BasicBilling-Start`;

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.checkout-order-totals .order-total .value span';
		this.CODE_VALUE_SELECTOR = '.b-coupon_minisummary-coupon a';
		this.CART_FORM_SELECTOR = '#coupon-form';

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/Cartcon-DeleteCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		this.isStackable = true;

		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.CHECK_ELEM_SELECTOR = '#dwfrm_basicbilling_sameAsShipping';
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isOnCart = href.startsWith(CART_PAGE_URL);
		const isOnDelivery = href.startsWith(DELIVERY_PAGE_URL);
		const isOnPayment = href.startsWith(BILLING_PAGE_URL);
		const checkElem = document.querySelector(this.CHECK_ELEM_SELECTOR);
		const isCheckElem = checkElem ? checkElem.checked : false;
		const currentState = (isOnCart && !emptyCart) || isOnDelivery || (isOnPayment && isCheckElem);
		return currentState;
	}

	removeCode(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'Otterbox.com',
	domain: 'otterbox.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
