import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractFormValues, extractElement } = DOM;
const { elemToFloat } = format;

const HOME_URL = 'https://www.kingsize.com';
const CART_PAGE_URL = `${HOME_URL}/cart`;

/* Behavior description:
+ Existing code: yes
+ Merchant code: yes (offers page)
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Payment
+ Is driver SPA: yes
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.PRODUCT_ITEM_SELECTOR = '.cart-row';
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode, #dwfrm_cart_couponCode';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.SHIPPING_SELECTOR = '.order-shipping .value';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount .value';
		this.TAX_SELECTOR = '.order-sales-tax .value';

		this.MERCHANT_CODE_SELECTOR = '[data-coupon]';

		const BASE_URL = `${HOME_URL}/on/demandware.store/Sites-kingsize-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.COUPONS_PAGE_URL = `${HOME_URL}/offers-coupons`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.REMOVE_ATTRIBUTE_SELECTOR = '#remove-coupon';

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};

		this.applyQueryParams.set('format', 'ajax');
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		return document.querySelector(this.CODE_INPUT_SELECTOR) && !document.querySelector(this.EMPTY_CART_SELECTOR);
	}

	getMerchantCodes() {
		return this._getCouponsPage().then(responseHTML => [
			...new Set(
				[...extractElement(this.MERCHANT_CODE_SELECTOR, responseHTML, true)]
					.map(el => el.getAttribute('data-coupon'))
					.filter(el => Boolean(el) && el !== '#'),
			),
		]);
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_getCouponsPage() {
		return this._sendRequestGET(this.COUPONS_PAGE_URL, {});
	}

	getCartPage() {
		return this._sendRequestGET(this.CART_PAGE_URL, {}).then(cartPage => {
			this.lastCartPage = cartPage;
			return cartPage;
		});
	}

	getOrderSummary() {
		return this.getCartPage();
	}

	removeCodeOnCart(code) {
		const removeParamName = this._getRemoveValueName(code, this.lastCartPage);
		if (!removeParamName) {
			// No need in the reuqest, as parameter name was not found for the code
			return Promise.resolve();
		}

		const removeRequestData = extractFormValues(this.CART_FORM_SELECTOR, this.lastCartPage);
		removeRequestData.params.set(removeParamName, this.REMOVE_COUPON_PARAM_VALUE);

		return this._sendRequestPOST(removeRequestData.action, {}, null, removeRequestData.params);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shippingElement = extractElement(this.SHIPPING_SELECTOR, response);
			const shippingDiscountElement = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const taxElement = extractElement(this.TAX_SELECTOR, response);

			const shipping = shippingElement ? elemToFloat(shippingElement) : 0;
			const shippingDiscount = shippingDiscountElement ? elemToFloat(shippingDiscountElement) : 0;
			const tax = taxElement ? elemToFloat(taxElement) : 0;

			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}
};

export default {
	name: 'KingSize',
	domain: 'kingsize.com',
	isSinglePageApp: true,
	Driver,
};
