import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

/**
 Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes 
+ Pages, where extension works:
 * cart
 * billing
 * checkout
 * shipping
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
 * maxCoupons (25, site is very fast)
 * we can avoid cart page
 */

const BASE_URL = 'https://www.rebeccataylor.com';
const CART_PAGE_URL = `${BASE_URL}/bag`;
const isOnCartPage = () =>
	window.location.href.startsWith(CART_PAGE_URL) && document.querySelector('#dwfrm_cart_couponCode');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.PRODUCT_ITEM_SELECTOR = isOnCartPage() ? '.mini-cart-product' : '.checkout-mini-cart .mini-cart-product';
		this.CART_ELEMENT_SELECTOR = '.cart-actions-title';
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode, #dwfrm_cart_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.SUCCESS_MESSAGE_SELECTOR = '.coupon .success, .coupon-sucess';
		this.isStackable = true;

		const BASE_REQUEST_URL = 'https://www.rebeccataylor.com/on/demandware.store/Sites-rebeccataylor-Site/default';
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		const { href } = window.location;
		const isInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnHref =
			href.startsWith(this.CART_PAGE_URL) ||
			href.startsWith(`${BASE_URL}/shipping`) ||
			href.startsWith(`${BASE_URL}/billing`) ||
			href.startsWith(`${BASE_URL}/checkout`);
		const currentState = isInput && isOnHref;
		return currentState;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return (
			window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CART_ELEMENT_SELECTOR)
		);
	}
};

export default {
	name: 'Rebecca Taylor',
	domain: 'rebeccataylor.com',
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 25,
	completionAction: completionActions.revisit,
	Driver,
};
