import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { log, completionActions, DOM, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

/* Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: yes
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: Cart, Checkout (Payment step)
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Session timer: 15 minutes
+ Special case about the driver works:
We don't use completionAction.revisit for FF, because user can avoid cart page
and we will receive popup in FF
 */

const CART_PAGE_URL = 'https://www.ugg.com/cart';
const CART_FORM_SELECTOR = 'form[name="dwfrm_cart"]';

const isCart = () => window.location.href.startsWith(CART_PAGE_URL) && document.querySelector(CART_FORM_SELECTOR);

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.cart__checkout__wrapper .order-total td:last-child, [ng-bind*="cartSummary.total"]';
		this.APPLIED_CODES_SELECTOR = '.coupon-code .value';
		this.PAYMENT_SELECTOR = '#spc-panel-billing.is-active';
		this.INPUT_SELECTOR = '#spc-coupon-code, #dwfrm_cart_couponCode';

		const BASE_URL = 'https://www.ugg.com/on/demandware.store/Sites-UGG-US-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/SPC-UpdateCartSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.COUPON_DESCRIPTION_SELECTOR = '.item-coupon-details';
		this.FREE_SHIPPING_REGEX = /free.*shipping/i;

		this.applyQueryParams = new URLSearchParams({
			format: 'ajax',
			spc: true,
			fromPos: false,
			orderNo: null,
		});

		this.isStackable = false;
		this.removeQueryParams = this.applyQueryParams;

		sessionTimer.initialize();
	}

	checkCondition() {
		const isSection = document.querySelector(`${this.PAYMENT_SELECTOR}, ${CART_FORM_SELECTOR}`);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isInput = document.querySelector(this.INPUT_SELECTOR);
		return isInput && isSection && isTotal && this.parseTotal();
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => (this._isOnCartPage() ? this.getCartPage() : this.getOrderSummary()))
			.then(response => this.parseTotal(response));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
					freeShipping: false,
				};
				result.isValid = !applyCodeResponse.couponStatus;

				if (!result.isValid) {
					return bus.$emit('code-checked', result);
				}
				const promise = this._isOnCartPage() ? this.getCartPage() : this.getOrderSummary();
				return promise.then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeShipping = isCart() ? this._isFreeShipping(orderSummary) : false;
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const tax = toFloat(response.tax);
			const shipping = toFloat(response.shipments[0].shipping_cost);
			return this.parseTotal(response, true) - tax - shipping;
		});
	}

	_isFreeShipping(response) {
		const couponDescriptionEl = extractElement(this.COUPON_DESCRIPTION_SELECTOR, response);
		return Boolean(couponDescriptionEl && couponDescriptionEl.textContent.match(this.FREE_SHIPPING_REGEX));
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	parseTotal(data, isPretax = false) {
		if ((data && !this._isOnCartPage()) || isPretax) {
			return toFloat(data.total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}

	_isOnCartPage() {
		return isCart();
	}
};

export default {
	name: 'UGG',
	domain: 'ugg.com',
	Driver,
	completionAction: isCart() ? completionActions.revisit : completionActions.default,
	isSinglePageApp: true,
};
