import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

const CART_PAGE_URL = 'https://www.perriconemd.com/bag';
const CHECKOUT_PAGE_URL = 'https://www.perriconemd.com/shipping';
const CART_CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
const CHECKOUT_CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		const BASE_URL = 'https://www.perriconemd.com/on/demandware.store/Sites-perriconemd-Site/default';

		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.isStackable = true;

		// Need to reapply codes because some codes might have different names
		// after they are applied, so codes retesting may fail
		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.CODES_ELEMENTS_SELECTOR = '.applied-coupons-tr';
		this.CODE_VALUE_SELECTOR = '.applied-coupons-code';

		this.applyQueryParams.set('format', 'ajax');

		this.PRODUCT_ITEM_SELECTOR = '.pt_cart .mini-cart-product, .checkout-mini-cart .mini-cart-product';
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'Perricone MD',
	domain: 'perriconemd.com',
	stackable: true,
	Driver,
	completionAction() {
		return document.querySelector('#cart-items-form') ? completionActions.revisit : completionActions.default;
	},
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return (
			document.querySelector(`${CHECKOUT_CODE_INPUT_SELECTOR}, ${CART_CODE_INPUT_SELECTOR}`) &&
			(href.startsWith(CART_PAGE_URL) || href.startsWith(CHECKOUT_PAGE_URL))
		);
	},
};
