import DemandwareAJAXDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Payment
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from current page
*/

const BASE_SITE_URL = 'https://www.torrid.com';
const isCart = () => document.querySelector('#dwfrm_cart_couponCode');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.orderTotal';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.DONATION_SELECTOR = '[data-itemprimarycat="torrid_foundation"] .price-original';
		this.APPLIED_CODES_SELECTOR = '.coupon-applied span, .promo-li .success:first-child';
		this.MERCHANT_CODES_SELECTOR = '.promotion span';
		this.MERCHANT_CODES_REGEX = /code: (\w+)/i;

		this.baseURL = `${BASE_SITE_URL}/on/demandware.store/Sites-torrid-Site/default`;

		this.APPLY_CODE_URL = `${this.baseURL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.baseURL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.baseURL}/COBilling-UpdateSummary`;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.APPLY_REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.isStackable = true;

		this.INPUT_SELECTOR = 'input[name*="_couponCode"]';
	}

	checkCondition() {
		const couponInput = document.querySelector(this.INPUT_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return couponInput && isTotal && this.parseTotal();
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)]
					.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
					.filter(Boolean)
					.map(matched => matched[1]),
			),
		];
	}

	applyCodes() {
		if (isCart()) {
			// We should remove query parameters, to prevent form submitting again on cart page only
			// `cannotRefresh` parameter can't be used
			// because on checkout page `POST` request should be used for page reloading
			window.history.replaceState(
				window.history.state,
				window.document.title,
				window.location.href.split('?')[0],
			);
		}
		// Do nothing because `stackCodes()` will apply best codes
		return Promise.resolve();
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(orderSummary => {
			const total = this.parseTotal(orderSummary);
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, orderSummary));
			const shippingElement = extractElement(this.SHIPPING_SELECTOR, orderSummary);
			const shipping = /\d/.test(shippingElement.textContent) ? elemToFloat(shippingElement) : 0;
			const donationSum = [...extractElement(this.DONATION_SELECTOR, orderSummary, true)]
				.map(elemToFloat)
				.reduce((a, b) => a + b, 0);

			return total - tax - shipping - donationSum;
		});
	}
};

export default {
	name: 'Torrid',
	domain: 'torrid.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	cartContinueUrl: isCart() ? window.location.href : `${BASE_SITE_URL}/billing`,
	isSinglePageApp: true,
};
