import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';
import { DOM, format, log } from '../../../utils';
import bus from '../../bus';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

// Driver behaviour description:
// •    Existing code: yes
// •    Remove code: yes
// •    Stackable: yes
// •    Free gift support: no
// •    'isValid' implemented: no
// •    Codes can be replaced by another code: no
// •    works on cart and checkout

const BASE_URL = 'https://www.harley-davidsonfootwear.com';
const CART_PAGE_URL = `${BASE_URL}/US/en/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-harleydavidson_us-Site/default`;
const IsOnCheckout = () => window.location.href.includes('checkout');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODE_VALUE_SELECTOR = '.coupon-info .value';
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/Cart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/Cart-UpdateBasketTotals`;

		this.APPLY_CODE_URL_CHECKOUT = `${BASE_REQUEST_URL}/Coupon-ApplyCoupon`;
		this.REMOVE_CODE_URL_CHECKOUT = `${BASE_REQUEST_URL}/Coupon-RemoveCoupon`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.SELECTED_SHIPPING_SELECTOR = '.shipping-method-v2.selected input';
		this.EXISTING_CODE_SELECTOR = '.coupon-info';
		this.EXISTING_CODE_SELECTOR_CHECKOUT = '.spc-applied-discount-message';
		this.EXISTING_CODE_REGEX = /applied - (.\w+)/i;
		this.PROMO_INPUT_SELECTOR = '#dwfrm_cart_couponCode, .spc-drawer-promoCode';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		const { href } = window.location;
		const isCorrectURL = href.includes('cart') || href.includes('checkout');
		const isInputPresent = (document.querySelector(this.PROMO_INPUT_SELECTOR) || {}).offsetParent;
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		return isCorrectURL && isInputPresent && !isCartEmpty;
	}

	getExistingCodes() {
		const codes = IsOnCheckout()
			? [...document.querySelectorAll(this.EXISTING_CODE_SELECTOR_CHECKOUT)]
					.map(el => el.textContent.match(this.EXISTING_CODE_REGEX))
					.filter(Boolean)
					.map(elem => elem[1])
			: [...document.querySelectorAll(this.EXISTING_CODE_SELECTOR)]
					.map(el => el.textContent.split(' '))
					.filter(Boolean)
					.map(elem => elem[0].trim());

		this.EXISTING_CODE_EXIST = !!codes.length;

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeTestCodes() {
		if (IsOnCheckout()) {
			this.CSRF_TOKEN = this._getWindowObjectData('window.data.csrfToken');
		}
	}

	removeCodes(codes) {
		return Promise.each(codes, code =>
			this.removeCode(code).then(response => {
				this.lastResponse = response;
			}),
		)
			.then(() => (IsOnCheckout() ? this.lastResponse : this.getOrderSummary()))
			.then(res => {
				return {
					originalPrice: this.parseTotal(res),
					originalItemsCount: this.parseItemsCount(res),
				};
			});
	}

	beforeCheckCodes() {
		if (!this.EXISTING_CODE_EXIST && !IsOnCheckout()) {
			const shippingMethodElem = document.querySelector(this.SELECTED_SHIPPING_SELECTOR);
			const shippingMethodValue = document.querySelector(
				`label[for*=${shippingMethodElem ? `${shippingMethodElem.id}` : '#shipping-method-001'}] span`,
			);
			this.shippingPrice = elemToFloat(shippingMethodValue) || 0;
		}
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		let originPrice = originalPrice;
		if (this.shippingPrice) {
			originPrice -= this.shippingPrice;
		}
		log('originalPrice: %s', originPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return (IsOnCheckout() ? Promise.resolve(applyCodeResponse) : this.getOrderSummary()).then(
					orderSummary => {
						result.finalPrice = this.parseTotal(orderSummary);
						result.finalDiscount = originPrice - result.finalPrice;
						result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;

						if (
							result.finalPrice < originalPrice ||
							// Free gifts code is acceptable if it won't increase total
							(result.finalPrice === originalPrice && result.freeGifts > 0)
						) {
							workingDiscounts.push(result);
						}

						return this.removeCode(code).then(() => bus.$emit('code-checked', result));
					},
				);
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		// eslint-disable-next-line prefer-const
		let { originalPrice, originalItemsCount } = validationInfo;

		if (this.shippingPrice) {
			originalPrice -= this.shippingPrice;
		}

		// Sort by discount and free gifts count
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code)
				.then(res => (IsOnCheckout() ? res : this.getOrderSummary()))
				.then(responseHTML => {
					const finalPrice = this.parseTotal(responseHTML);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(responseHTML) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});

					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this.removeCode(discount.code).then(() => {
						if (this.REAPPLY_CODES_WHILE_STACKING) {
							return Promise.each(successfulCodes, code => this.applyCode(code));
						}
						return Promise.resolve();
					});
				}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	// Demandware AJAX Driver methods
	applyCode(code) {
		if (IsOnCheckout()) {
			this.applyQueryParams.set('code', code);
			this.applyQueryParams.set('csrf_token', this.CSRF_TOKEN);

			return this._sendRequestPOST(
				this.APPLY_CODE_URL_CHECKOUT,
				this.APPLY_REQUEST_HEADERS,
				undefined,
				this.applyQueryParams,
			);
		}
		this.applyQueryParams.set('format', 'ajax');
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._sendRequestGET(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	removeCode(code) {
		if (IsOnCheckout()) {
			this.removeQueryParams.set('code', code);
			this.removeQueryParams.set('csrf_token', this.CSRF_TOKEN);

			return this._sendRequestPOST(
				this.REMOVE_CODE_URL_CHECKOUT,
				this.REMOVE_REQUEST_HEADERS,
				undefined,
				this.removeQueryParams,
			);
		}

		if (this.REMOVE_CODE_PARAM_NAME) {
			this.applyQueryParams.set('format', 'ajax');
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	parseTotal(data) {
		if (IsOnCheckout()) {
			if (data) {
				return toFloat(data.cart.totals.orderTotal);
			}
			return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
		}
		const totalElement = extractElement(this.TOTAL_SELECTOR, data);
		return elemToFloat(totalElement);
	}

	checkRestoreDataCondition() {
		return window.location.href.includes('cart');
	}

	getUserData() {
		const shippingMethodElem = document.querySelector(this.SELECTED_SHIPPING_SELECTOR);
		return shippingMethodElem ? `#${shippingMethodElem.id}` : '#shipping-method-001';
	}

	setUserData(userData) {
		document.querySelector(userData).click();
		return Promise.resolve();
	}

	_getWindowObjectData(variableString) {
		// We will put generated request payload to this element
		const requestDataElement = document.createElement('div');
		requestDataElement.id = 'get-data-genie';
		Object.assign(requestDataElement.style, { display: 'none' });

		document.documentElement.appendChild(requestDataElement);

		// Script which will generate request payload
		const requestDataScript = document.createElement('script');
		requestDataScript.textContent = `document.getElementById("${
			requestDataElement.id
		}").innerText = JSON.stringify(${variableString});`;
		document.documentElement.appendChild(requestDataScript);
		const data = JSON.parse(requestDataElement.innerText);
		// Remove created blocks
		requestDataElement.parentNode.removeChild(requestDataElement);
		requestDataScript.parentNode.removeChild(requestDataScript);
		return data;
	}
};

export default {
	name: 'Harley-Davidson Footwear',
	domain: 'harley-davidsonfootwear.com',
	restoreUserData: true,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
	showDifferentDiscountMessage: true,
	stackable: true,
	Driver,
};
