import DemandwareAJAXDriver from './_base';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isOnCartPage = window.location.href.includes('/cart');
		this.TOTAL_SELECTOR = '.order-total .textalign-right';
		const BASE_URL = 'https://www.hushpuppies.com/on/demandware.store/Sites-hushpuppies_us-Site/default/';
		this.CODE_SELECTOR = this.isOnCartPage ? '.cartcoupon .value' : '.info-line';

		this.APPLY_CODE_URL = `${BASE_URL}Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}COBilling-UpdateSummary`;

		this.CART_PAGE_URL = 'https://www.hushpuppies.com/US/en/cart';

		this.applyQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		let codes = [...document.querySelectorAll(this.CODE_SELECTOR)].map(el => {
			if (this.isOnCartPage) {
				return el.innerText.trim();
			}
			return el.getAttribute('data-couponcode');
		});
		codes = [...new Set(codes)];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	applyCodes(codes) {
		// We should remove query parameters, to prevent form submitting again on cart page only
		// `revisit` completion action parameter can't be used
		// because on checkout page `POST` request should be used for page reloading
		if (document.querySelector('#cart-table')) {
			window.history.replaceState(
				window.history.state,
				window.document.title,
				window.location.href.split('?')[0],
			);
		}
		return this.applyCode(codes[0]);
	}
};

export default {
	name: 'Hush Puppies',
	domain: 'hushpuppies.com',
	Driver,
	isOnCartUrl(href) {
		const baseUrl = 'https://www.hushpuppies.com/US/en/';
		return href.startsWith(`${baseUrl}cart`) || href.startsWith(`${baseUrl}billing`);
	},
};
