import DemandwareAJAXDriver from './_base';

// Behavior description:
// Driver is not stackable
// codes can be applied on the cart and on the checkout page
// codes can be removed only on the cart
// a valid code can't be replaced by another code or removed by applying invalid code
// GET requests from the checkout page is used for applying code
// POST request from the cart is used for removing code

const DOMAIN_URL = 'destinationmaternity.com';
const DOMAIN_CART_URL = `${DOMAIN_URL}/cart/`;
const DOMAIN_CHECKOUT_URL = `${DOMAIN_URL}/billing/`;
const INPUT_CODE_FLD_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';

		this.CART_PAGE_URL = `https://www.${DOMAIN_CART_URL}`;
		this.CHECKOUT_PAGE_URL = `https://www.${DOMAIN_CHECKOUT_URL}`;
		const BASE_URL = `https://www.${DOMAIN_URL}/on/demandware.store/Sites-dmat-us-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = this._isOnCartPage ? this.CART_PAGE_URL : this.CHECKOUT_PAGE_URL;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl =
			href.includes(this.CART_PAGE_URL) ||
			href.includes(this.CHECKOUT_PAGE_URL) ||
			href.includes('/Cart-SubmitForm') ||
			href.includes('/shipping') ||
			href.includes(`${DOMAIN_URL}/on/demandware.store/Sites-dmat-us-Site/default/Cart-AddToWishlist`);
		const isCodeInput = document.querySelector(INPUT_CODE_FLD_SELECTOR);
		return isRightUrl && isCodeInput;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	// the checkout page also can startsWith cart
	_isOnCartPage() {
		return Boolean(document.querySelector(this.CART_FORM_SELECTOR));
	}
};

export default {
	name: 'Destination Maternity',
	domain: 'destinationmaternity.com',
	Driver,
	isSinglePageApp: true,
};
