import Promise from 'bluebird';
import DWBaseDriver from '../demandware/_base';
import bus from '../../bus';
import { sessionTimer } from '../../lib';
import { DOM, completionActions, browserUtils } from '../../../utils';

const { wait } = browserUtils;

const { extractElement } = DOM;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes
========Special case=========
+ Don't support Checkout because can get out sign in on checkout page
+ Driver works as stackable, but can apply only two codes with discount
+ other codes will be applied but without discount
*/

const BASE_URL = 'https://www.roamans.com';
const CART_URL = `${BASE_URL}/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-fbbrands-Site/default`;
const ANOTHER_CART_URL = `${BASE_REQUEST_URL}/Cart-SubmitForm`;

const Driver = class Driver extends DWBaseDriver {
	constructor() {
		super();
		this.CART_EMPTY_SELECTOR = '.mini-cart-empty';
		this.CART_COUPON_SELECTOR = '#dwfrm_cart_couponCode';
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.MERCHANT_CODE_SELECTOR = '.oc-content-row[data-coupon] .oc-coupon-code';
		this.MERCHANT_CODE_IMG = 'map[id*="monetate_selectorBanner"] area[alt]';
		this.MERCHANT_CODE_REGEX = /code:?\s(\w*)/i;
		this.isStackable = false;
	}

	watchForCartUrl() {
		sessionTimer.initialize({
			actionInterval: super.watchForCartUrl(),
			timeout: 20,
		});
	}

	checkCondition() {
		const windowLocation = window.location.href.toLowerCase();

		const isWishList = windowLocation.includes('cart-addtowishlist');
		// For checkout and shipping pages we need have billing section,
		// on the cart page we can omit it
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		const isCart =
			(windowLocation.includes(CART_URL) ||
				isWishList ||
				windowLocation.includes(ANOTHER_CART_URL.toLowerCase())) &&
			document.querySelector(this.CART_COUPON_SELECTOR);
		return isCart && !isCartEmpty;
	}

	async getMerchantCodes() {
		const iframeElem = document.createElement('iframe');
		iframeElem.src = CART_URL;
		Object.assign(iframeElem.style, { display: 'none' });
		document.documentElement.appendChild(iframeElem);
		const getCodeElem = () => {
			return !!(
				[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_IMG)].length > 0 &&
				[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_SELECTOR)].length > 0
			);
		};
		await wait(getCodeElem, 20000, 300, true);
		if (getCodeElem()) {
			return [
				...new Set([
					...[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
						.map(el => (el.innerText.trim().toLowerCase() === 'no code needed' ? '' : el.innerText.trim()))
						.filter(Boolean),
					...[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_IMG)]
						.map(el => el.alt.match(this.MERCHANT_CODE_REGEX))
						.filter(Boolean)
						.map(match => match[1]),
				]),
			];
		}
		return [];
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;

		// Sort by discount
		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(discounts, discount =>
			this.applyCode(discount.code, this._requestData()).then(html => {
				this.lastResponse = html;
				const finalPrice = this.parseTotal(html);
				const finalDiscount = originalPrice - finalPrice;
				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
				});
				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}
				return this.removeCode(this._removeValueName(discount.code), this._requestData()).then(
					removeCodeResponse => {
						this.lastResponse = removeCodeResponse;
						return Promise.resolve();
					},
				);
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	_removeValueName(code) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, this.lastResponse, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.trim().toLowerCase() === code.toLowerCase();
		});

		return matchingCodesElement
			? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).name
			: this.DEFAULT_REMOVE_CODE_VALUE;
	}
};

export default {
	name: "Roaman's",
	domain: 'roamans.com',
	Driver,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
};
