/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["watchForCartUrl"] }] */

import DemandwareAJAXDriver from './_base';

const CART_PAGE_URL = 'https://www.agacistore.com/shoppingbag';
const CHECKOUT_INPUT = document.querySelector('#dwfrm_billing_couponCode');
/*
Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: yes (description)
•    Gather codes on site pages: no
•    Can apply codes on cart and checkout(only when shipping is not active)
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;
		this.TOTAL_SELECTOR = '.order-total .order-total-price-right, #cart-total';
		this.APPLIED_CODES_SELECTOR_ON_CHECKOUT = '#secondary .order-discount';
		this.APPLIED_CODES_SELECTOR_ON_CART = '.cart-coupon-code .value';
		this.APPLIED_CODE_REGEX = CHECKOUT_INPUT ? /(.+)\s\(/ : /(.+)\s/;

		const BASE_URL = 'https://www.agacistore.com/on/demandware.store/Sites-agaci-Site/default/';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = CHECKOUT_INPUT ? `${BASE_URL}/COBilling-UpdateSummary` : '';

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.CHECKOUT_SHIP_ELEM = '#shipping-section';
	}

	checkCondition() {
		const checkoutShipElem = CHECKOUT_INPUT
			? document.querySelector(this.CHECKOUT_SHIP_ELEM).style.opacity !== '1'
			: false;
		const currentState = !!CHECKOUT_INPUT && !CHECKOUT_INPUT.disabled && checkoutShipElem;
		return currentState;
	}

	getExistingCodes() {
		const checkoutCodeEl = document.querySelector(this.APPLIED_CODES_SELECTOR_ON_CHECKOUT);
		const checkoutCodes = checkoutCodeEl
			? [checkoutCodeEl.nextElementSibling.querySelector('td:last-child').innerText]
			: [];
		const codes = CHECKOUT_INPUT
			? checkoutCodes
			: [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR_ON_CART)]
					.filter(Boolean)
					.map(element => element.innerText.trim().match(this.APPLIED_CODE_REGEX)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	beforeCheckCodes() {
		return this.getCartPage().then(responseHTML => this.parseTotal(responseHTML));
	}
};

export default {
	name: "A'Gaci Store",
	domain: 'agacistore.com',
	Driver,
	isSinglePageApp: Boolean(CHECKOUT_INPUT),
	stackable: true,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_PAGE_URL) && (!!CHECKOUT_INPUT || document.querySelector('#dwfrm_cart_couponCode'));
	},
};
