import DemandwareAJAXDriver from './_base';

const CART_PAGE_URL = 'https://www.carolsdaughter.com/cart';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: no
•    Active pages: cart, checkout(payment step)
Optional:
•    Show message about free shipping on the cart page: no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: no */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order_total td:nth-child(2)';

		this.CODE_REMOVE_BTN_SELECTOR = '.remove_coupon, .couponcode_remove_button';
		this.CART_PAGE_URL = CART_PAGE_URL;

		const BASE_URL =
			'https://www.carolsdaughter.com/on/demandware.store/Sites-carolsdaughter-us-Site/default/Cart-AddRemoveCoupon';
		this.APPLY_CODE_URL = BASE_URL;
		this.REMOVE_CODE_URL = BASE_URL;
		this.UPDATE_REQUEST_URL = this.CART_PAGE_URL;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.isStackable = true;

		this.CODE_ELEM_SELECTOR = '#form_couponcode';
		this.EMPTY_CART_SELECTOR = '.cart_empty';

		this.applyQueryParams.set('format', 'ajax');
		this.applyQueryParams.set('type', 'add');

		this.removeQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('type', 'remove');
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCart = href.startsWith(CART_PAGE_URL) || href.includes('/Cart-AddToWishlist');
		const isOnCheckout = href.includes('/COCheckout-Show');
		const checkoutCouponForm = document.querySelector(this.CODE_ELEM_SELECTOR);
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		return !emptyCart && totalElem && (isOnCart || (isOnCheckout && checkoutCouponForm));
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.CODE_REMOVE_BTN_SELECTOR)]
			.filter(Boolean) // need for checkout
			.map(codeElement => codeElement.getAttribute('value') || codeElement.getAttribute('data-coupon'))
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: "Carol's Daughter",
	domain: 'carolsdaughter.com',
	stackable: true,
	Driver,
	isSinglePageApp: true,
};
