import DemandwareAJAXDriver from './_base';

/** Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout (Payment step with redirect)
+ Is driver SPA: no
+ Gather codes on site pages: no
 *
 * Added 25s wait for the outclick tab to close
 * as the driver works much faster than it takes for the outclick tab to close.
 * */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#csc-amount-total';
		this.APPLIED_CODES_SELECTOR = '#csc-table-coupon-codes [data-transaction]';
		this.PAYMENT_STEP_SELECTOR = '#csc-collapse-payment.show';
		this.EMPTY_CART_SELECTOR = '.cart-empty:not([class*="d-none"])';
		this.CART_CODE_INPUT_SELECTOR = '#csc-coupon-code';
		this.isStackable = true;

		const BASE_URL = 'https://www.mountainhardwear.com/on/demandware.store/Sites-MountainHardwear_US-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/SinglePageCheckout-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/SinglePageCheckout-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/SinglePageCheckout-UpdateOrderSummary`;

		this.CART_PAGE_URL = 'https://www.mountainhardwear.com/cart';
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'removeCoupon';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		this.APPLY_RESPONSE_KEY = null;

		this.OUTCLICK_TAB_CLOSE_TIMEOUT = 25;
	}

	checkCondition() {
		const cartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const cartCodeInput = document.querySelector(this.CART_CODE_INPUT_SELECTOR);
		const paymentStep = (document.querySelector(this.PAYMENT_STEP_SELECTOR) || {}).offsetParent;
		const promoInputVisible = cartCodeInput || paymentStep;
		const currentState = !cartEmpty && promoInputVisible;
		return currentState;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			element => element.dataset.transaction,
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this._watchForOutclickTab();
	}

	_watchForOutclickTab() {
		// outclick tab won't close by itself sometimes
		return new Promise(resolve => {
			chrome.runtime.onMessage.addListener(msg => msg.tabClosed && resolve());
			return setTimeout(() => resolve(), this.OUTCLICK_TAB_CLOSE_TIMEOUT * 1000);
		});
	}
};

export default {
	name: 'Mountain Hardware',
	domain: 'mountainhardwear.com',
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 25, // driver is fast
	Driver,
};
