import DemandwareAJAXDriver from './_base';
import { DOM, format, completionActions } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
Optional:
•    Show message about free shipping on the cart page (without discount info):no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no 
•    can't avoid redirect from the billing to the shipping page
*/

const BASE_SITE_URL = 'https://www.foodsaver.com';
const CART_PAGE_URL = `${BASE_SITE_URL}/cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.isStackable = true;

		const BASE_URL = `${BASE_SITE_URL}/on/demandware.store/Sites-food-saver-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
	}

	getExistingCodes() {
		return (this._isOnCartPage() ? Promise.resolve(null) : this.getCartPage()).then(responseHTML => {
			const codes = [...extractElement(this.CODE_VALUE_SELECTOR, responseHTML, true)].map(element =>
				element.innerText.trim(),
			);
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	applyCodes(codes) {
		if (this._isOnCartPage()) {
			// We should remove query parameters, to prevent form submitting again on cart page only
			// `cannotRefresh` parameter can't be used
			// because on checkout page `POST` request should be used for page reloading
			window.history.replaceState(
				window.history.state,
				window.document.title,
				window.location.href.split('?')[0],
			);
		}
		return this.applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const taxEl = extractElement(this.TAX_SELECTOR, response);
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			const shipping = shippingEl ? elemToFloat(shippingEl) || 0 : 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'Food Saver',
	domain: 'foodsaver.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		const emptyElem = document.querySelector('.total-products');
		return document.querySelector('.mini-cart-empty') || (emptyElem && emptyElem.innerText === '(0)');
	},
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) ||
				href.startsWith(`${BASE_SITE_URL}/shipping`) ||
				href.startsWith(`${BASE_SITE_URL}/customer`) ||
				href.startsWith(`${BASE_SITE_URL}/billing`) ||
				href.startsWith(
					`${BASE_SITE_URL}/on/demandware.store/Sites-food-saver-Site/default/Cart-AddToWishlist`,
				)) &&
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
};
