import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(Shipping page)
+ Is driver SPA: no
+ Gather codes on site pages: no
+ maxCoupons: 12, long code testing time
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		const applyFormValues = extractFormValues('form[name="frmCoupon"]');
		const removeFormValues = extractFormValues('form.coupanapplied');

		// Overridden values
		this.TOTAL_SELECTOR = 'h3.cart-total';
		this.APPLIED_CODE_SELECTOR = '.coupanapplied';
		this.TAX_SELECTOR = '.taxes span';
		this.SHIPPING_SELECTOR = '.shippingprice span';
		this.DONATION_SELECTOR = '.donation span';
		this.isStackable = true;

		const BASE_URL = 'https://www.verabradley.com/store/us/cart';
		this.APPLY_CODE_URL = `${BASE_URL}/coupons`;
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.UPDATE_REQUEST_URL = BASE_URL;

		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';
		this.REMOVE_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponRepoID';

		if (applyFormValues) {
			this.applyQueryParams = applyFormValues.params;
		}
		this.applyQueryParams.set('/atg/commerce/promotion/CouponFormHandler.claimCoupon', 'APPLY');

		if (removeFormValues) {
			this.removeQueryParams = removeFormValues.params;
		}
		this.removeQueryParams.set('remove', 'Remove');
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
			.filter(elem => elem.id !== 'dummyCouponForm')
			.map(elem => elem.id);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const calculatedShipping = Number.isNaN(shipping) ? 0 : shipping;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			const donation = elemToFloat(extractElement(this.DONATION_SELECTOR, response));
			return this.parseTotal(response) - calculatedShipping - tax - donation;
		});
	}
};

export default {
	name: 'Vera Bradley',
	domain: 'verabradley.com',
	stackable: true,
	maxCoupons: 12,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.verabradley.com/us/cart') ||
			href.startsWith('https://www.verabradley.com/us/checkout/shipping')
		);
	},
	isCartEmpty() {
		return document.querySelector('.emptycart');
	},
};
