import DemandwareAJAXDriver from './_base';
import { completionActions, format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/* Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
   - Cart page
   - Checkout page

+ Is driver SPA yes
+ Gather codes on site pages: no
 */

const BASE_URL = 'https://www.melissaanddoug.com';
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-melissaanddoug-Site/default`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.bfx-total-grandtotal';
		this.TAX_SELECTOR = '.order-totals__order-sales-tax div.bfx-total-tax';
		this.SHIPPING_SELECTOR = '.order-totals__order-shipping div.bfx-price';

		this.PRODUCT_ITEM_SELECTOR = '.mini-cart-product';

		this.CODES_ELEMENTS_SELECTOR = '.small-column .item-specific-properties';
		this.CODE_VALUE_SELECTOR = '.item-details__value.clearfix';

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;
		this.CART_PAGE_URL = `${BASE_URL}/cart`;

		// possible checkout url options
		this.CHECKOUT_PAGE_URLS = [`${BASE_URL}/shipping`, `${BASE_URL}/checkout`, `${BASE_URL}/billing`];

		this.isStackable = true;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');

		this.REAPPLY_CODES_WHILE_STACKING = true;
		this.CODE_FIELD_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';

		this.EMPTY_CART_SELECTOR = '#primary .cart-empty';
		this.CHECKOUT_BTN_SELECTOR = '.malachite-btn.button.cart-actions__checkout-button.bfx-checkout';
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCondition() {
		// Activate extension popup only when the code input is visible and we're on cart
		const onCart = window.location.href.startsWith(this.CART_PAGE_URL);
		const onCheckout =
			window.location.href.startsWith(this.CHECKOUT_PAGE_URL) ||
			window.location.href.startsWith(`${BASE_REQUEST_URL}/Cart-AddToWishlist`);

		const codeInput = document.querySelector(this.CODE_FIELD_SELECTOR);

		const emptyCartMsg = document.querySelector(this.EMPTY_CART_SELECTOR);

		return (
			(onCart || onCheckout) &&
			codeInput &&
			codeInput.offsetParent &&
			!(emptyCartMsg && emptyCartMsg.offsetParent)
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const getSummaryValue = selector => elemToFloat(extractElement(selector, responseHTML));

			const tax = getSummaryValue(this.TAX_SELECTOR) || 0;
			const shipping = getSummaryValue(this.SHIPPING_SELECTOR) || 0;
			const total = getSummaryValue(this.TOTAL_SELECTOR);

			return total - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(`${BASE_URL}/cart/`, {
			credentials: 'include',
			method: 'GET',
		}).then(response => response.text());
	}

	_isOnCartPage() {
		return this.CHECKOUT_PAGE_URLS && document.querySelector(this.CHECKOUT_BTN_SELECTOR);
	}
};

export default {
	name: 'Melissa And Doug',
	domain: 'melissaanddoug.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
};
