import DemandwareAJAXDriver from './_base';
import { completionActions, format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;
/* Behaviour
-    Existing code: yes
-    Remove code: yes
-    Stackable: yes
-    Free gift support: yes
-    'isValid' implemented: yes
-    Manually apply code: no
-    Is driver SPA and add the condition to show popup: no
 * Special case:
We can't get price with tax on the checkout page
Don't support checkout pages
 */

const BASE_URL = 'https://www.ashleystewart.com';
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-ashleystewart-Site/en_US`;
const CART_PAGE_URL = `${BASE_URL}/cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.PRODUCT_ITEM_SELECTOR = '.cart-row .item-total, .checkout-mini-cart .mini-cart-product';

		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		sessionTimer.initialize();
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const getSummaryValue = selector => elemToFloat(extractElement(selector, responseHTML));

			const tax = getSummaryValue(this.TAX_SELECTOR) || 0;
			const shipping = getSummaryValue(this.SHIPPING_SELECTOR) || 0;
			const total = getSummaryValue(this.TOTAL_SELECTOR);

			return total - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(`${BASE_URL}/cart/`, {
			credentials: 'include',
			method: 'GET',
		}).then(response => response.text());
	}
};

export default {
	name: 'Ashley Stewart',
	domain: 'ashleystewart.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			document.querySelector('#dwfrm_cart_couponCode') &&
			(href.includes('/cart') || href.includes('/Cart-UpdateCartAJAX'))
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
