import DemandwareAJAXDriver from './_base';
import { format, DOM, browserUtils, completionActions } from '../../../utils';

const BASE_URL = 'https://www.timex.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const INPUT_SELECTOR = '#dwfrm_cart_couponCode';

const { elemToFloat } = format;
const { extractElement } = DOM;
const { setCookie } = browserUtils;

/* Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
	- Cart page
	- Checkout page
+ Is driver SPA no
+ Gather codes on site pages: no */

const URLS = [`${BASE_URL}/billing`, `${BASE_URL}/shipping`, `${BASE_URL}/checkout`, CART_PAGE_URL];
const isOnCart = () => document.querySelector('#cart-items-form');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';

		this.APPLY_CODE_URL = 'https://www.timex.com/on/demandware.store/Sites-timexus-Site/en_US/Cart-AddCouponJson';
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.APPLY_REQUEST_HEADERS = {
			'content-type': 'application/x-www-form-urlencoded',
			'sec-fetch-dest': 'document',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
		this.SHIPPING_STEP_SELECTOR = '.step-1.active';
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const taxElem = extractElement(this.TAX_SELECTOR, responseHTML);
			const taxValue = taxElem ? elemToFloat(taxElem) : 0;
			const shippingElem = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const shippingValue = shippingElem ? elemToFloat(shippingElem) || 0 : 0;
			const total = this.parseTotal(responseHTML);
			return total - taxValue - shippingValue;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_sendRequestGET(requestURL, headers, queryParams) {
		setCookie('_gali', 'add-coupon');
		return this._sendRequest('GET', requestURL, headers, queryParams);
	}

	_isOnCartPage() {
		return isOnCart();
	}
};

export default {
	name: 'Timex',
	domain: 'timex.com',
	Driver,
	maxCoupons: 12, // due to testing time on Checkout
	isOnCartUrl(href) {
		return (
			URLS.some(url => href.startsWith(url)) &&
			document.querySelector(INPUT_SELECTOR) &&
			!document.querySelector('.step-1.active')
		);
	},
	completionAction: isOnCart() ? completionActions.revisit : completionActions.default,
};
