import DemandwareAJAXDriver from './_base';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.isStackable = true;

		const BASE_URL = 'https://www.keds.com/on/demandware.store/Sites-keds_us-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Cart-UpdateBasketTotals`;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		let codes = [];
		if (window.location.pathname.includes('/cart')) {
			codes = [...document.querySelectorAll('.cartcoupon .value')].map(elem => elem.innerText.trim());
		} else {
			const codesElements = [...document.querySelectorAll('[data-couponcode]')].map(el =>
				el.getAttribute('data-couponcode'),
			);
			codes = [...new Set(codesElements)];
		}

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Keds',
	domain: 'keds.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.keds.com/en/cart') || href.startsWith('https://www.keds.com/en/billing');
	},
};
