import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';
import bus from '../../bus';
import { sessionTimer } from '../../lib';

const BASE_URL = 'tillys.com';
const DOMAIN = `https://www.${BASE_URL}`;

/** Behaviour description:
 Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support:  no
•    ‘isValid’ implemented: no
•    Manually apply code: no
 * */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.remove-coupon';
		this.isStackable = true;

		const REQUEST_URL = 'https://www.tillys.com/on/demandware.store/Sites-tillys-Site/default';
		this.APPLY_CODE_URL = `${REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${REQUEST_URL}/COBilling-RemoveCouponCode`;
		this.UPDATE_REQUEST_URL = `${REQUEST_URL}/COBilling-UpdateSummary?checkoutStep=3`;
		this.CART_PAGE_URL = `${DOMAIN}/cart`;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.CODE_INPUT_ELM_SELECTOR = '#dwfrm_cart_couponCode';
		this.OUTCLICK_TAB_CLOSE_TIMEOUT = 15;
		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.lastStackedCode = null;
		this.EMPTY_CART = '.cart-empty';
		this.ERROR_PAGE = '.error-page-message';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 10,
		});
	}

	checkCondition() {
		const errorPage = document.querySelector(this.ERROR_PAGE);
		const emptyCart = document.querySelector(this.EMPTY_CART);
		const invalidCart = !!(errorPage && errorPage.offsetParent) || !!(emptyCart && emptyCart.offsetParent);
		const correctPages = ['billing', 'cart'];
		const { href } = window.location;
		const correctPage = href.startsWith(BASE_URL) || correctPages.some(urlPart => href.includes(urlPart));

		const codeInputField = document.querySelector(this.CODE_INPUT_ELM_SELECTOR);

		return correctPage && codeInputField && !invalidCart;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(el => el.getAttribute('code'));

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this._watchForOutclickTab();
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;
		// Sort by discount
		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this.applyCode(discount.code)
				.then(() => this.getOrderSummary())
				.then(responseHTML => {
					const finalPrice = this.parseTotal(responseHTML);
					const finalDiscount = originalPrice - finalPrice;
					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
					});
					if (finalPrice < currentPrice) {
						this.lastStackedCode = discount.code;
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						return Promise.resolve();
					}
					return this.removeCode(discount.code).then(() => {
						if (this.REAPPLY_CODES_WHILE_STACKING) {
							return Promise.each(successfulCodes, code => this.applyCode(code));
						}
						return Promise.resolve();
					});
				}),
		).then(() =>
			this.removeCode(this.lastStackedCode).then(() =>
				bus.$emit('stackable-complete', {
					codes: successfulCodes,
					finalPrice: currentPrice,
					finalDiscount: currentDiscount,
				}),
			),
		);
	}

	beforeApplyCodes() {
		// this page looks like cart but upon page reload it shows an error
		const unstableCartPage = window.location.href.includes('Cart-SubmitForm');
		window.history.replaceState(
			window.history.state,
			window.document.title,
			unstableCartPage ? this.CART_PAGE_URL : window.location.href.split('?')[0],
		);
	}

	applyCodes() {
		return this.applyCode(this.lastStackedCode);
	}

	_watchForOutclickTab() {
		// 30s max watch for outclick tab to close
		return new Promise(resolve => {
			chrome.runtime.onMessage.addListener(msg => msg.tabClosed && resolve());
			return setTimeout(() => resolve(), this.OUTCLICK_TAB_CLOSE_TIMEOUT * 1000);
		});
	}
};

export default {
	name: 'Tillys',
	domain: 'tillys.com',
	completionAction: completionActions.revisit,
	Driver,
	isSinglePageApp: true,
	stackable: true,
};
