import DemandwareAJAXDriver from './_base';

const CHECKOUT_PAGE_URL = 'https://www.ardene.com/us/en/checkout-billing/';
const CART_PAGE_URL = 'https://www.ardene.com/us/en/cart';
const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.item-details .value';

		const BASE_URL = 'https://www.ardene.com/on/demandware.store/Sites-ardene-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-DeleteCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(codeElement =>
			codeElement.innerText.trim(),
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Ardene',
	domain: 'ardene.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith(CART_PAGE_URL) || href.startsWith(CHECKOUT_PAGE_URL);
	},
};
