import DemandwareAJAXDriver from './_base';

/* Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
    - Cart page
    - Checkout page (billing step)
+ Is driver SPA no
+ Gather codes on site pages: no
+
+ Credentials (for signing in):
	- email address: oster@d-link.ml; password: 12345Temporary
*/

const BASE_URL = `${window.location.protocol}//www.oster.com`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-oster-Site/default`;
const CART_PAGE_URL = `${BASE_URL}/cart`;
const CUSTOMER_URL = `${BASE_URL}/customer`;
const BILLING_URL = `${BASE_URL}/billing`;
const SHIPPING_URL = `${BASE_URL}/shipping`;
const WISHLIST_URL = `${BASE_URL}/on/demandware.store/Sites-oster-Site/default/Cart-AddToWishlist`;
const isOnCart = () => Boolean(document.querySelector('#dwfrm_cart_couponCode'));

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = isOnCart() ? CART_PAGE_URL : `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_FORM_SELECTOR = '#cart-items-form, #dwfrm_billing';

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getOrderSummary() {
		// sending 2 identical requests and parsing total from the 2nd response,
		// since 1st one, for some reason, does not contain valid total amount
		return super.getOrderSummary().then(() => super.getOrderSummary());
	}

	_isOnCartPage() {
		return isOnCart();
	}
};

export default {
	name: 'Oster',
	domain: 'oster.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		const urls = [CART_PAGE_URL, CUSTOMER_URL, BILLING_URL, SHIPPING_URL, WISHLIST_URL];
		return (
			urls.some(url => href.includes(url)) &&
			document.querySelector('#dwfrm_billing_couponCode, #dwfrm_cart_couponCode')
		);
	},
	cartContinueUrl: isOnCart() ? CART_PAGE_URL : BILLING_URL,
	isCartEmpty() {
		return Boolean(document.querySelector('.mini-cart-empty'));
	},
};
