import DemandwareAJAXDriver from './_base';
import { completionActions, DOM, format } from '../../../utils';

const { extractElement, extractElementFromHTML } = DOM;
const { elemToFloat, toFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: yes
*/

const BASE_URL = 'https://www.nautica.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const SHIPPING_URL = `${BASE_URL}/shippingstart`;
const BILLING_URL = `${BASE_URL}/billing`;

function parseTotalValue(selector, html) {
	return elemToFloat(extractElement(selector, html));
}

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.TOTAL_SELECTOR = '.estimated-total td:last-child';

		const BASE_REQUEST_URL = 'https://www.nautica.com/on/demandware.store/Sites-nau-Site/default';
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.PRODUCT_ITEM_SELECTOR = '.cart-row';
		this.PRODUCT_NAME_SELECTOR = '.name';
		this.ITEM_PRICE_SELECTOR = '.price-total';
		this.DONATION_REGEX = /donation/i;

		this.applyQueryParams.set('format', 'ajax');

		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.PROMOTION_SELECTOR = '[class*=coupon-default]';

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
	}

	beforeTestCodes() {
		this.donationAmount = [...document.querySelectorAll(this.PRODUCT_ITEM_SELECTOR)]
			.filter(el => el.querySelector(this.PRODUCT_NAME_SELECTOR).innerText.match(this.DONATION_REGEX))
			.map(el => toFloat(el.querySelector(this.ITEM_PRICE_SELECTOR).innerText))
			.reduce((a, b) => a + b, 0);
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.PROMOTION_SELECTOR)].map(el => el.innerText).filter(Boolean);
	}

	getExistingCodes() {
		return Promise.resolve()
			.then(() => {
				if (this._isOnCartPage()) {
					return document.documentElement.innerHTML;
				}
				return this.getCartPage();
			})
			.then(html => {
				const appliedCodesSelector = this.APPLIED_CODES_SELECTOR
					? this.APPLIED_CODES_SELECTOR
					: this.CODE_VALUE_SELECTOR;
				const codes = [
					...new Set(
						[...extractElementFromHTML(html, appliedCodesSelector, true)].map(element =>
							element.innerText.trim(),
						),
					),
				];

				return {
					codes,
					codeCount: codes.length,
				};
			});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount - this.donationAmount;
		});
	}

	_getRemoveValueName(code, html) {
		const codesElements = extractElement(this.CODES_ELEMENTS_SELECTOR, html, true);
		const matchingCodesElement = [...codesElements].find(element => {
			const codesElement = element.querySelector(this.CODE_VALUE_SELECTOR);
			return codesElement && codesElement.innerText.trim().toLowerCase() === code.toLowerCase();
		});

		return matchingCodesElement ? matchingCodesElement.querySelector(this.REMOVE_CODE_SELECTOR).name : null;
	}

	parseTotal(html) {
		return parseTotalValue(this.TOTAL_SELECTOR, html);
	}
};

export default {
	name: 'Nautica',
	domain: 'nautica.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	cartContinueUrl: window.location.href.startsWith(CART_PAGE_URL) ? CART_PAGE_URL : window.location.href,
	isOnCartUrl(href) {
		const subtotalSelector = '.order-totals-table .bfx-total-subtotal';
		const hrefLowerCase = href.toLowerCase();
		const subtotal = document.querySelector(subtotalSelector);
		return (
			subtotal &&
			parseTotalValue(subtotalSelector) &&
			!document.querySelector('.checkout-order-totals') &&
			document.querySelector('[name="dwfrm_cart_couponCode"]') &&
			(hrefLowerCase.startsWith(CART_PAGE_URL) ||
				hrefLowerCase.startsWith(SHIPPING_URL) ||
				hrefLowerCase.startsWith(BILLING_URL))
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
