import DemandwareDriver from './_base';

/* Driver behavior:
• Existing code: yes (sometimes no - in case when we have text `15 %`)
• Remove code: no (We can't be sure that codes which we got in getExistingCodes is right)
• Code retesting: yes
• Stackable: yes
• Free gift support: no
• isValid implemented: yes
• Show message about free shipping: no
• Driver is SPA: yes, on cart & checkout
• Gather merchant codes: no
• So, we don't remove codes, only add.
 */
const BASE_URL = 'https://www.intermixonline.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-Intermix-Site/en_US`;
const cartContinueUrl = () => {
	const { href } = window.location;
	if (href.includes('Cart-SubmitForm')) {
		return CART_PAGE_URL;
	}
	if (href.includes('COBilling-Billing')) {
		return `${BASE_URL}/revieworder`;
	}
	return href;
};

const Driver = class Driver extends DemandwareDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .bfx-total-grandtotal, .order-subtotal .bfx-total-subtotal';
		this.CODE_VALUE_SELECTOR = '.discount span:last-child';

		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';

		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.EMPTY_CART_SELECTOR = '.mini-cart-empty';
	}

	checkCondition() {
		const { href } = window.location;
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isRightUrl =
			href.includes('cart') ||
			href.includes('revieworder') ||
			href.includes('orderconfirmation') ||
			href.includes('COBilling-Billing') ||
			href.includes('Cart-SubmitForm');
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const currentState = isCodeInput && isCodeInput.offsetParent && isRightUrl && !isCartEmpty;
		return currentState;
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.CODE_VALUE_SELECTOR)]
				.filter(element => !/%/.test(element.innerText))
				.map(element => element.innerText.replace(' Not Applied', '').replace(' Applied', '')),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCode() {
		return Promise.resolve();
	}
};

export default {
	name: 'INTERMIX',
	domain: 'intermixonline.com',
	stackable: true,
	isSinglePageApp: true,
	cartContinueUrl: cartContinueUrl(),
	Driver,
};
