import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

// Driver behavior description:
// -    Existing code: yes
// -    Remove code: yes
// -    Stackable: yes
// -    Free gift support: no
// -    ‘isValid’ implemented: yes
// Optional:
// -    Is driver SPA and add the condition to show popup: no
// - 	run code testing on the cart, wish list page and checkout-billing(user will be redirected to the previous step, data will be erased)

const BASE_URL = 'https://www.speckproducts.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const CHECKOUT_URL = `${BASE_URL}/checkout`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-speck-Site/default`;
const SHIPPING_URL = `${BASE_REQUEST_URL}/COShipping-Start`;
const CART_WISHLIST_URL = `${BASE_REQUEST_URL}/Cart-AddToWishlist`;
const isOnCartPage = () =>
	(window.location.href.startsWith(CART_PAGE_URL) || window.location.href.startsWith(CART_WISHLIST_URL)) &&
	document.querySelector('#dwfrm_cart_couponCode');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.SUCCESS_MESSAGE_SELECTOR = '.coupon .success, .coupon-sucess';
		this.MERCHANT_CODES_SELECTOR = '.coupon';

		this.TOTAL_SELECTOR = document.querySelector('.cart-logo')
			? '.order-subtotal td'
			: '.order-total td:last-child';
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)]
					.map(codeElement => codeElement.textContent.trim())
					.filter(el => !!el && el.match(/^\S+$/gm)),
			),
		];
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return isOnCartPage();
	}
};

export default {
	name: 'Speck',
	domain: 'speckproducts.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) ||
				href.startsWith(CHECKOUT_URL) ||
				href.startsWith(SHIPPING_URL) ||
				href.startsWith(CART_WISHLIST_URL)) &&
			document.querySelector('#dwfrm_cart_couponCode, #dwfrm_billing_couponCode')
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.mini-cart-empty'));
	},
};
