import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { DOM, format, log, completionActions } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;
const BASE_SITE_URL = 'https://www.uniqlo.com/us/en';
const CART_PAGE_URL = `${BASE_SITE_URL}/cart`;
const INPUT_CART_SELECTOR = '#dwfrm_cart_couponCode';
const isOnCartPage = () =>
	window.location.href.startsWith(CART_PAGE_URL) && Boolean(document.querySelector(INPUT_CART_SELECTOR));

// Driver behavior description:
// -    Existing code: yes
// -    Remove code: yes
// -    Stackable: yes
// -    Free gift support: yes
// -    ‘isValid’ implemented: yes
// Optional:
// -    Is driver SPA and add the condition to show popup: yes
// -		we support cart and checkout (payment) pages with reloading

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		const isTotal = document.querySelector('.order-total .order-value');
		this.TOTAL_SELECTOR = isTotal ? '.order-total .order-value' : '.order-subtotal .order-value:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.MERCHANT_CODES_SELECTOR = '.bx-click-to-copy .coupon';
		this.PRODUCT_ITEM_SELECTOR = '.mini-cart-product';
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.isStackable = true;

		const BASE_URL = 'https://www.uniqlo.com/on/demandware.store/Sites-UniqloUS-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-SubmitForm`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	checkCondition() {
		const isInput = document.querySelector(`${this.CODE_INPUT_SELECTOR}, ${INPUT_CART_SELECTOR}`);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isInput && isTotal;
	}

	getMerchantCodes() {
		const welcomeOfferCode = document.querySelector(this.MERCHANT_CODES_SELECTOR);
		return welcomeOfferCode ? [welcomeOfferCode.textContent] : [];
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};

				if (
					applyCodeResponse.status &&
					(applyCodeResponse.status === 'NO_ACTIVE_PROMOTION' ||
						applyCodeResponse.status === 'COUPON_CODE_UNKNOWN')
				) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;
					result.isValid = true;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const taxEl = extractElement(this.TAX_SELECTOR, response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	parseTotal(html) {
		const totalElem = document.querySelector('.order-total .order-value');
		if (html && !totalElem) {
			this.TOTAL_SELECTOR = '.order-total .order-value';
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, html));
	}

	_isOnCartPage() {
		return isOnCartPage();
	}
};

export default {
	name: 'UNIQLO',
	domain: 'uniqlo.com',
	stackable: true,
	isSinglePageApp: true,
	enforceOriginalCaseCodes: true,
	cartContinueUrl: isOnCartPage() ? window.location.href : `${BASE_SITE_URL}/Billing/`,
	completionAction: completionActions.revisit,
	Driver,
};
