import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { completionActions, DOM, log } from '../../../utils';

const { extractFormValues } = DOM;

const CART_PAGE_URL = 'https://www.bobstores.com/shoppingcart';
const CART_INPUT = 'dwfrm_cart_couponCode';
const CHECKOUT_INPUT = 'dwfrm_billing_couponCode';
const isOnCart = () => document.querySelector(`#${CART_INPUT}`);

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.CART_FORM_SELECTOR = 'form[name=dwfrm_cart]';
		this.CHECKOUT_FORM_SELECTOR = '#dwfrm_billing';

		this.APPLY_CODE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = isOnCart() ? CART_INPUT : CHECKOUT_INPUT;
		this.REMOVE_CODE_PARAM_NAME = CART_INPUT;
		this.CODE_INPUTS_SELECTOR = `#${CART_INPUT}, #${CHECKOUT_INPUT}`;
		this.CART_EMPTY_SELECTOR = '.cart-empty';
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const isCodeInput = document.querySelector(this.CODE_INPUTS_SELECTOR);
			const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
			const currentState = isCodeInput && !isCartEmpty;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	beforeTestCodes() {
		this.applyQueryParams = extractFormValues(
			isOnCart() ? this.CART_FORM_SELECTOR : this.CHECKOUT_FORM_SELECTOR,
		).params;
		return Promise.resolve();
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};

				const successState = this._getValueFromJSON(response);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this.parseTotal(response);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(response) - originalItemsCount;

				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					workingDiscounts.push(result);
				}

				return this.removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);
		if (isOnCart()) {
			this.applyQueryParams.set('dwfrm_cart_addCoupon', 'dwfrm_cart_addCoupon');
		} else {
			this.applyQueryParams.set('dwfrm_billing_applyCoupon', 'Apply');
		}

		return this._sendRequestPOST(window.location.href, this.APPLY_REQUEST_HEADERS, null, this.applyQueryParams);
	}

	_isOnCartPage() {
		return isOnCart();
	}
};

export default {
	name: "Bob's Stores",
	domain: 'bobstores.com',
	completionAction: completionActions.revisit,
	isSinglePageApp: true,
	stackable: true,
	Driver,
	isOnCartUrl() {
		return true;
	},
};
