import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ ‘isValid’ implemented: no
	+ Replace code: no
	+ Pages, where extension works: Billing
	+ Is SPA: no
	+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';

		const BASE_URL = 'https://www.nastygal.com/on/demandware.store/Sites-nastygal-US-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'couponID';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.APPLY_RESPONSE_KEY = null;

		this.APPLIED_CODE_SELECTOR = '[data-coupon]';
	}

	getExistingCodes() {
		const codes = [];
		const codeElement = document.querySelector(this.APPLIED_CODE_SELECTOR);
		if (codeElement) {
			codes.push(codeElement.getAttribute('data-coupon'));
		}

		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this.getOrderSummary().then(responseJSON => this.parseTotal(responseJSON));
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);

		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}
};

export default {
	name: 'Nasty Gal',
	domain: 'nastygal.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://www.nastygal.com/billing-continue');
	},
};
