import DemandwareAJAXDriver from './_base';
import { format } from '../../../utils';

const { toFloat } = format;

/*
Behavior description:
- existing code: yes
- merchant codes: yes
- remove code: yes
- stackable: no
- free gift support: no
- 'isValid' implemented: no
- manually apply code: no
- replace code: yes
- SPA: yes
- can run code testing only on Cart page
 */

const BASE_URL = 'https://www.glassesusa.com';
const BASE_REQUEST_URL = `${BASE_URL}/backend/optimaxcart/react`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		// Base class attributes
		this.TOTAL_SELECTOR = '#summary_grand_total';
		this.MERCHANT_CODES_SELECTOR = '#usa-cart-reminder';
		this.MERCHANT_CODES_REGEX = /code:\s(\w+)/i;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/applyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/removeCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/getQuote`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_RESPONSE_KEY = null;

		// Current class attributes
		this.CODE_SELECTOR = '[class^="cartSummaryDiscount__couponName"]';
		this.CODE_REGEXP = /\((.*?)\)/;
		this.CODE_INPUT_SELECTOR = '[name="couponField"]';
		this.EMPTY_CART_SELECTOR = '.empty-cart';
	}

	checkCondition() {
		const isCodeInputElement = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isCartEmptyElement = document.querySelector(this.EMPTY_CART_SELECTOR);
		return isCodeInputElement && !isCartEmptyElement;
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.CODE_SELECTOR)]
			.filter(Boolean)
			.map(el => el.innerText.trim().match(this.CODE_REGEXP)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = toFloat(response.totals.shippingPrice) || 0;
			return this.parseTotal(response) - shipping;
		});
	}

	parseTotal(objJSON) {
		let total = null;
		if (objJSON) {
			({ total } = objJSON.totals);
		} else {
			total = document.querySelector(this.TOTAL_SELECTOR).innerText;
		}
		return toFloat(total);
	}
};

export default {
	name: 'GlassesUSA',
	domain: 'glassesusa.com',
	Driver,
	isSinglePageApp: true,
};
