import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { browserUtils, completionActions, DOM, format, log } from '../../../utils';
import bus from '../../bus';

const { elemToFloat, toFloat } = format;
const { extractElement, setValue } = DOM;
const { getCookie, setCookie } = browserUtils;

/** Behavior description:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: no
•   Free gift support: no
•   isValid implemented: no
•   Show message about free shipping: no
•   Is SPA: yes
•   Gather merchant codes: no
•   Pages where extension work: Cart, checkout billing step 
====Special cases====
	- session timer: 15 min
	- Sometimes site have strange behavior and may send us wrong total
	- On checkout we applied code manually
	- we have some strange site behavior so we cover this 3 cases:
		In case user was on Cart - we do not show Genie popup on Checkout.
		In case user goes directly to Checkout, but has applied code manually, we provide fake code testing.
		In case user goes directly to Checkout, but the code wasn't applied manually - we can run code testing.
*/

const MAIN_URL = 'https://www.modcloth.com';
const CART_URL = `${MAIN_URL}/cart`;
const isOnCart = () => window.location.href.startsWith(CART_URL);
const isOnCheckout = () => window.location.href.includes('checkout');
const isCodeAppliedOnCheckout = () => document.querySelector('.activePromos');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isOnCartPage = window.location.href.includes('/cart');
		const BASE_URL = `${MAIN_URL}/on/demandware.store/Sites-modcloth-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/CheckoutCart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/CheckoutCart-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${MAIN_URL}/cart`;
		this.CART_PAGE_URL = CART_URL;
		this.CODE_INPUT_SELECTOR = '[name="couponCode"]';
		this.VALUE_SELECTOR = '.orderCost';
		this.ROW_SELECTOR = '#orderCostBreakdown tr';
		this.BILLING_STEP_SELECTOR = '#react-checkout-billing-step';
		this.APPLIED_CODES_SELECTOR = '#promoContainer .summaryHighlight';
		this.APPLY_REQUEST_HEADERS = {
			'content-type': 'application/json;charset=UTF-8',
		};
		this.APPLIED_CODE_INPUT_SELECTOR = '[name="couponCode"]';
		this.APPLIED_CODE_BUTTON_SELECTOR = '.actionLink';
		this.REMOVE_REQUEST_HEADERS = {
			'content-type': 'application/json;charset=UTF-8',
		};
		this.APPLY_RESPONSE_KEY = 'status';
		this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE = 'APPLIED';
		this.PRE_TAX_SHIPPING_REGEX = /adjustedMerchandizeCost":([\d.]+)/i;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		if (isOnCart() && !getCookie('wasOnCart')) {
			setCookie('wasOnCart', true, 20);
		}
		if (isOnCheckout() && isCodeAppliedOnCheckout() && !getCookie('skipCodeTesting')) {
			setCookie('skipCodeTesting', true, 20);
		}
		const codeInput = (document.querySelector(this.CODE_INPUT_SELECTOR) || {}).offsetParent;
		const isTotal = document.querySelector(this.VALUE_SELECTOR);
		const isUsa = getCookie('Globale_Simple') === 'USUSD';
		const isOnBilling = document.querySelector(this.BILLING_STEP_SELECTOR);

		return (
			(isOnCart() || isOnBilling) &&
			codeInput &&
			isTotal &&
			isUsa &&
			(isOnCart() ? true : !getCookie('wasOnCart'))
		);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(el => el.textContent.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		let lastResponse = null;
		return Promise.each(codes, code =>
			this.removeCode(code).then(response => {
				lastResponse = response;
			}),
		).then(() => ({
			originalPrice: this.parseTotal(lastResponse),
			originalItemsCount: this.parseItemsCount(lastResponse),
		}));
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
			};
			if (getCookie('skipCodeTesting') && isOnCheckout()) {
				return Promise.delay(2000).then(() => bus.$emit('code-checked', result));
			}
			return this.applyCode(code).then(applyCodeResponse => {
				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this.parseTotal(applyCodeResponse);
				result.finalDiscount = originalPrice - result.finalPrice;

				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					workingDiscounts.push(result);
				}

				return this.removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		});
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			return toFloat(response.match(this.PRE_TAX_SHIPPING_REGEX)[1]);
		});
	}

	applyCode(code) {
		return this._sendRequest(
			'POST',
			this.APPLY_CODE_URL,
			this.APPLY_REQUEST_HEADERS,
			null,
			JSON.stringify({ CouponCode: code }),
		);
	}

	applyCodeManually(code) {
		const codeInputElm = document.querySelector(this.APPLIED_CODE_INPUT_SELECTOR);
		const codeApplyBtn = document.querySelector(this.APPLIED_CODE_BUTTON_SELECTOR);

		setValue(codeInputElm, code);

		codeApplyBtn.click();
		return Promise.resolve();
	}

	removeCode(code) {
		if (getCookie('skipCodeTesting') && isOnCheckout()) {
			return Promise.resolve();
		}
		return this._sendRequest(
			'POST',
			this.REMOVE_CODE_URL,
			this.REMOVE_REQUEST_HEADERS,
			null,
			JSON.stringify({ couponID: code }),
		);
	}

	applyCodes(codes) {
		if (getCookie('skipCodeTesting') && isOnCheckout()) {
			return Promise.resolve();
		}
		return isOnCart() ? this.applyCode(codes[0]) : this.applyCodeManually(codes[0]);
	}

	_findValueByLabel(response, string) {
		return [...extractElement(this.ROW_SELECTOR, response, true)]
			.filter(elem => elem.innerText.includes(string))
			.map(elem => elem.querySelector(this.VALUE_SELECTOR))[0];
	}

	parseTotal(data) {
		if (data) {
			const shipping = data.shippingCost && !isOnCart() ? data.shippingCost : 0;
			const tax = data.tax && !isOnCart() ? data.tax : 0;
			return toFloat(data.adjustedMerchandizeCost) + toFloat(shipping) + toFloat(tax);
		}
		return elemToFloat(this._findValueByLabel(false, 'Total'));
	}
};

export default {
	name: 'ModCloth',
	domain: 'modcloth.com',
	completionAction: isOnCart() ? completionActions.default : completionActions.none,
	Driver,
	maxCoupons: 5,
	isSinglePageApp: true,
};
