/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["beforeApplyCodes"] }] */

import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(Payment, can't avoid redirect on the shipping step)
+ Is driver SPA: no
+ Gather codes on site pages: yes, from main page
Special case:
+ Can't use redirect because merchant redirect to the error page, so on FF browser's window may be shown
*/

const BASE_SITE_URL = 'https://www.katespade.com';

const onCart = () => window.location.href.match(/(bag|cart)/i);
const SURPRISE = window.location.href.includes('surprise');
const CART_FORM_SELECTOR = '#cart-items-form';
const CHECKOUT_FORM_SELECTOR = '#dwfrm_billing';

const Driver = class extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value, .order-total .price';

		this.NO_TAX_SELECTOR = '.order-sales-tax img';
		this.TAX_SELECTOR = '.order-sales-tax :nth-child(2)';
		this.SHIPPING_SELECTOR = '.order-shipping td:nth-child(2)';
		this.MERCHANT_CODE_SELECTOR = '#homepage-banner-slider strong';
		this.MERCHANT_CODE_IMAGE = '.katespade-sale-dropdown img';
		this.MERCHANT_CODE_REGEXP = /code\s(\w+)/i;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLIED_CODES_SELECTOR = `.rowcoupons ${onCart() ? '.cartcoupon' : '.message'} .value`;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.BASE_SURPRIZE_URL = 'https://surprise.katespade.com';

		const BASE_URL = SURPRISE
			? `${this.BASE_SURPRIZE_URL}/on/demandware.store/Sites-KateSale-Site/en_US/`
			: `${BASE_SITE_URL}/on/demandware.store/Sites-Shop-Site/en_US/`;
		this.APPLY_CODE_URL = `${BASE_URL}Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}Cart-DeleteCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}COBilling-UpdateSummary`;
	}

	getMerchantCodes() {
		const mainPageURL = SURPRISE ? this.BASE_SURPRIZE_URL : BASE_SITE_URL;
		return this._sendRequestGET(mainPageURL).then(responseHTML => {
			const codes = [...extractElement(this.MERCHANT_CODE_SELECTOR, responseHTML, true)]
				.map(elem => elem.innerText.trim())
				.filter(Boolean);
			[...extractElement(this.MERCHANT_CODE_IMAGE, responseHTML, true)]
				.map(elem => elem.alt.match(this.MERCHANT_CODE_REGEXP))
				.filter(Boolean)
				.map(elem => codes.push(elem[1]));
			return [...new Set(codes)];
		});
	}

	beforeApplyCodes() {
		if (document.querySelector(CART_FORM_SELECTOR)) {
			// Remove query params to avoid form resubmitting
			window.history.pushState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		}
		return Promise.resolve();
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const noTax = extractElement(this.NO_TAX_SELECTOR, response);
			const taxElem = extractElement(this.TAX_SELECTOR, response);
			const tax = noTax || !taxElem ? 0 : elemToFloat(taxElem);
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, response);
			const shipping =
				shippingEl && shippingEl.innerText.toLowerCase().trim() === 'free' ? 0 : elemToFloat(shippingEl);
			return this.parseTotal(response) - tax - shipping;
		});
	}
};

export default {
	name: 'Kate Spade',
	domain: 'katespade.com',
	Driver,
	isCartEmpty() {
		return (document.querySelector('.cart-empty-msg') || {}).offsetParent;
	},
	isOnCartUrl(href) {
		// For this merchant it is safer to check form existence
		return (
			!href.includes('eu.katespade.com') &&
			(document.querySelector(CART_FORM_SELECTOR) || document.querySelector(CHECKOUT_FORM_SELECTOR))
		);
	},
};
