import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { sessionTimer, handleDriverError } from '../../lib';

import { DOM, log, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat, toFloat } = format;

/* Driver behavior:
• Existing code: yes
• Remove code: yes
• Stackable: yes
• Free gift support: no
• isValid implemented: yes
• Show message about free shipping: no
• Driver is SPA: yes, on cart and checkout payment step
• Gather merchant codes: yes
• Can run code testing on Cart and Checkout */

const BASE_URL = 'https://www.sallybeauty.com';
const CART_URL = `${BASE_URL}/cart/`;
const IS_CART_EMPTY_REGEX = /cart is empty/i;
const IS_CART_EMPTY_SELECTOR = '.container.cart-page, .cart-empty';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.grand-total, .grand-total-sum';
		this.CODE_INPUT_SELECTOR = '.cart-promo-code';

		this.CART_PAGE_URL = CART_URL;

		this.PROMO_SELECTOR = '.carousel-item strong:nth-child(2)';
		this.APPLIED_CODES_SELECTOR = '.coupon-code__wrapper p';
		this.APPLIED_CODE_REGEX = /Applied Code (\w+)/;
		this.CSRF_TOKEN_SELECTOR = '.promo-code-form1 [name="csrf_token"]';

		const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-SA-Site/default`;
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/Cart-RemoveCouponLineItem`;

		this.CODE_ELEM_SELECTOR = '.coupon-price-adjustment';
		this.REMOVE_VALUE_SELECTOR = '.remove-coupon';
		this.REMOVE_VALUE_ATTRIBUTE = 'data-uuid';

		this.TAX_VALUE_SELECTOR = '.tax-total';
		this.SHIPPING_VALUE_SELECTOR = this._isOnCartPage() ? '.shipping-cost' : '.shipping-total-cost';
		this.SHIPPING_DISCOUNT_VALUE_SELECTOR = '.shipping-discount-total';

		sessionTimer.initialize();
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightURL = href.startsWith(CART_URL) || href.includes('Checkout-Begin');
		const isCartEmpty = [...document.querySelectorAll(IS_CART_EMPTY_SELECTOR)].some(item =>
			IS_CART_EMPTY_REGEX.test(item.textContent),
		);
		const isCodeInput = (document.querySelector(this.CODE_INPUT_SELECTOR) || {}).offsetParent;

		return !isCartEmpty && isOnRightURL && isCodeInput;
	}

	beforeTestCodes() {
		this.CSRF_TOKEN = document.querySelector(this.CSRF_TOKEN_SELECTOR).value;

		this.removeQueryParams = new URLSearchParams();
		this.applyQueryParams = new URLSearchParams({
			loyaltyreward: 'false',
		});

		this.lastResponse = null;
		return Promise.resolve();
	}

	getMerchantCodes() {
		return this._getUpdatedPage(CART_URL).then(response =>
			[...extractElement(this.PROMO_SELECTOR, response, true)].map(el => el.textContent).filter(Boolean),
		);
	}

	getExistingCodes() {
		const codes = this._getExistingCodes();
		return {
			codes,
			codeCount: codes.length,
		};
	}

	_getExistingCodes(html) {
		return [...extractElement(this.APPLIED_CODES_SELECTOR, html, true)]
			.filter(Boolean)
			.map(codeElement => codeElement.innerText.match(this.APPLIED_CODE_REGEX)[1]);
	}

	removeCodes(codes) {
		return Promise.each(codes, code =>
			this.removeCode(code, this._removeValue(code)).then(response => {
				this.lastResponse = response;
				return Promise.resolve();
			}),
		);
	}

	checkCodes(codes, originalPrice = this.parseTotal(this.lastResponse)) {
		log('originalPrice: %s', originalPrice);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			return this.applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				if (responseJSON.error === true) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				this.lastResponse = responseJSON;
				result.finalPrice = this.parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;
				if (result.finalPrice < originalPrice) {
					workingDiscounts.push(result);
				}
				return this.removeCode(code, this._removeValue(code)).then(removeCoderesponse => {
					this.lastResponse = removeCoderesponse;
					return bus.$emit('code-checked', result);
				});
			});
		}).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;
		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount => {
			return this.applyCode(discount.code).then(responseJSON => {
				this.lastResponse = responseJSON;
				const finalPrice = this.parseTotal(responseJSON);
				const finalDiscount = originalPrice - finalPrice;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
				});

				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}

				return this.removeCode(discount.code, this._removeValue(discount.code)).then(removeCodeResponse => {
					this.lastResponse = removeCodeResponse;
					const currentCodes = this._getUpdatedPage(window.location.href).then(response =>
						this._getExistingCodes(response),
					);

					const isCodesEqual =
						currentCodes.length === successfulCodes.length &&
						currentCodes.every(code => successfulCodes.includes(code));

					if (isCodesEqual) {
						return Promise.resolve();
					}

					return Promise.each(currentCodes, code => this.removeCode(code, this._removeValue(code))).then(
						removeResponse => {
							this.lastResponse = removeResponse;
							return Promise.each(successfulCodes, code => this.applyCode(code));
						},
					);
				});
			});
		}).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	getPreTaxShippingTotal() {
		// Tax and shipping are included on cart and checkout pages
		return this._getUpdatedPage(window.location.href).then(responseHTML => {
			const taxValue = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML));
			const shippingElem = extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML);
			const shippingValue = shippingElem ? elemToFloat(shippingElem) : 0;
			const shippingDiscountElem = extractElement(this.SHIPPING_DISCOUNT_VALUE_SELECTOR, responseHTML);
			const shippingDiscount = shippingDiscountElem ? elemToFloat(shippingDiscountElem) : 0;
			const totalValue = elemToFloat(extractElement(this.TOTAL_SELECTOR, responseHTML));
			return totalValue - taxValue - shippingValue + shippingDiscount;
		});
	}

	applyCode(code) {
		this.applyQueryParams.set('couponCode', code);
		this.applyQueryParams.set('csrf_token', this.CSRF_TOKEN);
		return this.manageCode(this.APPLY_CODE_URL, this.applyQueryParams);
	}

	removeCode(code, removeValueName) {
		this.removeQueryParams.set('code', code);
		this.removeQueryParams.set('uuid', removeValueName);
		return this.manageCode(this.REMOVE_CODE_URL, this.removeQueryParams);
	}

	manageCode(url, queryParams) {
		return fetch(`${url}?${queryParams.toString()}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_removeValue(code) {
		let removeValueUuid = null;

		if (this.lastResponse) {
			if (!this.lastResponse.totals) {
				handleDriverError(new Error('Response is incorrect.', { code, response: this.lastResponse }));
				return removeValueUuid;
			}
			removeValueUuid = extractElement(
				this.CODE_ELEM_SELECTOR,
				this.lastResponse.totals.discountsHtml,
			).getAttribute(this.REMOVE_VALUE_ATTRIBUTE);
		} else {
			const codesElements = [...document.querySelectorAll(this.CODE_ELEM_SELECTOR)];
			const matchingCodesElement = codesElements.find(element => {
				const codesElement = element.querySelector(this.APPLIED_CODES_SELECTOR);
				return codesElement && codesElement.innerText.match(this.APPLIED_CODE_REGEX)[1] === code;
			});
			if (matchingCodesElement) {
				removeValueUuid = matchingCodesElement
					.querySelector(this.REMOVE_VALUE_SELECTOR)
					.getAttribute(this.REMOVE_VALUE_ATTRIBUTE);
			}
		}
		return removeValueUuid;
	}

	_getUpdatedPage(url) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	parseTotal(json) {
		return json && json.totals
			? toFloat(json.totals.grandTotal)
			: elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Sally Beauty',
	domain: 'sallybeauty.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
