import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';
import { extractElement } from '../../../utils/DOM';

const { extractData } = DOM;
const { elemToFloat } = format;

/**
Driver behavior:
•   Existing code: yes, not always
•   Remove code: yes
•   Code retesting: yes
•   Stackable: yes
•   Free gift support: n
•   isValid implemented: yes
•   Show message about free shipping: n
•   Driver is SPA: yes, on cart and checkout billing step
•   Gather merchant codes: n
 */

const BASE_URL = 'https://www.hannaandersson.com';
const BASE_API_URL = `${BASE_URL}/on/demandware.store/Sites-hannaandersson-Site/default`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.APPLY_CODE_URL = `${BASE_API_URL}/COBilling-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_API_URL}/COBilling-UpdateSummary`;
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.CART_PAGE_URL = `${BASE_URL}/cart`;
		this.TAX_VALUE_SELECTOR = '.order-sales-tax .scp-order-total';
		this.SHIPPING_VALUE_SELECTOR = '.order-shipping .scp-order-total';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount .scp-shipping-discount';
		this.applyQueryParams.set('format', 'ajax');

		this.correctHrefs = [
			'/cart',
			'/Cart-SubmitForm',
			'/Cart-AddToWishlist',
			'/billing',
			'/COShipping-SingleShipping',
		];
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCart = document.querySelector('.cart-empty, .mini-cart-empty, .is-error-callout');
		const correctPage =
			href.includes('www.hannaandersson.com') &&
			this.correctHrefs.some(hrefPart => href.toLowerCase().includes(hrefPart.toLowerCase()));
		const codeInput = (document.querySelector(this.CODE_INPUT_SELECTOR) || {}).offsetParent;
		const totalRendered = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		return !emptyCart && correctPage && totalRendered && codeInput;
	}

	getExistingCodes() {
		return this._getCart().then(responseHTML => {
			const codes = extractData(this.APPLIED_CODES_SELECTOR, responseHTML, true)
				.filter(Boolean)
				.map(code => code.trim());
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getOrderSummary() {
		return this._sendRequestPOST(this.UPDATE_REQUEST_URL, this.UPDATE_REQUEST_HEADERS, this.updateQueryParams);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, responseHTML)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML)) || 0;
			return total - shipping - tax + shippingDiscount;
		});
	}

	_getCart() {
		return fetch(this.CART_PAGE_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Hanna Andersson',
	domain: 'hannaandersson.com',
	stackable: true,
	Driver,
	isSinglePageApp: true,
};
