import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { completionActions, format, DOM, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement, setValue } = DOM;

const CART_PAGE_URL = 'https://www.teva.com/cart';
const isOnCart = () => window.location.href.includes(CART_PAGE_URL) && document.querySelector('#cart-table');

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can't be replaced with another valid or invalid code;
- can apply multiple codes (stackable);
- code testing may be run on 'Cart' and 'Checkout' pages;
======== Special case ========
to remove the codes we need an additional request that receives the codes 'uuid' and in request of remove we will use this uuid
on checkout page we need to apply and to remove by click
I think that the site can give us gift items, but we can not test it now, because there is no such code
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR =
			'.cart__checkout__wrapper .order-total td:last-child, .table-totals tr:last-child td:last-child';
		this.APPLIED_CODES_SELECTOR = '.coupon-code .value';
		this.CODE_INPUT_SELECTOR = '#spc-coupon-code';
		this.CODE_REMOVE_BTN_SELECTOR = '.coupon__alert button';
		this.CODE_APPLY_BTN_SELECTOR = '[value="Apply Coupon"]';
		this.SUCCESS_MESSAGE_SELECTOR = '.coupon__status';
		this.ACTIVE_SELECTOR = '#cart-table, #spc-panel-billing.is-active';

		const BASE_URL = 'https://www.teva.com/on/demandware.store/Sites-TEVA-US-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/SPC-UpdateCartSummary`;
		this.CODE_INFO_URL = `${BASE_URL}/SPC-GetCouponsInBasket?spc=true&instart_disable_injection=true`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.applyQueryParams = new URLSearchParams({
			format: 'ajax',
			spc: true,
			instart_disable_injection: true,
		});

		this.removeQueryParams = this.applyQueryParams;
		this.itemNumber = null;
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const isOnHref = window.location.href.startsWith(CART_PAGE_URL);
			const isSelector = Boolean(document.querySelector(this.ACTIVE_SELECTOR));
			const currentState = isOnHref && isSelector;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	removeCodes() {
		return Promise.resolve()
			.then(() => (this._isOnCartPage() ? this._getCodesInfo() : this._removeCodesManually()))
			.then(responseARRAY =>
				this._isOnCartPage() ? Promise.each(responseARRAY, code => this.removeCode(code)) : responseARRAY,
			)
			.then(() => this.getOrderSummary())
			.then(responseJSON => ({
				originalPrice: this.parseTotal(responseJSON),
				originalItemsCount: this.parseItemsCount(responseJSON),
			}));
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};
				result.isValid = !applyCodeResponse.couponStatus;

				if (!result.isValid) {
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;
					result.isValid = true;

					if (
						result.finalPrice < originalPrice ||
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}
					return this._getCodesInfo()
						.then(responseARRAY => this.removeCode(responseARRAY[0]))
						.then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice, originalItemsCount } = validationInfo;

		discounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this.applyCode(discount.code)
				.then(() => this.getOrderSummary())
				.then(responseJSON => {
					const finalPrice = this.parseTotal(responseJSON);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(responseJSON) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});

					if (finalPrice < currentPrice || (finalPrice === currentPrice && freeGifts > currentFreeGifts)) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this._getCodesInfo()
						.then(responseARRAY => this.removeCode(responseARRAY[0]))
						.then(() => {
							if (this.REAPPLY_CODES_WHILE_STACKING) {
								return Promise.each(successfulCodes, code => this.applyCode(code));
							}
							return Promise.resolve();
						});
				}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	applyCodes(codes) {
		return !this._isOnCartPage() ? this._applyCodesManually(codes) : Promise.resolve();
	}

	_applyCodesManually(codes) {
		return this.removeCode(codes[0]).then(() => {
			setValue(document.querySelector(this.CODE_INPUT_SELECTOR), codes[0]);
			document.querySelector(this.CODE_APPLY_BTN_SELECTOR).click();
			const message = document.querySelector(this.SUCCESS_MESSAGE_SELECTOR);
			if (message) {
				message.remove();
			}
		});
	}

	_removeCodesManually() {
		[...document.querySelectorAll(this.CODE_REMOVE_BTN_SELECTOR)].forEach(buttonElem => buttonElem.click());
		return Promise.resolve();
	}

	parseTotal(data) {
		if (data) {
			return toFloat(data.total);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}

	_isOnCartPage() {
		return isOnCart();
	}

	_getCodesInfo() {
		return this._sendRequestGET(this.CODE_INFO_URL).then(responseJSON => responseJSON.map(element => element.uuid));
	}
};

export default {
	name: 'Teva',
	domain: 'teva.com',
	completionAction: () => (isOnCart() ? completionActions.revisit : completionActions.none),
	stackable: true,
	isSinglePageApp: true,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
