import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: Cart, Billing
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from Coupon page: https://www.aeropostale.com/coupons
+ Restore user data: yes
	- shipping method on Cart page
*/

const DOMAIN = 'https://www.aeropostale.com';
const BASE_REQUEST_URL = `${DOMAIN}/on/demandware.store/Sites-aeropostale-Site`;
const BASE_CART_REQUEST_URL = `${BASE_REQUEST_URL}/default`;
const BASE_CHECKOUT_REQUEST_URL = `${BASE_REQUEST_URL}/en_US`;
const isCart = window.location.href.includes('cart') || window.location.href.includes('Cart-SubmitForm');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';

		this.APPLY_CODE_URL = `${BASE_CHECKOUT_REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_CHECKOUT_REQUEST_URL}/COBilling-UpdateSummary`;
		this.CODE_VALUE_SELECTOR = '.cartcoupon.clearfix .value';
		this.CODE_INPUT_SELECTOR = '[name*="couponCode"]';

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.CART_PAGE_URL = `${DOMAIN}/cart/`;

		this.applyQueryParams.set('format', 'ajax');

		this.isStackable = true;
		this.REAPPLY_CODES_WHILE_STACKING = true;
		this.CODE_CONTAINER_SELECTOR = '.mobileSpan';
		this.CODE_REGEX = /code\s+(.*?)\s+/i;
		this.LOADER_SELECTOR = '.loader';
		this.CART_EMPTY_SELECTOR = '.cart-empty, .mini-cart-header';
		this.SHIPPING_PICKUP = '.item-delivery-options input';
		this.SHIPPING_OPTIONS_SELECTOR = '[name="shippingMethod"]';
		this.SELECTED_SHIPPING = null;
		this.SHIPPING_WRAPPER_SELECTOR = '.cart-shipping-methods';
		this.APPLIED_CODES_SELECTOR = null;
	}

	checkCondition() {
		const cartEmptyMsg = document.querySelector(this.CART_EMPTY_SELECTOR);
		const { href } = window.location;
		const isRightUrl = href.includes('/cart') || href.includes('/Cart-SubmitForm');
		const isCartEmpty = cartEmptyMsg && cartEmptyMsg.textContent.toLowerCase().includes('empty');
		const isLoader = (document.querySelector(this.LOADER_SELECTOR) || {}).offsetParent;
		const codeInputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		return !isCartEmpty && isRightUrl && !isLoader && codeInputEl;
	}

	beforeTestCodes() {
		if (isCart) {
			this.SELECTED_SHIPPING = document.querySelector(`${this.SHIPPING_OPTIONS_SELECTOR}:checked`)
				? document.querySelector(`${this.SHIPPING_OPTIONS_SELECTOR}:checked`).getAttribute('value')
				: '';
		}
		return Promise.resolve();
	}

	getMerchantCodes() {
		return this._getPromoPage().then(responseHTML => {
			return [...extractElement(this.CODE_CONTAINER_SELECTOR, responseHTML, true)]
				.filter(el => el.textContent.match(/use code/i))
				.map(el => el.textContent.match(this.CODE_REGEX))
				.filter(Boolean)
				.map(el => el[1]);
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}

	checkRestoreDataCondition() {
		return this._isShippingOptionsElement() && document.readyState === 'complete';
	}

	getUserData() {
		if (!this._isShippingOptionsElement()) {
			return null;
		}
		const shippingOrPickUpElement = [...document.querySelectorAll(this.SHIPPING_PICKUP)].find(
			element => element.checked,
		);
		const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_OPTIONS_SELECTOR)].find(
			element => element.checked,
		);
		return {
			shippingOrPickUpSelector: shippingOrPickUpElement
				? `#${shippingOrPickUpElement.getAttribute('id')}`
				: false,
			shippingSelector: `#${checkedShippingElement.getAttribute('id')}`,
		};
	}

	setUserData(userData) {
		if (userData.shippingOrPickUpSelector) {
			document.querySelector(userData.shippingOrPickUpSelector).click();
		}
		return Promise.delay(1000).then(() => document.querySelector(userData.shippingSelector).click());
	}

	_isShippingOptionsElement() {
		return (
			document.querySelector(this.SHIPPING_WRAPPER_SELECTOR) &&
			document.querySelector(`${this.SHIPPING_OPTIONS_SELECTOR}:checked`)
		);
	}

	_getPromoPage() {
		return fetch(`${DOMAIN}/coupons`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	getOrderSummary() {
		// on the cart for right discount we need to send `set shipping and get total` requests
		const promise = isCart
			? this._setShipping()
			: this._sendRequestGET(this.UPDATE_REQUEST_URL, this.UPDATE_REQUEST_HEADERS, this.updateQueryParams);
		return promise;
	}

	_setShipping() {
		return this._sendRequestGET(
			`${BASE_CART_REQUEST_URL}/COShipping-SelectShippingMethod?shippingMethodID=${this.SELECTED_SHIPPING}`,
		).then(() => this._getTotals());
	}

	_getTotals() {
		return this._sendRequestGET(`${BASE_CART_REQUEST_URL}/Cart-GetCartTotalsJson`);
	}
};

export default {
	name: 'Aeropostale',
	domain: 'aeropostale.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
	restoreUserData: true,
	cartContinueUrl: `${DOMAIN}/cart`,
};
