import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';
import bus from '../../bus';

const DEFAULT_URL = 'https://www.taylormadegolf.com';
const CART_PAGE_URL = `${DEFAULT_URL}/cart/`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .value, .order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.isStackable = true;

		const BASE_URL = `${DEFAULT_URL}/on/demandware.store/Sites-TMaG-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.CART_INPUT_FIELD_SELECTOR = '#dwfrm_cart_couponCode';
		this.CHECKOUT_INPUT_FIELD_SELECTOR = '#dwfrm_billing_couponCode';
		this.EMPTY_CART_SELECTOR = '.mini-cart-product, .minicart-quantity';
		this.MODAL_SELECTOR = '#checkout-login-modal';
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const checkoutURL =
				(window.location.href.startsWith(`${DEFAULT_URL}/checkout`) ||
					window.location.href.startsWith(`${DEFAULT_URL}/shipping`) ||
					window.location.href.includes('COBilling-Start')) &&
				document.querySelector(this.CHECKOUT_INPUT_FIELD_SELECTOR);
			const emptyCart = !document.querySelector(this.EMPTY_CART_SELECTOR);
			const modalPopup = document.querySelector(this.MODAL_SELECTOR)
				? document.querySelector(this.MODAL_SELECTOR).classList.contains('tinymodal-window-open')
				: false;

			const currentState = (this._isOnCartPage() || checkoutURL) && !emptyCart && !modalPopup;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	getExistingCodes() {
		const codes = [];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return (
			window.location.href.startsWith(this.CART_PAGE_URL) &&
			document.querySelector(this.CART_INPUT_FIELD_SELECTOR)
		);
	}
};

export default {
	name: 'TaylorMade Golf',
	domain: 'taylormadegolf.com',
	stackable: true,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
