import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Checkout (no input on Cart)
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const CART_PAGE_URL = 'https://www.lifeisgood.com/cart';
const CHECKOUT_PAGE_URL = 'https://www.lifeisgood.com/checkout';
const TOTAL_SELECTOR = '.order-summary-footer .order-total td:last-child, .order-total td:last-child';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = TOTAL_SELECTOR;
		this.CODE_VALUE_SELECTOR = '.cart-coupon .value';
		this.isStackable = true;

		this.PRICE_SELECTOR = '.price-sales';
		this.ITEM_SELECTOR = '.mini-cart-product';
		this.ITEM_NAME_SELECTOR = '.mini-cart-name';
		this.DONATION_REGEX = /donation/i;

		const BASE_URL = 'https://www.lifeisgood.com/on/demandware.store/Sites-LIG2015-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(el => el.innerText.trim());
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const donationEl = this._findDonationEl(response);
			const donation = donationEl ? elemToFloat(donationEl) : 0;
			return this.parseTotal(response) - shipping - tax - donation + shippingDiscount;
		});
	}

	_findDonationEl(response) {
		const targetEl = [...extractElement(this.ITEM_SELECTOR, response, true)].filter(el => {
			const skuEl = el.querySelector(this.ITEM_NAME_SELECTOR);
			return skuEl && skuEl.textContent.match(this.DONATION_REGEX);
		})[0];
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}
};

export default {
	name: 'Life Is Good',
	domain: 'lifeisgood.com',
	stackable: true,
	Driver,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		return (
			((href.startsWith(CART_PAGE_URL) && document.querySelector('.promo-code .payment-dialog')) ||
				href.startsWith(CHECKOUT_PAGE_URL) ||
				href.includes('COSummary-Start') ||
				href.includes('Cart-DonateJson') ||
				href.includes('Cart-RemoveProductAjax')) &&
			document.querySelector(TOTAL_SELECTOR) &&
			elemToFloat(document.querySelector(TOTAL_SELECTOR))
		);
	},
};
