import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { format, log } from '../../../utils';

const { toFloat } = format;

/*
Driver behavior description:
• Existing code: yes;
• Remove code: yes;
• Stackable: yes;
• Free gift support: no;
• ‘isValid’ implemented: yes;
• Manually apply code: no;
Optional:
• Show message about free shipping on the cart page (without discount info): no;
• Is driver SPA and add the condition to show popup: no;
• Gather codes on site pages: no;
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.BASE_URL = 'https://www.trinaturk.com/on/demandware.store/Sites-trinaturk-Site/default';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/Cart-RemoveCouponLineItem`;

		this.isStackable = true;

		this.TOTAL_SELECTOR = document.querySelector('#checkout-main') ? '.grand-total-sum' : '.grand-total';

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'code';

		this.CODES_ELEMENTS_SELECTOR = '.coupon-price-adjustment';
		this.CODES_ELEMENT_SELECTOR = '.coupon-code';
		this.REMOVE_CODE_SELECTOR = '.remove-coupon';
		this.REMOVE_VALUE_ATTRIBUTE = 'data-uuid';

		this.CSRF_TOKEN_SELECTOR = '[name="csrf_token"]';

		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.lastResponse = null;

		this.APPLIED_CODES_REGEX = /(\w+) -/;
	}

	beforeTestCodes() {
		const tokenElem = document.querySelector(this.CSRF_TOKEN_SELECTOR);
		this.applyQueryParams.set('csrf_token', tokenElem ? tokenElem.value : false);
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODES_ELEMENTS_SELECTOR)]
			.filter(Boolean)
			.map(el => el.innerText.match(this.APPLIED_CODES_REGEX)[1]);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code =>
			this.removeCode(code).then(response => {
				this.lastResponse = response;
			}),
		);
	}

	checkCodes(codes, originalPrice = this.parseTotal(this.lastResponse)) {
		log('originalPrice: %s', originalPrice);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(responseJson => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
				};
				if (responseJson.error) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				this.lastResponse = responseJson;
				result.finalPrice = this.parseTotal(responseJson);
				result.finalDiscount = originalPrice - result.finalPrice;
				if (result.finalPrice < originalPrice) {
					workingDiscounts.push(result);
				}
				return this.removeCode(code).then(removeCodeJson => {
					this.lastResponse = removeCodeJson;
					bus.$emit('code-checked', result);
				});
			}),
		).then(() => {
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
			});
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;

		// Sort by discount
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount =>
			this.applyCode(discount.code).then(responseJson => {
				this.lastResponse = responseJson;
				const finalPrice = this.parseTotal(responseJson);
				const finalDiscount = originalPrice - finalPrice;

				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
				});

				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}
				return this.removeCode(discount.code).then(removeCodeJson => {
					this.lastResponse = removeCodeJson;
					return Promise.each(successfulCodes, code => this.applyCode(code));
				});
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	removeCode(code) {
		this.removeQueryParams.set('uuid', this._getRemoveUuidValue(code));
		return super.removeCode(code);
	}

	_getRemoveUuidValue(code) {
		let removeValueUuid = null;
		if (this.lastResponse) {
			const codesObjects = this.lastResponse.totals.discounts;
			for (let i = 0; i < codesObjects.length; i++) {
				const codesObject = codesObjects[i];
				if (codesObject && codesObject.couponCode === code) {
					removeValueUuid = codesObjects[i].UUID;
					break;
				}
			}
		} else {
			const codesElements = Array.from(document.querySelectorAll(this.CODES_ELEMENTS_SELECTOR));
			const matchingCodesElement = codesElements.find(element => {
				const codesElement = element.querySelector(this.CODES_ELEMENT_SELECTOR);
				return codesElement && codesElement.innerText.match(/(\w+) -/)[1] === code;
			});
			if (matchingCodesElement) {
				removeValueUuid = matchingCodesElement
					.querySelector(this.REMOVE_CODE_SELECTOR)
					.getAttribute(this.REMOVE_VALUE_ATTRIBUTE);
			}
		}
		return removeValueUuid;
	}

	parseTotal(json) {
		if (json) {
			return toFloat(json.totals.grandTotal);
		}
		return toFloat(document.querySelector(this.TOTAL_SELECTOR).textContent);
	}
};

export default {
	name: 'Trina Turk',
	domain: 'trinaturk.com',
	Driver,
	stackable: true,
	isCartEmpty() {
		const cartElem = document.querySelector('.page-title');
		return cartElem ? cartElem.innerText.includes('BAG IS EMPTY') : false;
	},
	isOnCartUrl() {
		return !!document.querySelector('#couponCode');
	},
};
