/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_getHeaders", "getMerchantCodes"] }] */
import Promise from 'bluebird';
import bus from '../../bus';

import DemandwareAJAXDriver from './_base';
import BaseDriver from '../base';
import { sessionTimer } from '../../lib';
import { DOM, log, format, browserUtils, completionActions } from '../../../utils';

const { extractFormValues, extractElement, extractData } = DOM;

const { elemToFloat, getRandomInt } = format;
const { getCookie } = browserUtils;

/* Driver behavior description:
+   Existing code: yes
+   Remove code: yes
+   Stackable: no
+   Free gift support: no
+   ‘isValid’ implemented: yes
+	Is driver SPA: yes
+   Pages where extension works: Cart, Checkout(Payment Details step) - https://www.fullbeauty.com;
	Cart, Checkout - only Log in step, on other steps we lose a lot of information - https://www2.fullbeauty.com
+ 	Gather codes on site pages: yes(cart page)
+ 	SessionTimer 15 min
Optional:
+	We may get merchant codes only on cart page
+    credentials:  (adler7891@gmail.com 19942111Ts)
*/

const checkingVersion = window.location.href.includes('/www2');

const BASE_URL = 'https://www.fullbeauty.com';
const CART_URL = `${BASE_URL}/cart`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-fboutlet-Site/default`;
const MERCHANT_CODE_SELECTOR = '#monetate_selectorBanner_75f998f4_00 > img';
const MERCHANT_CODE_REGEX = /use code (\w+)/i;

const isOnCart = () =>
	window.location.href.includes(CART_URL) ||
	window.location.href.includes(`${BASE_REQUEST_URL}/Cart-SubmitForm`) ||
	window.location.href.includes(`${BASE_REQUEST_URL}/Cart-AddToWishlist`);

const _getMerchantCodes = () => {
	return extractElement(MERCHANT_CODE_SELECTOR)
		? [extractElement(MERCHANT_CODE_SELECTOR)]
				.map(elem => elem.alt.match(MERCHANT_CODE_REGEX))
				.filter(Boolean)
				.map(code => code[1])
		: [];
};
const DriverDemandwareAjax = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = false;
		this.TOTAL_SELECTOR = '.order-total .order-value';

		this.INPUT_CODE_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.APPLY_BTN_SELECTOR = '#add-coupon';

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.ERROR_MSG_SELECTOR = '.error.coupon-code-error';
		this.REMOVE_ATTRIBUTE_SELECTOR = '#remove-coupon';

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
		this.UPDATE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;
	}

	checkCondition() {
		const isRightUrl = window.location.href.includes('cart');
		const inputField = document.querySelector(this.INPUT_CODE_SELECTOR);
		return isRightUrl && inputField;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	getMerchantCodes() {
		return _getMerchantCodes();
	}

	beforeTestCodes() {
		this.applyQueryParams = new URLSearchParams();
		this.removeQueryParams = new URLSearchParams();
		this.updateQueryParams = new URLSearchParams();

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		const codeValue = document.querySelector(this.CODE_VALUE_SELECTOR);
		if (isOnCart() && codeValue) {
			return this._getUpdatedCheckout().then(response => {
				this.UUID = this._getRemoveParam(response);
			});
		}
		if (codeValue) {
			this.UUID = this._getRemoveParam();
		}
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};

				if (this._codeAppliedCheck(applyCodeResponse)) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this._getUpdatedCheckout().then(response => {
						this.UUID = this._getRemoveParam(response);
						return this.removeCode(code).then(() => bus.$emit('code-checked', result));
					});
				});
			}),
		);
	}

	applyCodes(codes) {
		return this.applyCode(codes[0]);
	}

	removeCode(code) {
		this.removeQueryParams.set('uuid', this.UUID);
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	_getRemoveParam(data) {
		return extractData(this.REMOVE_ATTRIBUTE_SELECTOR, data, false, 'data-uuid');
	}

	_getUpdatedCheckout() {
		return fetch(`${BASE_URL}/billing?${this.updateQueryParams.toString()}`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.text());
	}

	_codeAppliedCheck(data) {
		const errorMsg = extractElement(this.ERROR_MSG_SELECTOR, data);
		return errorMsg && errorMsg.textContent ? errorMsg.textContent.match(/invalid or expired/i) : false;
	}
};

const DOMAIN = 'https://www2.fullbeauty.com';
const SHOPING_BAG_URL = `${DOMAIN}/Shopping_bag/ShoppingBag.aspx`;
const CHECKOUT_URL = `${DOMAIN}/checkout/processcheckout.ashx`;
const IS_ON_CART = window.location.href.toLowerCase().includes('shopping_bag');

const DriverBase = class Driver extends BaseDriver {
	constructor() {
		super();
		this.PREFIX_NAME = '#ctl00_GlobalBodyContentPlaceHolder';
		this.CART_PREFIX = 'ctl00$GlobalBodyContentPlaceHolder';
		this.TOTAL_SELECTOR = `${
			this.PREFIX_NAME
		}_ucTotals_lblNonFinalSubTotal, #ctl00_BodyContent_OrderTotal_OrderTotal .AddTotal`;
		this.APPLIED_CODE_SELECTOR = `${
			this.PREFIX_NAME
		}_ucDisplayPromotionCode_promoCodeRpt_ctl00_lblPromo, .promo-code-applied .code-applied`;
		this.CODE_INPUT_SELECTOR = `#promo-code, ${this.PREFIX_NAME}_ucPromotionCode_txtCode`;
		this.formData = null;
		this.FORM_SELECTOR = '#aspnetForm';
		this.LOGIN_STEP_SELECTOR = '#login.collapsible-panel.active';
		this.ERROR_SELECTOR = '.couponFailedMsg';
	}

	checkCondition() {
		const hrefLowerCase = window.location.href.toLowerCase();
		const isCodeInputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnCartUrl = hrefLowerCase.includes('shopping_bag') && !hrefLowerCase.includes('shoppersclub');
		const isOnLoginStep = hrefLowerCase.includes('checkout') && document.querySelector(this.LOGIN_STEP_SELECTOR);
		const currenState = (isOnCartUrl || isOnLoginStep) && isCodeInputEl;
		return currenState;
	}

	getMerchantCodes() {
		return _getMerchantCodes();
	}

	beforeTestCodes() {
		this.formData = IS_ON_CART ? extractFormValues(this.FORM_SELECTOR).params : new URLSearchParams();
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [];
		const codeElement = document.querySelector(this.APPLIED_CODE_SELECTOR);
		if (codeElement) {
			codes.push(
				codeElement.textContent
					.replace(' - Promo Code: ', '')
					.split(' ')[0]
					.replace(',', ''),
			);
		}
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(responseHTML => (IS_ON_CART ? responseHTML : this._getUpdatedCart()))
			.then(response => this._parseTotal(response));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseHTML => (IS_ON_CART ? responseHTML : this._getUpdatedCart()))
				.then(responseHTML => {
					const finalPrice = this._parseTotal(responseHTML);
					const finalDiscount = originalPrice - finalPrice;
					const isValid = this._isValidCode(responseHTML);
					return this._removeCode(code).then(() =>
						bus.$emit('code-checked', {
							code,
							finalPrice,
							finalDiscount,
							isValid,
						}),
					);
				}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_applyCode(code) {
		let queryUrl = null;
		if (IS_ON_CART) {
			this.formData.set('__EVENTTARGET', '');
			this.formData.set(`${this.CART_PREFIX}$ucPromotionCode$ibtnApplyCode.x`, getRandomInt(15, 31));
			this.formData.set(`${this.CART_PREFIX}$ucPromotionCode$ibtnApplyCode.y`, getRandomInt(0, 15));
			this.formData.set(`${this.CART_PREFIX}$ucPromotionCode$txtCode`, code);
			queryUrl = SHOPING_BAG_URL;
		} else {
			queryUrl = `${CHECKOUT_URL}?Action=ApplyPromotion`;
			this.formData.set('InputData', JSON.stringify({ PromotionCode: code }));
		}
		return this._manageCode(queryUrl);
	}

	_removeCode(code) {
		let queryUrl = null;
		if (IS_ON_CART) {
			this.formData.set(
				'__EVENTTARGET',
				`${this.CART_PREFIX}$ucDisplayPromotionCode$promoCodeRpt$ctl00$lbtnRemovePromo`,
			);
			this.formData.set(`${this.CART_PREFIX}$ucPromotionCode$txtCode`, '');
			this.formData.delete(`${this.CART_PREFIX}$ucPromotionCode$ibtnApplyCode.x`);
			this.formData.delete(`${this.CART_PREFIX}$ucPromotionCode$ibtnApplyCode.y`);
			queryUrl = SHOPING_BAG_URL;
		} else {
			queryUrl = `${CHECKOUT_URL}?Action=RemovePromotion`;
			this.formData.set('InputData', JSON.stringify({ PromotionCode: code }));
		}
		return this._manageCode(queryUrl);
	}

	_manageCode(url) {
		return fetch(url, {
			method: 'POST',
			credentials: 'include',
			headers: this._getHeaders(),
			body: this.formData,
		}).then(response => response.text());
	}

	_isValidCode(response) {
		if (IS_ON_CART) {
			return !extractElement(this.ERROR_SELECTOR, response);
		}
		return Boolean(response.ApplyPromotions.SuccessText[0] && response.ApplyPromotions.SuccessText[0].Code);
	}

	_getUpdatedCart() {
		return fetch(`${CHECKOUT_URL}?Action=OrderTotal`, {
			method: 'POST',
			credentials: 'include',
			headers: this._getHeaders(),
		}).then(responseJSON => responseJSON.json());
	}

	_getHeaders() {
		if (IS_ON_CART) {
			return {
				Accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
				'Content-Type': 'application/x-www-form-urlencoded',
			};
		}
		return {
			'X-Request-Verification-Token': getCookie('__RequestVerification'),
			'X-Requested-With': 'XMLHttpRequest',
		};
	}

	_parseTotal(data) {
		let totalElement = null;
		if (IS_ON_CART) {
			totalElement = elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
		} else {
			totalElement = data
				? data.OrderTotal.FinalSubTotal
				: elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
		}
		return totalElement;
	}
};

export default {
	name: 'fullbeauty',
	domain: 'fullbeauty.com',
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
	cartContinueUrl: isOnCart() ? CART_URL : window.location.href.split('#')[0],
	get Driver() {
		return checkingVersion ? DriverBase : DriverDemandwareAjax;
	},
};
