import DemandwareAJAXDriver from './_base';
import { browserUtils, completionActions, DOM, format } from '../../../utils';

const { setCookie, getCookie } = browserUtils;
const { extractElement, setValue } = DOM;
const { elemToFloat } = format;

const DOMAIN = 'https://www.hugoboss.com';
const CART_URL = `${DOMAIN}/us/cart`;
const BASE_URL = `${DOMAIN}/on/demandware.store/Sites-US-Site/en_US`;
const IS_ON_CART = window.location.href.startsWith(CART_URL);

const _getPage = url => {
	return fetch(url).then(response => response.text());
};
const isOnPaymentStep = () => window.location.href.includes('CheckoutOnePage-UpdateSection');

/**
 * Driver behavior description:
 * Existing code: yes
 * Remove code: yes
 * Stackable: no
 * Free gift support: yes
 * Free shipping: no
 * ‘isValid’ implemented: yes
 * Replaced code: no
 * Manually remove code: on Payment step
 * Pages, where extension works:
 * 	- Cart sidebar
 *  - Cart page
 *  - Checkout (Billing step)
 * Is SPA: yes
 * Gather codes on site pages: no
 */

const CartDriver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-totals .order-total td:last-child';
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.SHIPPING_SELECTOR = '.order-totals__row--shipping .order-totals__col--sidebar-price';
		this.TAX_SELECTOR = '.order-sales-tax > td:last-child';

		this.APPLY_CODE_URL = `${BASE_URL}/Coupon-AddLineItem`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Coupon-RemoveLineItem`;
		this.UPDATE_REQUEST_URL = `${DOMAIN}/us/cart`;

		this.CART_FORM_SELECTOR = '.cart-items-form';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_RESPONSE_KEY = 'status';
		this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE = 'OK';

		this.APPLIED_CODE_BUTTON_SELECTOR = '[data-as-click="couponSent"]';
		this.APPLIED_CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.updateQueryParams.set('format', 'ajax');
		this.CSRF_TOKEN_SELECTOR = '[name="csrf_token"]';
		this.PRODUCT_ITEM_SELECTOR = '.cart-product__wrapper';
		this.SPINNER_SELECTOR = '.loader--active';
		this.SIDEBAR_CART_SELECTOR = '.sidebar--visible';
		this.SIDEBAR_CART_BUTTON = '.context-nav__list-item--shopping-bag--onepagecheckout a';
	}

	checkCondition() {
		const isGenieUsed = getCookie('is-genie-used');
		const isOnRightUrl = IS_ON_CART || window.location.href.includes(`${BASE_URL}/CheckoutOnePage`);
		const sidebarWithCart = document.querySelector(this.SIDEBAR_CART_SELECTOR);
		const isSpinner = document.querySelector(this.SPINNER_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isItemOnPage = document.querySelector(this.PRODUCT_ITEM_SELECTOR);
		const btnSidebarOpen = document.querySelector(this.SIDEBAR_CART_BUTTON);
		if (
			isGenieUsed === '1' &&
			!sidebarWithCart &&
			btnSidebarOpen &&
			document.readyState === 'complete' &&
			!isOnRightUrl
		) {
			btnSidebarOpen.click();
			setCookie('is-genie-used', '0', 90);
		}
		return (isOnRightUrl || sidebarWithCart) && !isSpinner && isTotal && isItemOnPage;
	}

	beforeTestCodes() {
		const tokenElem = document.querySelector(this.CSRF_TOKEN_SELECTOR);
		this.applyQueryParams.set('csrf_token', tokenElem ? tokenElem.value : false);
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll('.product-table__coupon__cartcoupon > span')]
			.filter(Boolean)
			.map(element => element.innerText.trim());
		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);
		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	applyCodeManually(code) {
		const codeInputElm = document.querySelector(this.APPLIED_CODE_INPUT_SELECTOR);
		const codeApplyBtn = document.querySelector(this.APPLIED_CODE_BUTTON_SELECTOR);

		setValue(codeInputElm, code);

		codeApplyBtn.click();
		return Promise.resolve();
	}

	removeCode(code) {
		this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	applyCodes(codes) {
		setCookie('is-genie-used', '1', 90);
		return isOnPaymentStep() ? this.applyCodeManually(codes[0]) : this.applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return _getPage(CART_URL).then(responseHTML => {
			const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, responseHTML));
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			return total - shipping - tax;
		});
	}
};

export default {
	name: 'Hugo Boss',
	domain: 'hugoboss.com',
	Driver: CartDriver,
	isSinglePageApp: true,
	completionAction: isOnPaymentStep() ? completionActions.none : completionActions.default,
};
