import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import { completionActions, format, log, DOM } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement } = DOM;

const BASE_URL = 'https://www.kiehls.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;

/*
Driver behavior description:
•    Merchant code: yes
•    Existing code: yes
•    Remove code: yes
•    Stackable: no
•    Free gift support: yes
•    Discount support: yes
•    ‘isValid’ implemented: yes
•    active pages: cart
•    PreTaxShipping: yes
======== Special case ========
sometimes we have the code with free gifts, which we can choose in the pop-up.
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order_total td:last-child';
		this.APPLIED_CODES_SELECTOR = '.remove_coupon';
		this.MERCHANT_CODE_SELECTOR = 'span.promo-code';
		this.OFFER_PROMO_SELECTOR = '.b-main_menu_product_description p';
		this.OFFER_PROMO_REGEX = /code:\s+(\w+)/i;
		this.SHIPPING_TYPE_SELECTOR = '[name="shippingmethods"]';
		this.INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.PRODUCT_ITEM_GIFT_SELECTOR = '.bonus_row .item_quantity';
		this.PRODUCT_ITEM_SELECTOR = `select[name^="dwfrm_cart_shipments_i0_items_i"],
			.sample_row .rigth_colums_row .qty,
			.bonus_row .rigth_colums_row .qty,
			${this.PRODUCT_ITEM_GIFT_SELECTOR}`;
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.BASE_REQUEST_URL = 'https://www.kiehls.com/on/demandware.store/Sites-kiehls_us-Site/en_US';
		this.GET_PRODUCT_REQUEST_URL = `${this.BASE_REQUEST_URL}/Product-GetBonusProducts`;
		this.ADD_PRODUCT_REQUEST_URL = `${this.BASE_REQUEST_URL}/Cart-AddBonusProduct`;
		this.APPLY_CODE_URL = `${this.BASE_REQUEST_URL}/Cart-AddRemoveCoupon`;
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.UPDATE_CART_REQUEST_URL = `${this.BASE_REQUEST_URL}/COCheckout-GetOrderSummaryModel`;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.SHIPPING_REQUEST_URL = `${this.BASE_REQUEST_URL}/COShipping-SelectShippingMethod`;
		this.GET_OFFER_PAGE_REQUEST_URL = `${BASE_URL}/offers`;
		this.applyQueryParams = new URLSearchParams({
			format: 'ajax',
			type: 'add',
		});
		this.removeQueryParams = new URLSearchParams({
			format: 'ajax',
			type: 'remove',
		});

		this.TAX_VALUE_SELECTOR = '.order_sales_tax td:last-child';
		this.HIDDEN_TAX_SELECTOR = '.estimated_tax_result_box .estimated_tax_amount';
		this.SHIPPING_VALUE_SELECTOR = '.order_shipping td:last-child';
		this.SHIPPING_DISCOUNT_VALUE_SELECTOR = '.order_shipping_discount td:last-child';
		this.ORDER_SUB_TOTAL_SELECTOR = '.order_subtotal td:last-child';
		this.GIFT_SCRIPT_SELECTOR = '#statuses';
		this.PRODUCT_PID_SELECTOR = '[id*="check-bonus-"]';
		this.PRODUCT_PID_REG = /(\d+)/g;
		this.PROMO_MAX_ITEM_COUNT_SELECTOR = '.promotion p';
		this.PROMO_MAX_ITEM_COUNT_REG = /please\s+select\s+(\d+)/i;
		this.isStackable = false;
	}

	checkCondition() {
		const cartPage = window.location.href.startsWith(this.CART_PAGE_URL);
		const isInput = document.querySelector(`${this.INPUT_SELECTOR}, ${this.APPLIED_CODES_SELECTOR}`);
		const isEmpty = document.querySelector(this.TOTAL_SELECTOR) && this.parseTotal() === 0;
		const subTotalEl = document.querySelector(this.ORDER_SUB_TOTAL_SELECTOR);
		const isSubTotalNotZero = subTotalEl && elemToFloat(subTotalEl);
		return cartPage && !isEmpty && isInput && isSubTotalNotZero;
	}

	async getMerchantCodes() {
		const responseHTML = await this._getOfferPage();
		const offerCodes = [...extractElement(this.OFFER_PROMO_SELECTOR, responseHTML, true)]
			.map(elem => elem.innerText.match(this.OFFER_PROMO_REGEX))
			.filter(Boolean)
			.map(elem => elem[1]);

		const cartPageCodes = [...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
			.map(el => el.textContent.trim())
			.filter(Boolean);
		return [...new Set([...offerCodes, ...cartPageCodes])];
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			codeElement => codeElement.dataset.coupon,
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this.removeCode(codes[0]).then(responseHTML => {
			return this._getCartPage().then(response => ({
				originalPrice: this.parseTotal(response),
				originalItemsCount: this.parseItemsCount(responseHTML),
			}));
		});
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
			};
			return this.applyCode(code).then(async applyCodeResponse => {
				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				const extraItemsCount = await this._checkAndApplyGiftsFromPopup(code, applyCodeResponse);

				const orderSummary = await this._getCartPage();
				result.finalPrice = this.parseTotal(orderSummary);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(applyCodeResponse) + extraItemsCount - originalItemsCount;

				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					workingDiscounts.push(result);
				}
				await this.removeCode(code).then(() => bus.$emit('code-checked', result));
				return Promise.resolve();
			});
		});
	}

	async applyCodes(codes) {
		const applyCodeResponse = await this.applyCode(codes[0]);
		await this._checkAndApplyGiftsFromPopup(codes[0], applyCodeResponse);
		return Promise.resolve();
	}

	async getPreTaxShippingTotal() {
		const responseJSON = await this._getCartPage();
		const { estimatedTotal, shippingDiscount = 0, shippingAmount, tax } = responseJSON;
		return toFloat(estimatedTotal) - toFloat(shippingAmount) - toFloat(tax) + toFloat(shippingDiscount);
	}

	parseTotal(data) {
		if (data) {
			return toFloat(data.estimatedTotal);
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR));
	}

	parseItemsCount(html) {
		return [...extractElement(this.PRODUCT_ITEM_SELECTOR, html, true)]
			.filter(item => item.value || item.textContent)
			.map(item => item.value || item.textContent)
			.reduce((a, b) => toFloat(a) + toFloat(b), 0);
	}

	// this method return json total without info about free gifts
	// if we use get /cart html response, we can't get correct full tax and shipping info
	// because it partly load in demandware AJAX and sometimes this info can be incorrect
	_getCartPage() {
		return this._sendRequestGET(this.UPDATE_CART_REQUEST_URL, this.UPDATE_REQUEST_HEADERS, this.updateQueryParams);
	}

	_sendRequest(method, requestURL, headers, queryParams, bodyData) {
		const finalRequestURL =
			queryParams && queryParams.toString() ? `${requestURL}?${queryParams.toString()}` : requestURL;

		return this._shippingRequest().then(() =>
			fetch(finalRequestURL, {
				method,
				credentials: 'include',
				headers,
				body: bodyData,
			})
				.then(response => response.text())
				.then(response => {
					// Return JSON if it is possible
					try {
						return JSON.parse(response);
					} catch (e) {
						return response;
					}
				}),
		);
	}

	// we should send shipping request each time because shipping can set by default after other requests
	_shippingRequest() {
		const shippingEl = document.querySelector(this.SHIPPING_TYPE_SELECTOR);
		if (shippingEl) {
			const queryParams = new URLSearchParams({
				shippingMethodID: Number(shippingEl.value),
			});
			const url = `${this.SHIPPING_REQUEST_URL}?${queryParams.toString()}`;
			return fetch(url, {
				method: 'GET',
				credentials: 'include',
			}).then(response => response.json());
		}
		return Promise.resolve();
	}

	_extraGiftsIdAfterApply(responseHTML) {
		const scriptTextContent = extractElement(this.GIFT_SCRIPT_SELECTOR, responseHTML);
		const scriptObject = scriptTextContent ? JSON.parse(scriptTextContent.textContent.trim()) : null;
		return scriptObject && scriptObject.bonusItemId ? scriptObject.bonusItemId[0] : null; // null or bonusId
	}

	_getExtraGiftsFromPopupRequest(extraGiftsId) {
		const queryParams = new URLSearchParams({
			bonusDiscountLineItemUUID: extraGiftsId,
			format: 'ajax',
		});
		const url = `${this.GET_PRODUCT_REQUEST_URL}?${queryParams.toString()}`;
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	// we can add max 3 bonus items,
	// get 3 product PID
	_getProductPID(responseHTML) {
		const promoMaxItemCountEl = [...extractElement(this.PROMO_MAX_ITEM_COUNT_SELECTOR, responseHTML, true)].find(
			item => item.textContent.match(this.PROMO_MAX_ITEM_COUNT_REG),
		);
		const maxSelectedItemCount = promoMaxItemCountEl
			? Number(promoMaxItemCountEl.textContent.match(this.PROMO_MAX_ITEM_COUNT_REG)[1])
			: 0;

		return [...extractElement(this.PRODUCT_PID_SELECTOR, responseHTML, true)]
			.map(item => item.getAttribute('id'))
			.slice(0, maxSelectedItemCount)
			.map(itemId => itemId.match(this.PRODUCT_PID_REG)[0]);
	}

	_addExtraGiftsFromPopupRequest(body, extraGiftsId) {
		const queryParams = new URLSearchParams({
			bonusDiscountLineItemUUID: extraGiftsId,
			personalizationFlow: false,
		});
		const url = `${this.ADD_PRODUCT_REQUEST_URL}?${queryParams.toString()}`;
		return fetch(url, {
			method: 'POST',
			credentials: 'include',
			body: JSON.stringify(body),
		}).then(response => response.json());
	}

	async _checkAndApplyGiftsFromPopup(code, applyCodeResponse) {
		const extraGiftsId = this._extraGiftsIdAfterApply(applyCodeResponse);
		let extraItemsCount = 0;
		if (extraGiftsId) {
			const extraGiftsHTML = await this._getExtraGiftsFromPopupRequest(extraGiftsId);
			const extraGiftsPID = await this._getProductPID(extraGiftsHTML);
			const bonusproducts = extraGiftsPID.map(item => {
				return {
					product: {
						options: {},
						pid: item,
						productName: '',
						qty: 1,
					},
				};
			});
			if (bonusproducts.length) {
				const { success } = await this._addExtraGiftsFromPopupRequest({ bonusproducts }, extraGiftsId);
				if (success) {
					log(`% ${bonusproducts.length} free gifts were added from the gift-popup after applying: ${code}`);
					extraItemsCount = bonusproducts.length;
				}
			}
		}
		return extraItemsCount || 0;
	}

	_getOfferPage(url = this.GET_OFFER_PAGE_REQUEST_URL) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: "Kiehl's",
	domain: 'kiehls.com',
	Driver,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
};
