import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

/* Behavior description:
	- can get existing codes
	- can remove codes on cart page
	- cannot apply invalid code
	- can apply multiple codes (stackable)

	======== Special case ========
	have to reload checkout page to apply code, can't avoid loss of data
*/

const CART_PAGE_URL = 'https://www.pendleton-usa.com/cart';
const BIILING_PAGE_URL = 'https://www.pendleton-usa.com/billing';
const ON_CART_PAGE = window.location.href.startsWith(CART_PAGE_URL);

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.isStackable = true;

		const BASE_URL = 'https://www.pendleton-usa.com/on/demandware.store/Sites-PWM-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'Pendleton',
	domain: 'pendleton-usa.com',
	stackable: true,
	Driver,
	cartContinueUrl: !ON_CART_PAGE ? BIILING_PAGE_URL : false,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		return document.querySelector('.cart-empty');
	},
	isOnCartUrl(href) {
		const correctPages = ['billing', 'COShipping-SingleShipping', 'COBilling-Billing'];
		return (
			ON_CART_PAGE ||
			(correctPages.some(urlPart => href.includes(urlPart)) && document.querySelector('#dwfrm_billing'))
		);
	},
};
