import DemandwareAJAXDriver from './_base';
import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes (can remove on basket)
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Payment
+ Is driver SPA: yes
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_PAGE_URL = 'https://www.crabtree-evelyn.com/cart';

		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';

		this.BASE_URL = 'https://www.crabtree-evelyn.com/on/demandware.store/Sites-crabtreeevelyn_us-Site/default';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-UpdateSummary`;

		this.APPLIED_CODES_SELECTOR = '.rowcoupons .cart-value';
		this.SHIPPING_SELECTOR = '.order-shipping .valuealign';
		this.TAX_SELECTOR = '.order-sales-tax .valuealign';

		this.applyQueryParams.set('format', 'ajax');

		this.isStackable = true;
	}

	checkCondition() {
		return Boolean(document.querySelector(this.CODE_INPUT_SELECTOR));
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const calculatedTax = tax || 0;
			const calculatedShipping = shipping || 0;
			return this.parseTotal(response) - calculatedShipping - calculatedTax;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code.toUpperCase());
	}

	applyCode(code) {
		return super.applyCode(code.toUpperCase());
	}

	_isOnCartPage() {
		return document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'Crabtree & Evelyn',
	domain: 'crabtree-evelyn.com',
	isSinglePageApp: true,
	stackable: true,
	Driver,
};
