import DemandwareAJAXDriver from './_base';

/**
Driver behavior description:
+ Existing code: yes 
+ Remove code: yes 
+ Stackable: yes 
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart
+ Is driver SPA: no
+ Gather codes on site pages: no
 */

const CART_URL = 'https://www.nixon.com/us/en/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total-value';
		this.APPLIED_CODES_SELECTOR = '[data-promocode]';
		this.isStackable = true;

		const BASE_URL = 'https://www.nixon.com/on/demandware.store/Sites-US-Site/en_US/FluidCheckout';
		this.APPLY_CODE_URL = `${BASE_URL}-ApplyPromotionCode`;
		this.REMOVE_CODE_URL = `${BASE_URL}-RemovePromotionCode`;
		this.UPDATE_REQUEST_URL = CART_URL;

		this.applyQueryParams.set('returnObject', 'Basket,OrderSummary,ShippingOptions');
		this.removeQueryParams = this.applyQueryParams;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(codeElement =>
			codeElement.getAttribute('data-promocode'),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	applyCode(code) {
		this.applyQueryParams.set('promoCode', code);
		this.applyQueryParams.set('userSubmittedCode', code);

		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	removeCode(code) {
		this.removeQueryParams.set('promoCode', code);
		this.removeQueryParams.set('userSubmittedCode', code);

		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'Nixon',
	domain: 'nixon.com',
	stackable: true,
	Driver,
	isCartEmpty() {
		return document.querySelector('.empty-cart');
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_URL);
	},
};
