import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import DWBaseDriver from '../demandware/_base';
import { completionActions, log, sendXhr, format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable:  no
+ Free gift support:  no
+ Free shipping:  no
+ ‘isValid’ implemented:  no
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Cart page, billing step of the checkout
+ Is driver SPA no
+ Gather codes on site pages: yes, from Cart
+ Special case about the driver works: Implemented as 2 separate drivers for Cart, Checkout
+ Credentials (for signing in): ypa@mail.com / Temporary_1
+ sessionTimer: 15 min
*/

// check is this cart url to manage comletionaActions and call proper driver
const isOnCart = () =>
	(window.location.href.includes('bathandbodyworks.com/cart') ||
		window.location.href.includes('bathandbodyworks.com/on')) &&
	document.querySelector('#cart-items-form');
// get existing codes function is same for both drivers
const APPLIED_CODES_SELECTOR = '[data-couponcode]';
function _getExistingCodes() {
	const codes = [...document.querySelectorAll(APPLIED_CODES_SELECTOR)].map(el => el.getAttribute('data-couponcode'));

	return {
		codes,
		codeCount: codes.length,
	};
}

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode, #dwfrm_cart_couponCode';
		this.TOTAL_SELECTOR = '.order-total .order-value';

		this.MERCHANT_CODE_SELECTOR = '.promo-banner';
		this.MERCHANT_CODE_REGEX = /Code: (\w+)/i;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		const BASE_URL = 'https://www.bathandbodyworks.com/on/demandware.store/Sites-BathAndBodyWorks-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCouponCode`;

		this.CART_PAGE_URL = 'https://www.bathandbodyworks.com/cart';

		this.applyQueryParams.set('format', 'ajax');

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	getMerchantCodes() {
		return this._getUpdatedCart().then(response => {
			return [...extractElement(this.MERCHANT_CODE_SELECTOR, response, true)]
				.filter(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX))
				.map(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX)[1]);
		});
	}

	getExistingCodes() {
		return _getExistingCodes();
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	removeCode(code) {
		const bodyData = new URLSearchParams({ code });
		return this._sendRequestPOST(
			this.REMOVE_CODE_URL,
			{ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
			null,
			bodyData,
		);
	}

	_getUpdatedCart() {
		return fetch('https://www.bathandbodyworks.com/cart', {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_sendRequest(method, requestURL, headers, queryParams, bodyData) {
		const finalRequestURL = queryParams ? `${requestURL}?${queryParams.toString()}` : requestURL;
		// In Firefox we may fail with fetch (possibly because of origin header)
		return sendXhr(finalRequestURL, {
			method,
			credentials: 'include',
			headers,
			body: bodyData,
		})
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}
};

const RegularDwDriver = class DwDriver extends DWBaseDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.MERCHANT_CODE_SELECTOR = '.promo-banner';
		this.MERCHANT_CODE_REGEX = /Code: (\w+)/i;

		this.isStackable = false;
		this.ERROR_MESSAGE_SELECTOR = '.error-form';

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
			.filter(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX))
			.map(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX)[1]);
	}

	getExistingCodes() {
		return _getExistingCodes();
	}

	checkCodes(codes) {
		const originalPrice = this.parseTotal(this.lastResponse);
		log('originalPrice: %s', originalPrice);
		const originalItemsCount = this.parseItemsCount(this.lastResponse);
		log('originalItemsCount: %s', originalItemsCount);
		const workingDiscounts = [];
		return Promise.each(codes, code => {
			return Promise.delay(1000).then(() =>
				this.applyCode(code, this._requestData()).then(html => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						freeGifts: 0,
						isValid: false,
					};
					if (this._codeAppliedCheck(html)) {
						return bus.$emit('code-checked', result);
					}
					this.lastResponse = html;
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(html) - originalItemsCount;
					result.isValid = true;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(this._removeValueName(code), this._requestData()).then(removeCodeHtml => {
						this.lastResponse = removeCodeHtml;
						return bus.$emit('code-checked', result);
					});
				}),
			);
		}).then(() => {
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			});
			return Promise.resolve();
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Bath & Body Works',
	domain: 'bathandbodyworks.com',
	completionAction: completionActions.revisit,
	get Driver() {
		return isOnCart() ? RegularDwDriver : Driver;
	},
	isOnCartUrl(href) {
		return (
			(href.includes('bathandbodyworks.com/cart') ||
				href.includes('bathandbodyworks.com/checkout') ||
				href.includes('bathandbodyworks.com/on')) &&
			document.querySelector('#cart-items-form, #dwfrm_billing')
		);
	},
};
