import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';

/* Behavior description:
+ Existing code: yes, but not always;
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: valid code be replaced with another valid code with bigger discount
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(if Shipping form is not active)
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ credentials: joseph@gmail.com, 12345qwerty
======== Special case ========
Sometimes element with getExistingCode disappears.
(We have similar problems if get code from a page, but from another elements)

Not always can get existing code. If we want to remove code,
we need to pass valid code in removeCodes params, but this is not possible.
sometimes we can see fake discount with FAKE_CODE after code testing on popup.
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = false;

		this.TOTAL_SELECTOR = '.order-total-value';
		this.APPLIED_CODES_SELECTOR = '.promo-code-applied .message';
		this.REG_EXP = /applied code ([\w-]+)/;
		this.REG_TOKEN = /var\s+token\s+=\s+\{"token":"(\w+)/;

		const BASE_URL = 'https://www.johnvarvatos.com/on/demandware.store/Sites-johnvarvatos-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/FluidCheckout-ApplyPromotionCode`;
		this.REMOVE_CODE_URL = `${BASE_URL}/FluidCheckout-RemovePromotionCode`;
		this.UPDATE_REQUEST_URL = window.location.href;

		this.CART_PAGE_URL = 'https://www.johnvarvatos.com/shoppingbag';
		this.APPLY_CODE_PARAM_NAME = 'promoCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.EMPTY_CART_SELECTOR = '.empty-cart';
		this.CODE_INPUT_SELECTOR = '[name="promoCode"]';
		this.NOT_SHIPPING_STEP_SELECTOR = '#shippingForm.summary';
		this.SHIPPING_FORM_FILLED_SELECTOR = '#shipping-addressForm .saved-addresses';
		this.OUT_OF_STOCK_SELECTOR = '.cart-error';
		this.applyQueryParams.set('returnObject', 'Basket,OrderSummary,ShippingOptions');
	}

	checkCondition() {
		const { href } = window.location;
		const isEmptyCartEl = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isCodeInputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		const itemOutOfStockEl = document.querySelector(this.OUT_OF_STOCK_SELECTOR);
		const isNotShipping = href.includes('checkoutpage')
			? document.querySelector(this.NOT_SHIPPING_STEP_SELECTOR) ||
			  (document.querySelector(this.SHIPPING_FORM_FILLED_SELECTOR) || {}).offsetParent
			: true;
		return isCodeInputEl && !isEmptyCartEl && isNotShipping && !itemOutOfStockEl;
	}

	beforeTestCodes() {
		const token = [...document.querySelectorAll('script')]
			.map(el => el.textContent)
			.find(element => element.match(this.REG_TOKEN))
			.match(this.REG_TOKEN)[1];

		this.applyQueryParams.set('token', token);
		this.removeQueryParams = this.applyQueryParams;
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(el => el.innerText.match(this.REG_EXP)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'John Varvatos',
	domain: 'johnvarvatos.com',
	Driver,
	isSinglePageApp: true,
};
