import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';
import { toFloat } from '../../../utils/format';
import { extractData, extractElement } from '../../../utils/DOM';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(Billing)
+ Is driver SPA: no
+ Gather codes on site pages: no
 */

const BASE_URL = 'https://www.vitaminworld.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const BILLING_URL = `${BASE_URL}/billing`;
const BILLING_EDIT_URL = `${BASE_URL}/billing-edit`;
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-vitaminworld_us-Site/default`;
const CART_SUBMIT_URL = `${BASE_REQUEST_URL}/Cart-SubmitForm`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.CODE_VALUE_SELECTOR = '.billing-applied .label-promo';
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-of-type';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-of-type';
		this.TAX_SELECTOR = '.order-sales-tax td:last-of-type';
		this.APPLIED_CODES_SELECTOR = '.label-promo';
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shippingDiscount = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML) || 0;
			const shipping = extractData(this.SHIPPING_SELECTOR, responseHTML);
			const tax = extractData(this.TAX_SELECTOR, responseHTML);

			const calculatedShippingDiscount = shippingDiscount !== 0 ? shippingDiscount.textContent : 0;
			const calculatedShipping = shipping.toLowerCase().includes('free') ? 0 : shipping;
			const calculatedTax = tax.trim().includes('TBD') ? 0 : tax;

			return total - toFloat(calculatedShipping) - toFloat(calculatedTax) + toFloat(calculatedShippingDiscount);
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href.includes('Cart-SubmitForm') ? CART_PAGE_URL : window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(responseHTML => responseHTML.text());
	}
};

export default {
	name: 'Vitamin World',
	domain: 'vitaminworld.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	cartContinueUrl: window.location.href.includes('Cart-SubmitForm') ? CART_PAGE_URL : window.location.href,
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) ||
				href.startsWith(BILLING_URL) ||
				href.startsWith(BILLING_EDIT_URL) ||
				href.startsWith(CART_SUBMIT_URL)) &&
			document.querySelector('#dwfrm_cart_couponCode, #dwfrm_billing_couponCode')
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.mini-cart-empty'));
	},
};
