import DemandwareAJAXDriver from './_base';
import { DOM, completionActions } from '../../../utils';
import { elemToFloat } from '../../../utils/format';

const { extractElement } = DOM;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Gather codes on site pages: no
*/

const CART_PAGE_URL = 'https://www.lumens.com/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.ordertotal .value';
		this.APPLIED_CODES_SELECTOR = '.applycoupon li, .redemption .submitedcoupon';
		this.PRODUCT_ITEM_SELECTOR = '.cartproductrow, .checkoutminisummary .summaryproduct';
		this.isStackable = true;

		const BASE_URL = 'https://www.lumens.com/on/demandware.store/Sites-Lumens-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCLI`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.SHIPPING_SELECTOR = '.ordershipping .value';
		this.TAX_SELECTOR = '.ordersalestax .taxCalcSpan';

		this.CART_FORM_SELECTOR = '#dwfrm_cart';

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'coupon';
		this.CODE_REGEX = /: (.*?) Remove/;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			codeElement => codeElement.textContent.match(this.CODE_REGEX)[1],
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const calculatedShipping = Number.isNaN(shipping) ? 0 : shipping;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			const calculatedTax = Number.isNaN(tax) ? 0 : tax;
			return this.parseTotal(response) - calculatedShipping - calculatedTax;
		});
	}
};

export default {
	name: 'Lumens',
	domain: 'lumens.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cartempty'));
	},
	isOnCartUrl(href) {
		return href.startsWith(CART_PAGE_URL);
	},
};
