import DemandwareAJAXDriver from './_base';
import completionActions from '../../../utils/completion-actions';

import { format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
	- Cart page
+ Is driver SPA yes
+ Gather codes on site pages: yes (https://tartecosmetics.com), cart page
+ SessionTimer: 15min
Credentials: ypa@mail.com / Temporary_1
*/

const DOMAIN_URL = 'https://tartecosmetics.com';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.PRODUCT_ITEM_SELECTOR = '.cart-row';
		this.CART_PAGE_URL = `${DOMAIN_URL}/en_US/bag`;

		this.CODE_VALUE_SELECTOR = '.cartcoupon .value, .coupon .success';
		this.CART_FORM_SELECTOR = '.cart-items-form';

		const BASE_URL = `${DOMAIN_URL}/on/demandware.store/Sites-tarte-Site/en_US`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;

		// On cart page shipping and tax is not included, need to get updated total from cart
		this.UPDATE_REQUEST_URL = this.CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');

		this.CHARGES_REGEX = /(shipping|handling|tax)/i;
		this.CHARGES_SELECTOR = '.order-totals-table td:last-child';

		this.BANNER_SELECTOR = 'a[data-click="hyperlink"]';
		this.SUBSCRIBED_USER_CODE_SELECTOR = '.coupon';
		this.MERCHANT_CODE_SELECTOR = '.code-text';
		this.MERCHANT_CODE_REGEX = /code. (\w+)/i;
		sessionTimer.initialize();
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightPage = href.startsWith(this.CART_PAGE_URL);
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);

		return isOnRightPage && codeInput;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	async getMerchantCodes() {
		const subscribedUserCodeElement = document.querySelector(this.SUBSCRIBED_USER_CODE_SELECTOR);
		const subscribedUserCode = subscribedUserCodeElement ? [subscribedUserCodeElement.textContent] : [];

		const cartCodes = [...document.querySelectorAll(this.BANNER_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);

		const mainPage = await fetch(DOMAIN_URL, { method: 'GET' });
		const mainPageHtml = await mainPage.text();
		const mainPageCodes = extractElement(this.MERCHANT_CODE_SELECTOR, mainPageHtml, true)
			? [...extractElement(this.MERCHANT_CODE_SELECTOR, mainPageHtml, true)]
					.map(item => item.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(item => item[1])
					.filter(Boolean)
			: [];

		return [...new Set([...cartCodes, ...mainPageCodes, ...subscribedUserCode])];
	}

	async getPreTaxShippingTotal() {
		const cartStr = await fetch(this.UPDATE_REQUEST_URL, { credentials: 'include' }).then(response =>
			response.text(),
		);
		const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, cartStr));
		const taxes = [...extractElement(this.CHARGES_SELECTOR, cartStr, true)]
			.filter(el => el.parentElement.textContent.match(this.CHARGES_REGEX))
			.map(elemToFloat)
			.filter(Boolean)
			.reduce((charges, charge) => charges + charge, 0);
		return total - taxes;
	}
};

export default {
	name: 'Tarte Cosmetics',
	domain: 'tartecosmetics.com',
	completionAction: completionActions.revisit,
	Driver,
	isSinglePageApp: true,
};
