import DemandwareAJAXDriver from './_base';
import bus from '../../bus';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isOnCartPage = window.location.href.includes('/cart');
		this.CODES_INPUTS_SELECTORS = '#dwfrm_couponcode_couponCode , #dwfrm_cart_couponCode';
		this.isOnCheckoutPage = window.location.href.includes('/COCheckout-Show');

		this.TOTAL_SELECTOR = '.order_total .text_right';
		const BASE_URL =
			'https://www.lancome-usa.com/on/demandware.store/Sites-lancome-us-Site/default/Cart-AddRemoveCoupon';
		this.isStackable = true;

		this.APPLY_CODE_URL = BASE_URL;
		this.REMOVE_CODE_URL = BASE_URL;
		this.UPDATE_REQUEST_URL = BASE_URL;

		this.REMOVE_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.applyQueryParams.set('type', 'add');

		this.updateQueryParams.set('format', 'ajax');

		this.removeQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('type', 'remove');
	}

	getExistingCodes() {
		let codes = [...document.querySelectorAll('.coupon__info .remove_coupon')].map(el =>
			el.getAttribute('data-coupon'),
		);
		codes = [...new Set(codes)];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	watchForCartUrl() {
		let inputExistsBefore = null;
		const intervalId = setInterval(() => {
			// Activate extension popup in case we are on the right URL and
			// we have codes input visible
			const checkoutCodeInput = Boolean(document.querySelector(this.CODES_INPUTS_SELECTORS));
			const inputStateChanged = checkoutCodeInput !== inputExistsBefore;
			const isOnCartOrCheckout = this.isOnCartPage || this.isOnCheckoutPage;
			if (isOnCartOrCheckout && checkoutCodeInput && inputStateChanged) {
				bus.$emit('cart-active');
				clearInterval(intervalId);
			}
			inputExistsBefore = checkoutCodeInput;
		}, 1000);
	}
};

export default {
	name: 'Lancome',
	domain: 'lancome-usa.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith(
				'https://www.lancome-usa.com/on/demandware.store/Sites-lancome-us-Site/default/COCheckout-Show',
			) && href.startsWith('https://www.lancome-usa.com/cart')
		);
	},
};
