import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import { DOM, completionActions, format, log } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
    - Cart page
    - Checkout page (Billing step with folded address form)

+ Is driver SPA yes
	- Right page
	- Address form on the billing step is collapsed without an applied code
    - Not empty

+ Gather codes on site pages: no
*/

const CART_PAGE_URL = 'https://www.hickoryfarms.com/cart';
const isOnCart = () => window.location.href.startsWith(CART_PAGE_URL);

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .hf-price-right';

		const BASE_URL = 'https://www.hickoryfarms.com/on/demandware.store/Sites-HickoryFarms-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.applyQueryParams.set('sourceCode', true);
		this.removeQueryParams = this.applyQueryParams;

		this.APPLIED_CODE_SELECTOR = '.delete-coupon';

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.currentPrice = null;

		this.SHIPPING_SELECTOR = '.order-shipping .hf-price-right';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount .hf-price-right';
		this.TAX_SELECTOR = '.order-sales-tax .hf-price-right a';
	}

	checkCondition() {
		const isCartEmpty = (document.querySelector('.cart-empty') || {}).offsetParent;
		const isOnBilling = window.location.href.endsWith('https://www.hickoryfarms.com/billing');
		const isBillingForm = (document.querySelector('.billing-upper') || {}).offsetParent;
		const isCodeUnappplied = !this._getExistingCodes().codeCount;
		return (isOnCart() || (isOnBilling && !isBillingForm && isCodeUnappplied)) && !isCartEmpty;
	}

	getExistingCodes() {
		return this._getExistingCodes();
	}

	removeCodes(codes) {
		return this.removeCode(codes[0])
			.then(() => this._getOrderSummary())
			.then(response => {
				this.currentPrice = this.parseTotal(response);
				return this.currentPrice;
			});
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		this.currentPrice = originalPrice;
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this._getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;

					if (result.finalDiscount < 0) {
						result.isValid = false;
						return bus.$emit('code-checked', result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	_getOrderSummary() {
		if (isOnCart()) {
			return this.getCartPage();
		}
		return super.getOrderSummary();
	}

	_getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(el => el.value);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this._getOrderSummary().then(response => {
			const shippingDiscountElem = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const total = this.parseTotal(response);
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscount = shippingDiscountElem && shipping ? elemToFloat(shippingDiscountElem) : 0;
			return total - tax - shipping + shippingDiscount;
		});
	}

	getCartPage(count = 0) {
		// In some case total price doesn't change with first response,
		// so we try repeat request again and check when price change.
		return this._sendRequestGET(CART_PAGE_URL, {}).then(response => {
			const total = this.parseTotal(response);
			if (this.currentPrice && this.currentPrice <= total && count < 2) {
				return this.getCartPage(count + 1);
			}
			return response;
		});
	}

	parseTotal(html) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, html));
	}
};

export default {
	name: 'Hickory Farms',
	domain: 'hickoryfarms.com',
	Driver,
	isSinglePageApp: true,
	completionAction: completionActions.revisit,
};
