import DemandwareAJAXDriver from './_base';
import { extractElement } from '../../../utils/DOM';
import { elemToFloat } from '../../../utils/format';

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Billing
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODE_SELECTOR = 'button[data-coupon]';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		const BASE_URL = 'https://us.boohoo.com/on/demandware.store/Sites-boohoo-NA-Site/en_US/';

		this.APPLY_CODE_URL = `${BASE_URL}Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}COBilling-UpdateSummary`;

		this.REMOVE_CODE_PARAM_NAME = 'couponID';

		this.applyQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(el => el.dataset.coupon);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes(originalPrice) {
		if (originalPrice) {
			return originalPrice;
		}
		// if delete code manually on page present two different totals price (current and old)
		// need to send request to get right total
		return this.getOrderSummary().then(response => this.parseTotal(response));
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);

		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, this.applyQueryParams);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const taxEl = extractElement(this.TAX_SELECTOR, response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}
};

export default {
	name: 'Boohoo',
	domain: 'boohoo.com',
	Driver,
	isOnCartUrl(href) {
		return href.startsWith('https://us.boohoo.com/billing-continue');
	},
};
