import Promise from 'bluebird';
import bus from '../../bus';
import DemandwareAJAXDriver from './_base';
import { DOM, completionActions, log } from '../../../utils';

const { extractElement, extractData } = DOM;

/*
Driver behavior:
•   Existing code: yes
•   Remove code: yes
•   Code retesting: yes
•   Stackable: no
•   Free gift support: no
•   isValid implemented: yes
•   Show message about free shipping: no
•   Driver is SPA: yes, on cart when total is present
•   Gather merchant codes: yes
•   Special case: no
•   Credentials: no
•   support only cart page
 */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_FORM_SELECTOR = '.cart-coupon-code [class*="coupon-code"]';
		this.CODE_VALUE_SELECTOR = '.cart-table .cartcoupon .value';
		this.APPLIED_CODES_SELECTOR = '.cart-table .cartcoupon .value, #promoCodeHolder';
		this.TOTAL_SELECTOR = '.order-subtotal .right, .value.ocp-order-total-value';
		this.FLAG_SELECTOR = '.flag[title=US]';

		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.CART_PAGE_URL = 'https://www.pacsun.com/on/demandware.store/Sites-pacsun-Site/default/Cart-Show';

		const baseURL = 'https://www.pacsun.com/on/demandware.store/Sites-pacsun-Site/default';
		this.APPLY_CODE_URL = `${baseURL}/COCheckoutUtil-AddPromoCode`;
		this.REMOVE_CODE_URL = `${baseURL}/COCheckoutUtil-DeletePromoCode`;
		this.UPDATE_REQUEST_URL = this.CART_PAGE_URL;

		this.applyQueryParams.set('instart_disable_injection', true);
		this.applyQueryParams.set('format', 'ajax');

		this.removeQueryParams.set('instart_disable_injection', true);
		this.removeQueryParams.set('format', 'ajax');

		this.updateQueryParams.set('instart_disable_injection', true);
		this.updateQueryParams.set('format', 'ajax');

		this.EMPTY_CART_SELECTOR = '.cart-empty-msg';
		this.CODE_ERROR_MESSAGE = '.promoError';
		this.CODE_NOT_APPLIED_MSG = '.bonusitem';
		this.CODE_REGEX = /Promo Code:\s+(\w+)/;
		this.MERCHANT_COUPON_REGEX = /(code|coupon|promo)\W{1,3}(\w+)/i;
		this.PROMO_BAR_SELECTOR = '.nano-bar';
	}

	checkCondition() {
		const { href } = window.location;
		const correctTotal = (document.querySelector(this.TOTAL_SELECTOR) || { innerText: '' }).innerText.match(
			'[0-9]+',
		);
		const cartCouponForm = document.querySelector(this.CART_FORM_SELECTOR);
		const correctPage = href.toLowerCase().startsWith(this.CART_PAGE_URL.toLowerCase());
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isUSA = document.querySelector(this.FLAG_SELECTOR);
		return correctPage && cartCouponForm && !emptyCart && correctTotal && isUSA;
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.PROMO_BAR_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_COUPON_REGEX))
			.filter(Boolean)
			.filter(arr => arr.length >= 3)
			.map(arr => arr[2].toUpperCase())
			.filter((code, index, arr) => arr.indexOf(code) === index);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(codeElement =>
			codeElement.innerText.trim(),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};

				const codeCheck = this._isCodeInvalid(applyCodeResponse);

				if (codeCheck) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	getPreTaxShippingTotal() {
		// delay added because of sentry errors
		return Promise.delay(3000)
			.then(() => this.getOrderSummary())
			.then(responseHTML => {
				// no taxes on the cart page, and no shipping
				return this.parseTotal(responseHTML);
			});
	}

	_isCodeInvalid(data) {
		return (
			extractElement(this.CODE_ERROR_MESSAGE, data) ||
			extractData(this.CODE_NOT_APPLIED_MSG, data).match(/Not Applied/i)
		);
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CART_FORM_SELECTOR);
	}
};

export default {
	name: 'PacSun',
	domain: 'pacsun.com',
	completionAction: completionActions.revisit,
	Driver,
	isSinglePageApp: true,
};
