import DemandwareAJAXDriver from './_base';
import { completionActions, DOM } from '../../../utils';

const { extractElement } = DOM;

const BASE_URL = 'https://shop.americantourister.com/';
const CHECKOUT_PAGE_URL = `${BASE_URL}checkout`;
const BILLING_PAGE_URL = `${BASE_URL}billing`;
const CART_PAGE_URL = `${BASE_URL}cart`;
const APPLY_COUPON_FLD_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
const IS_ON_CART = () => document.querySelector('#dwfrm_cart_couponCode');

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes 
+ Free shipping:  no
+ ‘isValid’ implemented: yes 
+ Pages, where extension works:
   - cart
   - billing
+ Is driver SPA: no
+ Gather codes on site pages:  yes
+ Special case about the driver works.
We can't avoid redirect on the shipping page from the billing
 */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.BASE_REQUEST_URL = `${BASE_URL}on/demandware.store/Sites-americantourister-Site/default`;
		this.APPLY_CODE_URL = `${this.BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_REQUEST_URL}/COBilling-UpdateSummary`;

		this.isStackable = true;
		this.TOTAL_CART_SELECTOR = '.order-subtotal td:last-child';
		this.PRODUCT_ITEM_SELECTOR = '.cart-row .item-total, .checkout-mini-cart .mini-cart-product';
		this.PROPOSED_CODE_SELECTOR = '.html-slot-container p';
		this.CODE_REGEX = /code\s+(\w+)/i;

		this.applyQueryParams.set('format', 'ajax');
		this.TOTAL_SELECTOR = IS_ON_CART() ? this.TOTAL_CART_SELECTOR : this.TOTAL_SELECTOR;
		this.APPLY_REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};
		this.UPDATE_REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};

		this.CHECKOUT_BUTTON_SELECTOR = '#add-coupon';
		this.SUCCESS_MESSAGE_SELECTOR = '.success, .coupon-sucess';
	}

	getMerchantCodes() {
		return this._getUpdatedCart().then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.PROPOSED_CODE_SELECTOR, responseHTML, true)]
						.map(el => el.textContent.match(this.CODE_REGEX))
						.filter(Boolean)
						.map(el => el[1]),
				),
			];
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_getUpdatedCart() {
		return fetch(CART_PAGE_URL, {
			credentials: 'include',
			method: 'GET',
		}).then(response => response.text());
	}

	_isOnCartPage() {
		return IS_ON_CART();
	}
};

export default {
	name: 'American Tourister',
	domain: 'americantourister.com',
	Driver,
	completionAction: completionActions.revisit,
	stackable: true,
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_PAGE_URL) ||
				href.startsWith(BILLING_PAGE_URL) ||
				href.startsWith(CHECKOUT_PAGE_URL) ||
				href.toLowerCase().includes('cart-addtowishlist')) &&
			document.querySelector(APPLY_COUPON_FLD_SELECTOR)
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.mini-cart-empty'));
	},
};
