import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { log } from '../../../utils';
import { extractElement } from '../../../utils/DOM';

/* Driver behavior description:
   + Existing code: yes
   + Remove code: yes
   + Stackable: yes
   + Free gift support: yes
   + ‘isValid’ implemented: yes
   + Gather codes on site pages: yes(cart page)
   + Free shipping: no
   + is SPA: yes
*/

const BASE_URL = 'https://www.surfstitch.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const CHECKOUT_BILLING_URL = `${BASE_URL}/checkout-billing`;
const CHECKOUT_BILLING_BACK_URL = `${BASE_URL}/checkout-billing-back`;
const BASE_REQUEST_URL = 'https://www.surfstitch.com/on/demandware.store/Sites-ss-au-Site/en_AU';

const IS_CART = window.location.href.includes('/cart');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value, .ss-order-summary__total .ss-order-summary__total__amount';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.isStackable = true;
		this.PRODUCT_ITEM_SELECTOR = '.cart-row, .product';
		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode';
		this.MERCHANT_CODE_SELECTOR = '.header-promotion__coupon';
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightUrl =
			href.startsWith(CART_PAGE_URL) ||
			href.startsWith(CHECKOUT_BILLING_URL) ||
			href.startsWith(CHECKOUT_BILLING_BACK_URL);

		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isOnRightUrl && !isCartEmpty && isTotal;
	}

	getMerchantCodes() {
		return this.getCartPage().then(resHtml => {
			return extractElement(this.MERCHANT_CODE_SELECTOR, resHtml)
				? [extractElement(this.MERCHANT_CODE_SELECTOR, resHtml).textContent]
				: [];
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
					freeGifts: 0,
				};

				const successState = this._getValueFromJSON(response);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					result.isValid = false;
					// Code was not applied
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				}

				return this.getOrderSummary().then(cartResponse => {
					result.finalPrice = this.parseTotal(cartResponse);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(cartResponse) - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}
};

export default {
	name: 'SurfStitch',
	domain: 'surfstitch.com',
	stackable: true,
	maxCoupons: 10,
	isSinglePageApp: true,
	cartContinueUrl: IS_CART ? CART_PAGE_URL : window.location.href,
	Driver,
};
