import DemandwareAJAXDriver from './_base';
import { DOM } from '../../../utils';

const { extractElementFromHTML } = DOM;

const CART_PAGE_URL = 'https://www.boxlunch.com/cart';
const CART_FORM_SELECTOR = '#cart-items-form';
const isOnCartPage = () => Boolean(document.querySelector(CART_FORM_SELECTOR));

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
Optional:
•    Show message about free shipping on the cart page: no
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: no */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon, #apply-coupon';
		this.REMOVE_SELECTOR = '.remove-coupon, .coupon-applied .remove';

		this.BASE_URL = 'https://www.boxlunch.com/on/demandware.store/Sites-boxlunch-Site/default';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-UpdateSummary`;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');

		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.isStackable = true;

		this.MAX_WAIT_TIME = 2500;
		this.TIME_STEP = 500;
	}

	checkCondition() {
		const currentState = !!document.querySelector(this.CODE_INPUT_SELECTOR);
		return currentState && isOnCartPage();
	}

	getExistingCodes() {
		return this.getCartPage().then(html => {
			const codes = [...extractElementFromHTML(html, this.CODE_VALUE_SELECTOR, true)]
				.filter(el => Boolean(el))
				.map(element => element.innerText.trim());

			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	applyCodes(codes) {
		return super.applyCodes(codes).then(() => super.applyCode(codes[0]));
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return isOnCartPage();
	}
};

export default {
	name: 'BoxLunch',
	domain: 'boxlunch.com',
	isSinglePageApp: true,
	stackable: true,
	Driver,
	isOnCartUrl() {
		return false;
	},
};
