import DemandwareAJAXDriver from './_base';
import { format, DOM, completionActions } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

const BASE_SITE_URL = 'https://shop.samsonite.com';
const CART_PAGE_URL = `${BASE_SITE_URL}/cart`;
const { href } = window.location;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Don't support checkout due to strange site behaviour
+ Is driver SPA: no
+ Gather codes on site pages: yes, from Cart header
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode';
		this.APPLIED_CODES_SELECTOR = '.promoCodes .code';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.TOTAL_SELECTOR = '.order-total .summary-value, .order-total td:last-child';

		this.TABLE_TOTALS_SELECTOR = '.order-totals .nomarginrow';
		this.PRICE_VALUE_SELECTOR = '.summary-value';

		this.PROPOSED_CODES_SELECTOR = '.header-top-banner .textbold b:nth-child(2)';
		this.CART_FORM_SELECTOR = '#promo-area form';

		this.isStackable = true;

		const BASE_URL = `${BASE_SITE_URL}/on/demandware.store/Sites-samsonite-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.CODE_VALUE_SELECTOR = '.promoCodes .code';

		this.TAX_SELECTOR = '.order-sales-tax .order-totals-value-text';
		this.SHIPPING_SELECTOR = '.order-shipping .order-totals-value-text';
	}

	getMerchantCodes() {
		const code = document.querySelector(this.PROPOSED_CODES_SELECTOR);
		return (code ? [...code.textContent.split('&')] : [null]).filter(Boolean).map(el => el.trim());
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(this._findElement('Shipping', response)) || 0;
			const taxEl = this._findElement('Tax', response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.TABLE_TOTALS_SELECTOR, response, true)].find(
			el => el.textContent.includes(targetString) && !el.textContent.includes('Total'),
		);

		return targetEl ? targetEl.querySelector(this.PRICE_VALUE_SELECTOR) : null;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		return window.location.href.startsWith(this.CART_PAGE_URL) && document.querySelector(this.CODE_INPUT_SELECTOR);
	}
};

export default {
	name: 'Samsonite',
	domain: 'shop.samsonite.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isOnCartUrl() {
		return (
			(href.startsWith(CART_PAGE_URL) || href.includes('/Cart-AddToWishlist')) &&
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
	isCartEmpty() {
		return (document.querySelector('.empty-msg') || {}).offsetParent;
	},
};
