import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

const TOTAL_SELECTOR = '.checkoutminisummary .ordertotal .value, .ordertotalstable .ordertotal .value';
const CHECKOUT_INPUT_SELECTOR = '#dwfrm_billingcoupon_couponCode';

const isCheckout = () => Boolean(document.querySelector(CHECKOUT_INPUT_SELECTOR));

const isTotal = () => {
	const total = document.querySelector(TOTAL_SELECTOR);
	return total && document.querySelector(TOTAL_SELECTOR).innerText !== '$0.00';
};

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: no
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = TOTAL_SELECTOR;
		this.APPLIED_CODES_SELECTOR = '.coupon-applied-block li, .remove.promo-remove';
		this.PRODUCT_ITEM_SELECTOR = '.cartproductrow, .checkoutminisummary .summaryproduct';
		this.isStackable = true;

		const BASE_URL = 'https://www.yliving.com/on/demandware.store/Sites-YLiving-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCLI`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COOnePage-UpdateSummary`;

		this.CART_PAGE_URL = window.location.href;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'coupon';

		this.removeQueryParams = this.applyQueryParams;

		this.APPLIED_CODE_REG = /Code Applied:(\s|\s\()(\w+)/i;
	}

	getExistingCodes() {
		const checkoutInput = document.querySelector(CHECKOUT_INPUT_SELECTOR);
		let codes = [];
		if (checkoutInput) {
			codes = new Set(
				[...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
					.filter(Boolean)
					.map(el => el.getAttribute('id')),
			);
		} else {
			codes = new Set(
				[...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
					.filter(el => el.innerText.match(this.APPLIED_CODE_REG))
					.map(el => el.innerText.match(this.APPLIED_CODE_REG)[2]),
			);
		}

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}
};

export default {
	name: 'YLiving',
	domain: 'yliving.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isOnCartUrl() {
		return (
			document.querySelector(`[name="dwfrm_cart_couponCode"], ${CHECKOUT_INPUT_SELECTOR}`) &&
			isTotal() &&
			!isCheckout()
		);
	},
	// isCartEmpty() is not necessarily
};
