import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { format, DOM, completionActions } from '../../../utils';

const { toFloat, toInt } = format;
const { extractData, setValue } = DOM;

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can't be replaced with another valid code;
- can apply multiple codes (stackable);
- code testing may be run on 'Cart' and 'Checkout' page;
*/

const CART_FORM_SELECTOR = '#cart-promo-form';

const isOnCart = () => Boolean(document.querySelector(CART_FORM_SELECTOR));

const CART_PAGE_URL = 'https://www.johnhardy.com/cart';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total td:last-child';
		this.APPLIED_CODES_SELECTOR = '.discount .label';
		this.COUNT_ITEM_SELECTOR = '.mini-cart-quantity-label';
		this.INPUT_CODE_SELECTOR = '#dwfrm_billing_couponCode, #dwfrm_cart_couponCode';
		this.INPUT_DISABLED_SELECTOR = '.pt_checkout #secondary .summary .checkout-disable';
		this.APPLY_BTN_SELECTOR = '#add-coupon';

		this.CART_FORM_SELECTOR = CART_FORM_SELECTOR;

		this.isStackable = true;

		const BASE_URL = 'https://www.johnhardy.com/on/demandware.store/Sites-johnhardy-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-DeleteCouponJson`;
		this.UPDATE_REQUEST_URL = window.location.href;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.TOTAL_REG = /(&#36;)|[^0-9.-]+/g;
		this.EXISTING_CODE_REG = /code:\s+(\w+)/;
	}

	watchForCartUrl() {
		let previousState = false;
		setInterval(() => {
			const isInput = Boolean(document.querySelector(this.INPUT_CODE_SELECTOR));
			const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));
			const isInputDisabled = Boolean(document.querySelector(this.INPUT_DISABLED_SELECTOR));
			const currentState = isInput && isTotal && !isInputDisabled;
			if (currentState !== previousState) {
				bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
			}
			previousState = currentState;
		}, 500);
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(el => el.innerText.match(this.EXISTING_CODE_REG)[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	parseItemsCount(data) {
		return toInt(extractData(this.COUNT_ITEM_SELECTOR, data) || 0);
	}

	parseTotal(data = false) {
		return toFloat(extractData(this.TOTAL_SELECTOR, data).replace(this.TOTAL_REG, ''));
	}

	applyCodes(codes) {
		if (isOnCart()) {
			return Promise.resolve();
		}
		// code from stack codes, that will be set manually to refresh page with applied codes for the checkout page
		const lastCode = codes[codes.length - 1];
		return this.removeCode(lastCode);
	}

	completeExperience(codes) {
		return this._manuallyCodeApply(codes[0]);
	}

	_manuallyCodeApply(code) {
		const codeInput = document.querySelector(this.INPUT_CODE_SELECTOR);
		const codeBtn = document.querySelector(this.APPLY_BTN_SELECTOR);
		setValue(codeInput, code);
		codeBtn.click();
		return Promise.resolve();
	}
};

export default {
	name: 'John Hardy',
	domain: 'johnhardy.com',
	completionAction: () => (isOnCart() ? completionActions.revisit : completionActions.custom),
	stackable: true,
	isSinglePageApp: true,
	Driver,
	// isCartEmpty is not necessarily
	isOnCartUrl() {
		return false;
	},
};
