import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';

// Behavior description:
// we can apply only one code at the same time
// we can`t replace a valid code with invalid one
// we can`t remove applied code
// one valid code can replace invalid code
// requests for the cart are used on the checkout page
// discount is counted from the total that was set before run extension
// we support only cart, bacause on the checkout page all user data will be erased

const DOMAIN_URL = 'coldwatercreek.com';
const DOMAIN_CART_URL = `${DOMAIN_URL}/cart`;
const CHECKOUT_INPUT_CODE_FLD = '#dwfrm_billing_couponCode';
const CART_INPUT_CODE_FLD = '#dwfrm_cart_couponCode';
const INPUT_CODE_FLD = `${CART_INPUT_CODE_FLD}, ${CHECKOUT_INPUT_CODE_FLD}`;
const isOnCart = () => Boolean(document.querySelector(CART_INPUT_CODE_FLD));

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.IS_ON_CART = isOnCart();
		this.TOTAL_SELECTOR = '.order-total .order-totals-value';
		this.APPLIED_CODES_REGEX = /order_coupon_promo_code": "(\w*)/;
		this.isStackable = false;

		this.CART_PAGE_URL = `https://www.${DOMAIN_CART_URL}`;
		const BASE_URL = `https://www.${DOMAIN_URL}/on/demandware.store/Sites-coldwater_us-Site/default`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = this.IS_ON_CART ? this.CART_PAGE_URL : `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.CHECKOUT_APPLY_BTN_SELECTOR = '#add-coupon';
		this.CHECKOUT_CODE_MESSAGE_SELECTOR = '.form-indent .redemption-code, .form-indent .error';

		this.applyQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		/* On the checkout page codes are applied without refreshing the page, so if a user applies codes 2
		times manually without refreshing the page before extension work then the first code will be extracted.
		From UpdatedCart we can get it right. */

		return this.getCartPage().then(responseHTML => {
			const codesRegex = responseHTML.match(this.APPLIED_CODES_REGEX);
			const codes = codesRegex ? [codesRegex[1]] : [];
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCodes() {
		return Promise.resolve();
	}

	applyCodes(codes) {
		// remove messages from previous applied codes
		[...document.querySelectorAll(this.CHECKOUT_CODE_MESSAGE_SELECTOR)].map(element => element.remove());
		return this.applyCode(codes[0]);
	}

	// No possibility to remove a code
	removeCode() {
		return Promise.resolve();
	}
};

export default {
	name: 'Coldwater Creek',
	domain: 'coldwatercreek.com',
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl(href) {
		return href.includes(DOMAIN_CART_URL) && Boolean(document.querySelector(INPUT_CODE_FLD));
	},
};
