import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';

/* BEHAVIOUR
 * Can apply more than one code at the same time (stackable)
 * Can remove on cart page
 * Can getExistingCodes
 */

const DOMAIN_URL = 'https://www.pacificcoast.com';
const CART_URL = `${DOMAIN_URL}/cart`;
const BASE_URL = `${DOMAIN_URL}/on/demandware.store/Sites-PacificCoast-Site/default`;
const WISH_LIST_URL = `${DOMAIN_URL}/on/demandware.store/Sites-PacificCoast-Site/default/Cart-AddToWishlist`;
const CART_INPUT_SELECTOR = '#dwfrm_cart_couponCode';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemovePromoCode`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');

		this.CART_PAGE_URL = CART_URL;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'Pacific Coast Feather',
	domain: 'pacificcoast.com',
	completionAction: completionActions.revisit,
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			document.querySelector(CART_INPUT_SELECTOR) && (href.startsWith(CART_URL) || href.startsWith(WISH_LIST_URL))
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
