import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

/*
Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
Optional:
•    Is driver SPA and add the condition to show popup: yes
•    Gather codes on site pages: yes
•    Can apply codes on cart and checkout
=======Special case=======
Sometimes after deleting codes merchant can relocate us to "/express" page. 
We don't support this page because of strange behavior on it.
 */

const API_REQUEST_URL = 'https://www.vineyardvines.com/';
const CART_PAGE_URL = `${API_REQUEST_URL}cart`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = 'dd.ordersummary_cost_total';
		this.APPLIED_CODES_SELECTOR = '.ordersummary .ordersummary_promotion_title';
		this.INPUT_FIELD_SELECTOR = '#promo-code, #billingCoupon';
		this.isStackable = true;

		const BASE_URL = `${API_REQUEST_URL}on/demandware.store/Sites-Vineyard-Vines-Site/en_US`;
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Cart-OrderSummaryVariation`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
		this.applyQueryParams.set('format', 'ajax');
		this.APPLIED_CODE_REGEX = /promotion (.*?) applied/i;

		this.PROMO_SELECTOR = '#TT_main .feature_title span';
		this.PROMO_REGEX = /code: (\w+)/i;

		this.CART_EMPTY_SELECTOR = '#bag .bag_emptymsg';
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl = href.includes('/cart') || href.includes('/billinginfo');
		const isInputField = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		return isRightUrl && isInputField && !isCartEmpty;
	}

	getMerchantCodes() {
		return fetch(API_REQUEST_URL)
			.then(res => res.text())
			.then(html => {
				return [
					...new Set(
						[...extractElement(this.PROMO_SELECTOR, html, true)]
							.map(item => item.textContent.match(this.PROMO_REGEX))
							.filter(Boolean)
							.map(el => el[1]),
					),
				];
			});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(el => el.textContent.match(this.APPLIED_CODE_REGEX)[1])
			.filter(Boolean);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = this._findValueByLabel(response, 'Shipping');
			const tax = this._findValueByLabel(response, 'Sales Tax');
			const shippingDiscount = this._findValueByLabel(response, 'Shipping Discount');

			return this.parseTotal(response) - shipping + shippingDiscount - tax;
		});
	}

	_findValueByLabel(response, label) {
		const element = [...extractElement('dt', response, true)].find(el => el.textContent.includes(label));
		return element ? elemToFloat(element.nextElementSibling) : 0;
	}

	applyCode(code) {
		this.applyQueryParams.set(this.APPLY_CODE_PARAM_NAME, code);

		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, null, this.applyQueryParams);
	}

	removeCode(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		}

		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, null, this.removeQueryParams);
	}
};

export default {
	name: 'vineyard vines',
	domain: 'vineyardvines.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
