import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';

const CART_PAGE_URL = 'https://www.dwr.com/cart';
const TOTAL_SELECTOR = '.order-total .order-value';
const INPUT_CODE_BLOCK_SELECTOR = '.have-a-promo-code';
const BASE_URL = 'https://www.dwr.com';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
	- Cart page
	- Checkout page

+ Is driver SPA no
+ Gather codes on site pages: no
*/

const URLS = [
	`${BASE_URL}/billing`,
	`${BASE_URL}/shipping`,
	`${BASE_URL}/checkout`,
	`${BASE_URL}/on/demandware.store/Sites-dwr-Site`,
	CART_PAGE_URL,
];

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = TOTAL_SELECTOR;
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.DISCOUNT_SELECTOR = '.order-discount.rowcoupons';

		this.APPLY_CODE_URL = 'https://www.dwr.com/on/demandware.store/Sites-dwr-Site/en_US/Cart-AddCouponJson';
		this.REMOVE_CODE_URL = this.APPLY_CODE_URL;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		// Looks like we may recieve that code isn't valid, but actually it is
		// so safer to skip this check
		this.APPLY_RESPONSE_KEY = null;
		this.APPLIED_CODES_REGEX = /couponCode=(\w*)/;
		this.APPLIED_CODE_SELECTOR = '.country a';
		this.CHECKOUT_BTN_SELECTOR = '#button-checkout-cart';

		// seems like it's better not to check url here since there are some cases
		// where url can change after some activity on the page
		this.isOnCartPage = () => document.querySelector(this.CHECKOUT_BTN_SELECTOR);
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
				.map(element => element.href.match(this.APPLIED_CODES_REGEX))
				.filter(Boolean)
				.map(element => element[1])
				.filter(Boolean),
		);

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes() {
		return this._removeCodes();
	}

	beforeCheckCodes(originalPrice) {
		// Sometimes after code testing, applied code may disappear from the page. In such case code
		// retesting won't show any results. This method was added in order to get correct original price.
		const isDiscountElement = (document.querySelector(this.DISCOUNT_SELECTOR) || {}).offsetParent;

		if (!originalPrice && isDiscountElement) {
			return this._removeCodes();
		}

		return Promise.resolve(originalPrice);
	}

	beforeApplyCodes() {
		// once an invalid code is applied on the cart page, the url will change and as a result
		// code won't be applied after the code testing

		if (this.isOnCartPage()) {
			window.history.replaceState(window.history.state, window.document.title, CART_PAGE_URL);
		}
	}

	applyCodes(codes) {
		return this.applyCode(codes[0]);
	}

	removeCode() {
		return this.applyCode('fake code');
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, responseHTML)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, responseHTML)) || 0;
			const total = this.parseTotal(responseHTML);

			return total - tax - shipping;
		});
	}

	_removeCodes() {
		return this.removeCode()
			.then(() => this.getCartPage())
			.then(resonseHTML => this.parseTotal(resonseHTML));
	}
};

export default {
	name: 'Design Within Reach',
	domain: 'dwr.com',
	Driver,
	isOnCartUrl(href) {
		return URLS.some(url => href.startsWith(url) && document.querySelector(INPUT_CODE_BLOCK_SELECTOR));
	},
};
