import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { sessionTimer, backgroundMessaging } from '../../lib';
import { completionActions, log, DOM, format, browserUtils } from '../../../utils';

const { getCookie } = browserUtils;
const { extractData, extractElement } = DOM;
const { toFloat, elemToFloat } = format;

/** Behavior description:
+ Existing code: yes
+ Remove code: not always, can remove code if couponToken presents in cookie
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: yes (checkout)
+ Pages, where extension works:
	* cart
+ Is driver SPA yes
    * cookie is enabled
	* is not empty
	* header text
	* is not maxCodeTesting
+ Gather codes on site pages: no
+ Special case about the driver works
	* maxCoupons 5
	* sessionTimer yes/15min
	* disable codes testing after 2 runs for 60 minutes
We do not show a popup for 60 minutes, and we also have a limit on maxCoupons of 5, due to blocking
Also, if the problem persists after 60 minutes, we mark the codes as invalid
Remove codes manually because on remove code request is token which is not present anywhere
But if manually click on remove code button couponToken appears in cookie
 * */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-total .total-value';
		this.TAX_SELECTOR = '.order-total .tax .total-value';
		this.SHIPPING_SELECTOR = '.order-total .shipping .total-value';
		this.APPLIED_CODES_SELECTOR = '.promo .action-remove-promo';
		this.PRODUCT_ITEM_SELECTOR = '.product-line.product';
		this.TITLE_SELECTOR = '.cart-title';
		this.EMPTY_SELECTOR = '.h1.cart-empty';
		this.isStackable = true;
		this.CODE_REMOVE_BTN_SELECTOR = '.action-remove-promo';

		this.BASE_URL = 'https://www.newbalance.com/on/demandware.store/Sites-newbalance_us2-Site/en_US';
		this.APPLY_CODE_URL = `${this.BASE_URL}/COBilling-ApplyCoupon`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/COBilling-RemovePromoCode`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-UpdateSummary`;
		this.CART_PAGE_URL = `${this.BASE_URL}/Cart-Show`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'promoCode';
		this.isInvalid = false;

		this.DISABLE_TIME_MS = 60 * 60 * 1000; // 60 minutes
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(this.DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(this.DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		const { cookieEnabled } = window.navigator;
		const isEmpty = document.querySelector(this.EMPTY_SELECTOR);
		const headerElement = document.querySelector(this.TITLE_SELECTOR);
		const isHeader = headerElement && headerElement.innerText.trim().match(/my (?:cart|bag)/i);
		return isNotMaxCodeTesting && !isEmpty && isHeader && cookieEnabled;
	}

	beforeTestCodes() {
		return this.applyCode('fake-code').then(responseJSON => {
			this.isInvalid = Object.keys(responseJSON).length === 0;
			return Promise.resolve();
		});
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
				.filter(Boolean)
				.map(el => el.getAttribute('data-code')),
		);
		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCodeManually(code))
			.then(() => this.getCartPage())
			.then(response => this.parseTotal(response));
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: true,
				};

				if (
					applyCodeResponse.redemptionErrorMsg ||
					this.isInvalid ||
					Object.keys(applyCodeResponse).length === 0
				) {
					// Code was not applied
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;
					result.isValid = true;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(this.DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(this.DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}

	getPreTaxShippingTotal() {
		return this.getCartPage().then(responseHTML => {
			const total = toFloat(extractData(this.TOTAL_SELECTOR, responseHTML));

			// shipping element not always presents on the page
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const shippingData = shippingEl ? elemToFloat(shippingEl) : 0;
			// Might be "Please select a shipping method"
			const shipping = Number.isNaN(shippingData) ? 0 : shippingData;

			const taxData = toFloat(extractData(this.TAX_SELECTOR, responseHTML));
			// Tax will be estimated after visiting checkout page
			const tax = Number.isNaN(taxData) ? 0 : taxData;

			return total - shipping - tax;
		});
	}

	_removeCodeManually() {
		document.querySelector(this.CODE_REMOVE_BTN_SELECTOR).click();
		return Promise.resolve();
	}

	removeCode(code) {
		if (this.REMOVE_CODE_PARAM_NAME) {
			this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
			// if manually click on remove code button couponToken appears in cookie
			this.removeQueryParams.set('couponToken', getCookie('couponToken'));
		}
		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}
};

export default {
	name: 'New Balance',
	domain: 'newbalance.com',
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 5,
	Driver,
	completionAction: completionActions.revisit,
};
