import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';

const CART_PAGE_URL = 'https://www.avenue.com/en_US/cart';

const { toFloat } = format;
const { extractElement } = DOM;
/*
Driver behavior:
• Existing code: yes
• Remove code: yes
• Code retesting: yes
• Stackable: no
• Free gift support: no
• isValid implemented: yes
• Show message about free shipping: no
• Cart, checkout payment section
• Driver is SPA: yes, on cart or checkout pages if it's not empty
• Gather merchant codes: yes

driver similar to loralette.com
we don't need to set completionAction to revisit for FF.
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-totals-value';
		this.APPLIED_COUPON_SELECTOR = '.coupon-discount-success, .coupon-name';
		this.HAVE_PROMO_CODE_BUTTON_SELECTOR = '#have_promo_code, .cart-coupon-code-input';
		this.COUNT_EL_SELECTOR = '.mini-cart-amount';
		this.EXTRA_CART_ELEMENT_SELECTOR = '.pt_cart';
		this.MERCHANT_PROMO_SELECTOR = '.promo-info a';
		this.MERCHANT_PROMO_REGEX = /code\s*(\w+)/im;
		this.CODE_FORM_SELECTOR = '[name="dwfrm_cart"]';

		this.SHIPPING_SELECTOR = '.order-shipping .order-totals-value';
		this.TAX_SELECTOR = '.order-sales-tax .order-totals-value';

		this.ACTIVE_PAYMENT_SELECTOR = '.billing_block .section-box-header.active';

		const BASE_URL = 'https://www.avenue.com/on/demandware.store/Sites-Avenue-Site/en_US';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');
	}

	checkCondition() {
		const isOnHref = window.location.href.startsWith(this.CART_PAGE_URL);
		const cartForm = document.querySelector(this.CODE_FORM_SELECTOR);
		const countEl = document.querySelector(this.COUNT_EL_SELECTOR);
		const isPayment = document.querySelector(this.ACTIVE_PAYMENT_SELECTOR);
		const isCartEmpty = countEl && countEl.innerText === '0';
		return ((isOnHref && cartForm) || isPayment) && !isCartEmpty;
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_PROMO_SELECTOR)]
			.map(el => el.innerText.match(this.MERCHANT_PROMO_REGEX))
			.filter(arr => arr && arr.length > 0)
			.map(el => el[1]);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_COUPON_SELECTOR)].map(
			element => element.textContent.trim().split(/\s/)[0],
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this.removeCode(codes[0])
			.then(() => this.getOrderSummary())
			.then(response => this.parseTotal(response));
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	applyCodes(codes) {
		if (document.querySelector(this.EXTRA_CART_ELEMENT_SELECTOR)) {
			// We should remove query parameters, to prevent form submitting again on cart page only
			// `cannotRefresh` parameter can't be used
			// because on checkout page `POST` request should be used for page reloading
			window.history.replaceState(
				window.history.state,
				window.document.title,
				window.location.href.split('?')[0],
			);
		}
		return this.applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shipping = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const taxes = extractElement(this.TAX_SELECTOR, responseHTML);
			// we have `free` for shipping when it is not calculated
			const calculatedShipping = shipping && shipping.textContent.includes('FREE') ? 0 : shipping;
			return (
				total - toFloat((taxes || {}).textContent || 0) - toFloat((calculatedShipping || {}).textContent || 0)
			);
		});
	}
};

export default {
	name: 'Avenue',
	domain: 'avenue.com',
	Driver,
	isSinglePageApp: true,
};
