import DemandwareAJAXDriver from './_base';

const { DOM, format } = require('../../../utils');

const { extractElement } = DOM;
const { elemToFloat } = format;

/** Behavior description:
 * - existing code: yes
 * - remove code: yes
 * - stackable: yes
 * - free gift support: yes
 * - 'isValid' implemented: yes
 * - Gather codes on site pages: yes
 * - replace code: can't check, currently there is only one valid code
 * - we run testing on the checkout and cart pages. They will be reloaded at the end.
 * - after testing on the checkout payment data will be erased.
 * - Also, on the FF we have modal before reloading page. Because with completion action `revisit` we can't appply codes at the end of the testing
 * */

const BASE_URL = 'https://www.madewell.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const BASE_URL_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-madewellUS-Site/default`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.PRODUCT_ITEM_SELECTOR = '.mini-cart-product, .checkout-mini-cart .mini-cart-product';
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_URL_REQUEST_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL_REQUEST_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL_REQUEST_URL}/COBilling-UpdateSummary`;
		this.PROMO_CODES_URL = `${BASE_URL}/promo-codes-discounts-coupons.html`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};

		this.CODE_INPUT_SELECTOR = '#dwfrm_billing_couponCode, #dwfrm_cart_couponCode';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.EMPTY_CART_SELECTOR = '.cart-empty';

		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
		this.CODE_CONTAINER_SELECTOR = '.slick-track .slick-slide';
		this.CODE_REGEX = /CODE (\w+)./;

		this.MERCHANT_CODES_SELECTOR = '.sections h3';
		this.MERCHANT_CODE_REGEX = /code (\w+)/i;
	}

	checkCondition() {
		const href = window.location.href.toLowerCase();
		const isOnCart = href.startsWith(CART_PAGE_URL) && document.querySelector(this.CART_FORM_SELECTOR);
		const isOnRightUrl = isOnCart || href.includes('/coshipping-singleshipping') || href.includes('/billing');
		const isEmptyCartEl = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isCodeInputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		const currentState = isOnRightUrl && !isEmptyCartEl && isCodeInputEl;
		return currentState;
	}

	async getMerchantCodes() {
		const merchantCodes = [];

		await this._getPromoCodesPage().then(responseHTML => {
			merchantCodes.push(
				...[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
					.map(el => el.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(el => el[1]),
			);
		});

		await this.getCartPage().then(responseHTML => {
			const merchantCode = responseHTML.match(this.CODE_REGEX);
			if (merchantCode) {
				merchantCodes.push(...[merchantCode[1]]);
			}
		});

		return [...new Set(merchantCodes)];
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_getPromoCodesPage() {
		return fetch(this.PROMO_CODES_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Madewell',
	domain: 'madewell.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
