import DemandwareAJAXDriver from './_base';

/* BEHAVIOUR
+ Remove/apply code methods work with POST
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ Codes can be replaced by another code: no
+ Is driver SPA: yes (Cart/Checkout)
 */

const BASE_URL = 'https://www.petsmart.com';
const CART_PAGE_URL = `${BASE_URL}/cart`;
const CHECKOUT_PAGE_URL = `${BASE_URL}/cart-checkout`;
const BASE_REQUEST_URL = 'https://www.petsmart.com/on/demandware.store/Sites-PetSmart-Site/default';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.estimated-total .value, .mini-cart-subtotals .value, .order-total .order-value';
		this.isStackable = true;
		this.CODE_INPUT_SELECTOR = '.promocode, .cart-promo-code';

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/CartController-ApplyPromo?instart_disable_injection=true`;
		this.REMOVE_CODE_URL = `${BASE_REQUEST_URL}/CartController-RemovePromo?instart_disable_injection=true`;
		this.UPDATE_REQUEST_URL = `${BASE_REQUEST_URL}/Cart-MiniCart`;
		this.CART_DATA_URL = `${BASE_REQUEST_URL}/CartController-CartData?instart_disable_injection=true`;
		this.updateQueryParams = new URLSearchParams({
			instart_disable_injection: true,
		});

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'promocode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
			'content-type': 'application/json;charset=UTF-8',
		};
		this.REMOVE_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
			'content-type': 'application/json;charset=UTF-8',
		};

		this.UPDATE_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};

		this.EMPTY_CART_SELECTOR = '.mini-cart-empty';
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl = href.startsWith(CART_PAGE_URL) || href.startsWith(CHECKOUT_PAGE_URL);
		const isEmptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		return isInput && !isEmptyCart && isRightUrl;
	}

	getExistingCodes() {
		return this._getCartData().then(responseJSON => {
			const codes = responseJSON.promoCodes.codes.map(el => el.promoCodeID);
			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	applyCode(code) {
		const body = JSON.stringify({ promocode: code });
		return this._sendRequestPOST(this.APPLY_CODE_URL, this.APPLY_REQUEST_HEADERS, null, body);
	}

	removeCode(code) {
		const body = JSON.stringify({ promocode: code });
		return this._sendRequestPOST(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, null, body);
	}

	_getCartData() {
		return fetch(this.CART_DATA_URL, {
			method: 'POST',
			headers: this.APPLY_REQUEST_HEADERS,
			body: JSON.stringify({ errorMessages: [] }),
		}).then(response => response.json());
	}
};

export default {
	name: 'PetSmart',
	domain: 'petsmart.com',
	stackable: true,
	isSinglePageApp: true,
	Driver,
};
