import DemandwareAJAXDriver from './_base';

const DOMAIN_URL = 'https://www.exofficio.com';
const BASE_URL = `${DOMAIN_URL}/on/demandware.store`;
const CART_PAGE_URL = `${DOMAIN_URL}/checkout`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.isStackable = true;

		this.APPLY_CODE_URL = `${BASE_URL}/Sites-ExOfficio_US-Site/en_US/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Sites-ExOfficio_US-Site/en_US/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Sites-ExOfficio_US-Site/en_US/OnePageCheckout-UpdateSummary`;

		this.CART_FORM_SELECTOR = '#next';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.CODE_VALUE_SELECTOR = '[data-couponcode]';
		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.CODE_VALUE_SELECTOR)].map(codeElement =>
			codeElement.getAttribute('data-couponcode'),
		);

		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'ExOfficio',
	domain: 'exofficio.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith(BASE_URL) ||
			href.startsWith(CART_PAGE_URL) ||
			(href.startsWith(DOMAIN_URL) && href.includes('cart'))
		);
	},
};
