import DemandwareAJAXDriver from './_base';
import { sessionTimer } from '../../lib';
import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

const CART_PAGE_URL = 'https://www.elfcosmetics.com/shopping-bag';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: no
•    ‘isValid’ implemented: yes
•    Manually apply code: no
•    sessionTimer: 15min.
Optional:
•    Show message about free shipping on the cart page (without discount info):no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.elfcosmetics.com/on/demandware.store/Sites-elf-us-Site/en_US';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-UpdateSummary`;
		this.CART_PAGE_URL = CART_PAGE_URL;

		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.CODES_ELEMENTS_SELECTOR = '.cartcoupon';
		this.CODE_VALUE_SELECTOR = '.value';

		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.applyQueryParams.set('format', 'ajax');
		this.APPLY_RESPONSE_KEY = null;

		this.isStackable = true;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response));
			const calculatedShipping = shipping || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			const calculatedTax = tax || 0;
			return this.parseTotal(response) - calculatedShipping - calculatedTax;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	_isOnCartPage() {
		const { href } = window.location;
		return href.startsWith(CART_PAGE_URL) || href.includes('Cart-SubmitForm');
	}
};

const _continueUrl = () => {
	const { href } = window.location;
	if (href.includes('shopping-bag') || href.includes('Cart-SubmitForm')) {
		return CART_PAGE_URL;
	}
	return 'https://www.elfcosmetics.com/billing';
};

export default {
	name: 'Elf Cosmetics',
	domain: 'elfcosmetics.com',
	stackable: true,
	Driver,
	cartContinueUrl: _continueUrl(),
	isOnCartUrl(href) {
		return href.startsWith(CART_PAGE_URL) || href.includes('billing') || href.includes('/Cart-SubmitForm');
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty')) || Boolean(document.querySelector('.error-page-message'));
	},
};
