import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { sessionTimer } from '../../lib';
import { log, DOM, format, sendXhr } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/* Behavior description:
	* Existing code: yes
	* Remove code: yes
	* Replace code:  no
	* Stackable:  no
	* Free gift:  no
	* Free shipping:  no
	* ‘isValid’ implemented: yes
	* SessionTimer yes (15min)
	* Pages, where extension works: cart, billing
	* Is SPA: yes
====Special cases====
 	* Also if the user selected 'Pick Up In Store' we have two another pages where we can apply the code,
	* so in the first checkout step after the cart, we can apply code only after revisiting page.
	* Next step we can't support because we need to save user data.

*/

const CART_PAGE_URL = 'https://www.partycity.com/cart';
const SHIPPING_PAGE_URL = 'https://www.partycity.com/shipping';
const BILLING_PAGE_URL = 'https://www.partycity.com/billing';
const isOnCart =
	window.location.href.startsWith(CART_PAGE_URL) || window.location.href.toLowerCase().endsWith('/cart-submitform');
const isOnBilling = () =>
	!!document.querySelector('#dwfrm_billing') && !!document.querySelector('.billing-address-section.hide');

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, .bill-coupn input';
		this.APPLY_BUTTON_SELECTOR = '#add-coupon';
		this.REMOVE_BUTTON_SELECTOR = '#remove-coupon';
		this.TOTAL_SELECTOR = '.order-total span:last-child';
		this.CART_EMPTY_SELECTOR = '.cart-empty';
		this.CHECKOUT_FORM_SELECTOR = '#dwfrm_singleshipping_shippingAddress';
		this.BILLING_SECTION_SELECTOR = '.billing-address-section';
		this.HEADING_ELEM_SELECTOR = '.step-1.active';
		this.APPLIED_CODES_SELECTOR = 'script[type="text/javascript"]';
		this.APPLIED_CODES_REGEX = /"coupon_codes":\s+?\[\s+?"(\w+)"/i;

		this.SHIPPING_SELECTOR = '.order-shipping span:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.promo-shipping>span:last-child';
		this.TAX_SELECTOR = '.order-sales-tax span:last-child';

		this.BASE_URL = 'https://www.partycity.com/on/demandware.store/Sites-partycity_us2-Site/en_US';
		this.APPLY_CODE_URL = `${this.BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${this.BASE_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${this.BASE_URL}/COBilling-UpdateSummary`;

		this.REMOVE_CODE_PARAM_NAME = 'couponCode';

		this.APPLY_REQUEST_HEADERS = {
			'x-requested-with': 'XMLHttpRequest',
		};
		this.REMOVE_REQUEST_HEADERS = this.APPLY_REQUEST_HEADERS;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
		this.updateQueryParams.set('format', 'ajax');

		this.isStackable = false;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		const billingSection = document.querySelector(this.BILLING_SECTION_SELECTOR);
		if (billingSection) {
			const isBillingSectionFilled = billingSection.classList.contains('hide');
			if (!isBillingSectionFilled) {
				bus.$emit('cart-inactive');
			}
		}

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isRightUrl =
			isOnCart ||
			href.toLowerCase().startsWith(SHIPPING_PAGE_URL) ||
			href.toLowerCase().startsWith(BILLING_PAGE_URL) ||
			href.toLowerCase().endsWith('/cart-submitform');
		const isCartEmpty = document.querySelector(this.CART_EMPTY_SELECTOR);
		const isInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const activeStep = document.querySelector(this.HEADING_ELEM_SELECTOR);
		const currentState = isRightUrl && !isCartEmpty && isInput && !activeStep;
		return currentState;
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)]
			.map(el => el.textContent.match(this.APPLIED_CODES_REGEX))
			.filter(Boolean)
			.map(matched => matched[1]);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
					isValid: false,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) {
					// Code was not applied
					return bus.$emit('code-checked', result);
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;
					result.isValid = true;
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	beforeApplyCodes() {
		window.history.pushState(
			window.history.state,
			window.document.title,
			isOnBilling() ? BILLING_PAGE_URL : window.location.href,
		);
		return Promise.resolve();
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shippingDiscountElem = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscountValue = shippingDiscountElem ? elemToFloat(shippingDiscountElem) : 0;
			const shipping = extractElement(this.SHIPPING_SELECTOR, response);
			const shippingValue = shipping ? elemToFloat(shipping) - shippingDiscountValue : 0;
			const tax = extractElement(this.TAX_SELECTOR, response);
			const taxValue = tax ? elemToFloat(tax) : 0;
			return this.parseTotal(response) - shippingValue - taxValue;
		});
	}

	_isOnCartPage() {
		return (
			window.location.href.startsWith(this.CART_PAGE_URL) ||
			window.location.href.toLowerCase().endsWith('/cart-submitform')
		);
	}

	_sendRequest(method, requestURL, headers, queryParams, bodyData) {
		const finalRequestURL =
			queryParams && queryParams.toString() ? `${requestURL}?${queryParams.toString()}` : requestURL;

		return sendXhr(finalRequestURL, {
			method,
			credentials: 'include',
			headers,
			body: bodyData,
		})
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					return JSON.parse(response);
				} catch (e) {
					return response;
				}
			});
	}
};

export default {
	name: 'Party City',
	domain: 'partycity.com',
	Driver,
	cartContinueUrl: isOnCart ? CART_PAGE_URL : window.location.href,
	isSinglePageApp: true,
};
