import DemandwareAJAXDriver from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(Payment step)
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Credentials: joseph@gmail.com, 12345qwert
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';
		this.isStackable = false;
		this.INPUT_BILLING_CODE = '#dwfrm_billing_couponCode';
		this.EMPTY_CART_SELECTOR = '.cart-empty';
		this.CART_INPUT_SELECTOR = '#dwfrm_cart_couponCode';

		const BASE_URL = 'https://www.kingsizedirect.com/on/demandware.store/Sites-kingsize-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary?stepCount=2`;

		this.CART_PAGE_URL = 'https://www.kingsizedirect.com/cart';
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;

		this.TAX_VALUE_SELECTOR = '.order-sales-tax .value';
		this.SHIPPING_VALUE_SELECTOR = '.order-shipping .value';
	}

	checkCondition() {
		const isCodeInput = document.querySelector(`${this.INPUT_BILLING_CODE}, ${this.CART_INPUT_SELECTOR}`);
		const isCartEmpty = Boolean(document.querySelector(this.EMPTY_CART_SELECTOR));
		const isTotal = Boolean(document.querySelector(this.TOTAL_SELECTOR));

		const currentState = isCodeInput && isTotal && !isCartEmpty;
		return currentState;
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	getPreTaxShippingTotal() {
		// Tax and shipping are included on cart and checkout pages
		return this.getCartPage().then(responseHTML => {
			const taxValue = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML));
			const shippingValue = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML));
			const isTaxValue = taxValue || 0;
			const isShippingValue = shippingValue || 0;
			return this.parseTotal(responseHTML) - isTaxValue - isShippingValue;
		});
	}
};

export default {
	name: 'KingSize Direct',
	domain: 'kingsizedirect.com',
	isSinglePageApp: true,
	Driver,
};
