import DemandwareAJAXDriver from './_base';
import { extractElement } from '../../../utils/DOM';
import { elemToFloat } from '../../../utils/format';

const CART_PAGE_URL = 'https://www.bcbg.com/en/cart';
const IS_ON_CHECKOUT = window.location.href.toLowerCase().includes('checkout');

/* Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(where is code input)
+ Is driver SPA: yes
+ Gather codes on site pages: no
* */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-total-value';
		this.APPLIED_CODES_SELECTOR = '.appliedCoupon .cartcoupon .value';
		this.isStackable = false;

		const BASE_URL = 'https://www.bcbg.com/on/demandware.store/Sites-BCBG-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Checkout-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/Checkout-RefreshSummary`;

		this.CART_PAGE_URL = CART_PAGE_URL;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = this.APPLY_CODE_PARAM_NAME;

		this.CODE_INPUT_SELECTOR = '.toShow #dwfrm_billing_couponCode, #dwfrm_cart_couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.updateQueryParams.set('format', 'ajax');

		this.BILLING_SECTION = '#onepage_billing.active';
		this.SAME_TO_SHIIPING_SELECTOR = '#shipAsBill:checked';
		this.LOGIN_SECTION = '#onepage_account.active';
		this.ORDER_SUMMARY_ACTIVE = '#onepage_summary.active';
		this.SHIPPING_ACTIVE = '#onepage_shipping.active';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';
	}

	checkCondition() {
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const onCart = window.location.href.toLowerCase().includes('cart');
		const isBilling = document.querySelector(this.BILLING_SECTION);
		const isSameToShipping = document.querySelector(this.SAME_TO_SHIIPING_SELECTOR);
		const isLogin = document.querySelector(this.LOGIN_SECTION);
		const isOrderSummary = document.querySelector(this.ORDER_SUMMARY_ACTIVE);
		const isShipping = document.querySelector(this.SHIPPING_ACTIVE);
		const currentState =
			((IS_ON_CHECKOUT && ((isBilling && isSameToShipping) || isLogin || isOrderSummary || isShipping)) ||
				onCart) &&
			(codeInput && codeInput.offsetParent);
		return currentState;
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax;
		});
	}
};

export default {
	name: 'BCBG',
	domain: 'bcbg.com',
	isSinglePageApp: true,
	Driver,
};
