import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import bus from '../../bus';
import { DOM, completionActions, log } from '../../../utils';
import { sessionTimer, backgroundMessaging } from '../../lib';
import { extractData } from '../../../utils/DOM';
import { toFloat } from '../../../utils/format';

const { extractElement } = DOM;

/** Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: yes
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works:
    - Cart
+ Is driver SPA: yes
    - is on Cart page and there are promocode input field(or delete code button) and total price
	- cart isn't empty
+ Gather codes on site pages: yes
+ Special case about the driver works (if it needed).
    - maxCoupons: 10 - site blocks us
	- sessionTimer yes (20min)
	- doesn't allow retesting, because site blocks us
+ Credentials (joseph@gmail.com, 12345qwerty)
*/

const DOMAIN = 'surlatable.com';
const BASE_URL = 'https://www.surlatable.com';
const CART_URL = `${BASE_URL}/cart/shoppingCart.jsp`;

const isOnCart = () => window.location.href.startsWith(CART_URL);
const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.CART_TOTAL_SELECTOR = '.orderSumTotalPrice';
		this.APPLIED_CODES_SELECTOR = '#couponCodeToBeRemove_0';
		this.DYN_SESS_CONF_SELECTOR = 'input[name="_dynSessConf"]';
		this.DYNCHARSET_SELECTOR = 'input[name="_dyncharset"]';

		this.TOTAL_SELECTOR = this.CART_TOTAL_SELECTOR;

		this.MANAGE_CODE_URL = 'https://www.surlatable.com/checkout/opc/singlePageSignIn.jsp';
		this.APPLY_CODE_URL = this.MANAGE_CODE_URL;
		this.REMOVE_CODE_URL = this.MANAGE_CODE_URL;
		this.UPDATE_REQUEST_URL = CART_URL;
		this.APPLY_CODE_PARAM_NAME = '/atg/commerce/promotion/CouponFormHandler.couponClaimCode';
		this.REMOVE_CODE_PARAM_NAME = '/atg/commerce/order/ShoppingCartModifier.couponCodeToBeRemove';

		this.REMOVE_CODE_BUTTON_SELECTOR = '.remove, #removeCoupon';
		this.INPUT_CODE_SELECTOR = '#couponCode';
		this.CODE_DESCRIPTION_SELECTOR = '#helpBlockPromo, #promocodespay';
		this.EMPTY_CART_SELECTOR = '.ppError + div';
		this.ERROR_MSG_SELECTOR = '.Zebra_Tooltip';
		this.FREE_SHIPPING_REGEX = /free shipping/g;
		this.EMPTY_CART_REGEX = /cart is empty/g;

		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes
		this.MERCHANT_CODES_SELECTOR = '#freeship-banner-evergreen span';
		this.FEE_SELECTOR = '.orderSumHandlingPrice';
		this.SHIPPING_SELECTOR = '.orderSumShipPrice';
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.MERCHANT_CODES_SELECTOR)].map(elem => elem.innerText).filter(Boolean);
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 20,
		});
	}

	async checkCondition() {
		let isDisabledCodeTesting = false;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			isDisabledCodeTesting = Date.now() - driverStorage.lastTestTimestamp < this.DISABLE_TIME_MS;
		}

		const isCodeInputElement = document.querySelector(this.INPUT_CODE_SELECTOR);
		const isRemoveCodeElement = document.querySelector(this.REMOVE_CODE_BUTTON_SELECTOR);
		const totalElement = document.querySelector(this.TOTAL_SELECTOR);
		const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isEmptyCart = emptyCartElement
			? emptyCartElement.textContent.toLowerCase().match(this.EMPTY_CART_REGEX)
			: false;
		const currentState =
			isOnCart() &&
			totalElement &&
			(!isDisabledCodeTesting && !isEmptyCart && (isCodeInputElement || isRemoveCodeElement));
		return currentState;
	}

	beforeTestCodes() {
		const dynSessConfElement = document.querySelector(this.DYN_SESS_CONF_SELECTOR);
		const dynSessConf = dynSessConfElement ? dynSessConfElement.value : null;

		const dyncharsetElement = document.querySelector(this.DYNCHARSET_SELECTOR);
		const dyncharset = dyncharsetElement ? dyncharsetElement.value : 'UTF-8';

		this.applyQueryParams.set('from', 'applyPromotion');
		this.applyQueryParams.set('_dyncharset', dyncharset);
		this.applyQueryParams.set('_dynSessConf', dynSessConf);
		this.applyQueryParams.set('_D:/atg/commerce/promotion/CouponFormHandler.couponClaimCode', '');
		this.applyQueryParams.set('_DARGS', '/checkout/opc/includes/promoSum.jsp.paymentAddPromoCodeForm');

		this.removeQueryParams.set('from', 'removePromotion');
		this.removeQueryParams.set('_dyncharset', dyncharset);
		this.removeQueryParams.set('_dynSessConf', dynSessConf);
		this.removeQueryParams.set('_D:/atg/commerce/order/ShoppingCartModifier.couponCodeToBeRemove', '');
		this.removeQueryParams.set('_DARGS', '/checkout/opc/includes/promoSum.jsp.paymentFormPromoCodesId_0');
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR)]
			.filter(Boolean)
			.map(codeElement => codeElement.value);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this.removeCode(codes[0])
			.then(() => this.getOrderSummary())
			.then(response => this.parseTotal(response));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
				freeShipping: false,
			};
			return this.applyCode(code)
				.then(() => this.getOrderSummary())
				.then(response => {
					if (this._isCodeInvalid(response)) {
						result.isValid = false;
						return bus.$emit('code-checked', result);
					}
					result.finalPrice = this.parseTotal(response);
					result.freeShipping = this._checkFreeShipping(response);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
		});
	}

	beforeApplyCodes() {
		backgroundMessaging.setDriverStorage(DOMAIN, {
			lastTestTimestamp: Date.now(),
		});
	}

	async getPreTaxShippingTotal() {
		const cartHTML = await this.getOrderSummary();
		const total = this.parseTotal(cartHTML);
		const shipping = extractElement(this.SHIPPING_SELECTOR, cartHTML)
			? extractData(this.SHIPPING_SELECTOR, cartHTML).trim()
			: 0;
		const fee = extractElement(this.FEE_SELECTOR, cartHTML) ? extractData(this.FEE_SELECTOR, cartHTML).trim() : 0;

		return total - (shipping.toLowerCase() !== 'free' ? toFloat(shipping) : 0) - toFloat(fee);
	}

	_isCodeInvalid(response) {
		const inputElement = extractElement(this.INPUT_CODE_SELECTOR, response);
		const isInputElement = inputElement && inputElement.type !== 'hidden';
		const errorElement = extractElement(this.ERROR_MSG_SELECTOR, response);
		const isErorrElement = errorElement
			? [...extractElement(this.ERROR_MSG_SELECTOR, response, true)].some(el => el.style.display === 'block')
			: false;
		return !response || isInputElement || isErorrElement;
	}

	_checkFreeShipping(response) {
		return Boolean(
			extractElement(this.CODE_DESCRIPTION_SELECTOR, response)
				.textContent.toLowerCase()
				.match(this.FREE_SHIPPING_REGEX),
		);
	}
};

export default {
	name: 'Sur la Table',
	domain: 'surlatable.com',
	Driver,
	maxCoupons: 10, // due to site blocks us
	isSinglePageApp: true,
	cartContinueUrl: CART_URL, // after removing code manually on Cart URL address is changed so need to change URL
	completionAction: completionActions.default,
};
