import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { completionActions } from '../../../utils';
import { sessionTimer } from '../../lib';

const BASE_URL = 'https://www.neutrogena.com';
const REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-Neutrogena-Site/default`;
/**
 * Driver behavior description:
 -    Existing code: yes
 -    Remove code: yes
 -    Stackable: yes
 -    Free gift support: no
 -    ‘isValid’ implemented: yes
 Optional:
 -    Show message about free shipping on the cart page (without discount info): no
 -    Is driver SPA and add the condition to show popup: no
 -	  sessionTimer 15 min
 -	  run only on the cart page
 */
const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;
		this.REAPPLY_CODES_WHILE_STACKING = true;

		this.APPLY_CODE_URL = `${REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = `${REQUEST_URL}/COBilling-UpdateSummary`;
		this.CART_PAGE_URL = `${BASE_URL}/cart`;

		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODES_SELECTOR = '.cartcoupon .value';

		this.applyQueryParams.set('format', 'ajax');

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}

	beforeApplyCodes() {
		// remove codes apply message to avoid duplicates
		[...document.querySelectorAll(this.CODE_APPLY_MSG_SELECTOR)].map(msg => msg.remove());
		return Promise.resolve();
	}
};

export default {
	name: 'Neutrogena',
	domain: 'neutrogena.com',
	stackable: true,
	Driver,
	completionAction: completionActions.revisit,
	isCartEmpty() {
		const emptyCartMsg = document.querySelector('.cart-empty');
		return emptyCartMsg && emptyCartMsg.offsetParent;
	},
	isOnCartUrl(href) {
		return href.toLowerCase().startsWith(`${BASE_URL}/cart`);
	},
};
