import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/* Behavior description:
 * - can get existing codes
 * - can remove codes
 * - cannot apply invalid code
 *
 * =============== Special case ============
 * cannot support pages of payment and review steps due to loss of users data
 */

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.APPLIED_CODE_SELECTOR = '#success-coupon-code strong';

		this.CART_PAGE_URL = 'https://www.ralphlauren.com/cart';
		this.SHIPPING_VALUE_SELECTOR = '.order-shipping td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount td:last-child';

		this.isStackable = true;

		const BASE_URL = 'https://www.ralphlauren.com/on/demandware.store/Sites-RalphLauren_US-Site/default';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-DeleteCouponCode`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;

		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'couponcode';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(elem => elem.innerText.trim());

		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		// Tax is present at the end of checkout page
		return this.getCartPage().then(responseHTML => {
			const shippingValue = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML));
			const isShippingValue = Number.isNaN(shippingValue) ? 0 : shippingValue;
			const shippingDiscountElem = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, responseHTML);
			const shippingDiscount = shippingDiscountElem ? elemToFloat(shippingDiscountElem) : 0;
			const isShippingDiscount = Number.isNaN(shippingDiscount) ? 0 : shippingDiscount;
			const totalValue = this.parseTotal(responseHTML);
			return totalValue - isShippingValue + isShippingDiscount;
		});
	}
};

export default {
	name: 'Ralph Lauren',
	domain: 'ralphlauren.com',
	stackable: true,
	Driver,
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	isOnCartUrl(href) {
		return (
			document.querySelector('#coupon-code-accordion') &&
			(href.startsWith('https://www.ralphlauren.com/cart') ||
				href.startsWith('https://www.ralphlauren.com/checkoutshipping') ||
				href.startsWith('https://www.ralphlauren.com/billing') ||
				href.startsWith('https://www.ralphlauren.com/revieworder') ||
				href.includes('COBilling-Billing'))
		);
	},
};
