import DemandwareAJAXDriver from './_base';
import { elemToFloat } from '../../../utils/format';
import { extractElement } from '../../../utils/DOM';

/* Driver behavior description:
•    Existing code: yes
•    Remove code: yes
•    Stackable: yes
•    Free gift support: yes
•    ‘isValid’ implemented: yes
•    Manually apply code: no
•    Pages, where extension works: Cart
Optional:
•    Show message about free shipping on the cart page (without discount info): no
•    Is driver SPA and add the condition to show popup: no
•    Gather codes on site pages: no */

const BASE_URL = 'https://www.elizabetharden.com/on/demandware.store/Sites-ElizabethArden-Site/default';
const CART_PAGE_URL = `${BASE_URL}/Cart-Show`;
const SECOND_CART_URL = `${BASE_URL}/Cart-AddToWishlist`;

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/COBilling-RemoveCouponJson`;
		this.UPDATE_REQUEST_URL = CART_PAGE_URL;

		this.applyQueryParams.set('format', 'ajax');

		this.CART_PAGE_URL = CART_PAGE_URL;

		this.isStackable = true;

		this.PRODUCT_ITEM_SELECTOR = '.cart-row .item-total, .checkout-mini-cart .mini-cart-product';
		this.SHIPPING_SELECTOR = '.order-shipping td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax td:last-child';

		this.CART_MERCHANT_CODES_ELEM_SELECTOR = '[class="header-promotion"]';
		this.MERCHANT_CODES_REGEX = /Code: (\w+)/i;
		this.MAIN_PAGE_MERCHANT_CODES_ELEM_SELECTOR = '.promoCode-line';
	}

	getMerchantCodes() {
		return fetch('https://www.elizabetharden.com/special-offers/', {
			credentials: 'include',
		})
			.then(res => res.text())
			.then(responseHTML => {
				const cartPageCodesElem = [...document.querySelectorAll(this.CART_MERCHANT_CODES_ELEM_SELECTOR)].find(
					el => el.offsetParent && el.textContent.match(this.MERCHANT_CODES_REGEX),
				);
				const cartCode = cartPageCodesElem
					? [cartPageCodesElem.textContent.match(this.MERCHANT_CODES_REGEX)[1]]
					: [];

				const mainPageCodes = [
					...extractElement(this.MAIN_PAGE_MERCHANT_CODES_ELEM_SELECTOR, responseHTML, true),
				]
					.map(el => el.textContent.trim().match(this.MERCHANT_CODES_REGEX))
					.filter(Boolean)
					.map(el => el[1]);

				return [...new Set([...mainPageCodes, ...cartCode])];
			});
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			return this.parseTotal(response) - tax - shipping;
		});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'Elizabeth Arden',
	domain: 'elizabetharden.com',
	stackable: true,
	Driver,
	enableFreeGiftsWarning: true,
	isOnCartUrl(href) {
		return (
			href.startsWith(CART_PAGE_URL) ||
			href.startsWith(SECOND_CART_URL) ||
			document.querySelector('#dwfrm_cart_couponCode')
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
};
