import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { DOM } from '../../../utils';

const { extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes (also caught valid, but not working codes)
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
	- Cart page #1
	- Cart page #2
	- Checkout page

+ Is driver SPA no
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
	- Some codes can replace one another. After deleting the last applied code, the previous one may appear on the page.
	- Revisit cannot be used here because of reloading a wrong page.

+ Credentials (for signing in)
	- bodyanets@gmail.com / Temporary_1
*/

const BASE_URL = 'https://www.peterthomasroth.com';
const BASE_REQUEST_URL = `${BASE_URL}/on/demandware.store/Sites-PeterThomasRoth-Site/en_US`;
const CHECKOUT_URL = `${BASE_REQUEST_URL}/COShipping-SingleShipping`;
const CART_URL_1 = `${BASE_URL}/cart`;
const CART_URL_2 = `${BASE_REQUEST_URL}/Cart-SubmitForm`;
const WISH_LIST_URL = `${BASE_URL}/on/demandware.store/Sites-PeterThomasRoth-Site/en_US/Cart-AddToWishlist`;

const CODE_INPUT_SELECTOR = '#dwfrm_cart_couponCode, #dwfrm_billing_couponCode';

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();

		this.APPLY_CODE_URL = `${BASE_REQUEST_URL}/Cart-AddCouponJson`;
		this.UPDATE_REQUEST_URL = CART_URL_1;
		this.CART_PAGE_URL = CART_URL_1;
		this.isOnCheckout = window.location.href.startsWith(CHECKOUT_URL);

		this.APPLY_RESPONSE_KEY = 'status';
		this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE = 'APPLIED';

		this.PRODUCT_ITEM_SELECTOR = '.cart-row';
		this.TOTAL_SELECTOR = '.order-value';

		this.CODE_VALUE_SELECTOR = '.cartcoupon .value';
		this.APPLIED_CODE_REGEXP = /[\w]+/;
		this.CODES_ELEMENTS_SELECTOR = 'div.item-details';
		this.REMOVE_CODE_SELECTOR = '.item-quantity button';
		this.ERROR_MESSAGE_SELECTOR = '.error';

		this.applyQueryParams = new URLSearchParams({
			format: 'ajax',
		});

		this.isStackable = true;
		this.firstResponse = null;
	}

	beforeTestCodes() {
		return (this.isOnCheckout ? this.getOrderSummary() : Promise.resolve(null)).then(responseHTML => {
			this.existingCodes = [];
			this.firstResponse = responseHTML;
			const currentCodes = this._getExistingCodes(responseHTML);

			if (currentCodes.length) {
				return this._removeCodes(currentCodes);
			}

			return Promise.resolve();
		});
	}

	getExistingCodes() {
		return {
			codes: this.existingCodes,
			codeCount: this.existingCodes.length,
		};
	}

	removeCodes() {
		return Promise.resolve();
	}

	beforeCheckCodes() {
		return {
			originalPrice: this.parseTotal(this.firstResponse),
			originalItemsCount: this.parseItemsCount(this.firstResponse),
		};
	}

	_getExistingCodes(data) {
		const codes = [...extractElement(this.CODE_VALUE_SELECTOR, data, true)].map(
			item => item.textContent.match(this.APPLIED_CODE_REGEXP)[0],
		);

		return codes;
	}

	_removeCodes(codes) {
		return Promise.each(codes, code => this.removeCode(code))
			.then(() => this.getOrderSummary())
			.then(responseHTML => {
				this.existingCodes = [...this.existingCodes, ...codes];
				const currentCodes = this._getExistingCodes(responseHTML);
				if (!currentCodes.length) {
					this.firstResponse = responseHTML;
					return Promise.resolve();
				}

				return this._removeCodes(currentCodes);
			});
	}

	removeCode(code) {
		return this.removeCodeOnCart(code);
	}
};

export default {
	name: 'PeterThomasRoth',
	domain: 'peterthomasroth.com',
	stackable: true,
	Driver,
	isOnCartUrl(href) {
		return (
			(href.startsWith(CART_URL_1) ||
				href.startsWith(CART_URL_2) ||
				href.startsWith(CHECKOUT_URL) ||
				href.startsWith(WISH_LIST_URL)) &&
			document.querySelector(CODE_INPUT_SELECTOR)
		);
	},
};
