import Promise from 'bluebird';
import DemandwareAJAXDriver from './_base';
import { completionActions, log } from '../../../utils';
import bus from '../../bus';

const US_CA = window.location.href.includes('/en-ca/') ? 'en-ca' : 'en-us';
const CART_URL = `https://www.sonos.com/${US_CA}/cart`;

// Driver behavior description:
// • Existing code: yes
// • Remove code: yes
// • Stackable: yes
// • Free gift support: yes
// • ‘isValid’ implemented: yes
// • Manually apply code: no

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.isStackable = true;

		this.TOTAL_SELECTOR = '.order-total .order-value';
		this.PRODUCT_ITEM_SELECTOR = '.cart-item';

		this.US_CA = US_CA === 'en-ca' ? 'Sites-Sonos_CA-Site/en_CA' : 'Sites-Sonos_US-Site/en_US';
		const BASE_URL = `https://www.sonos.com/on/demandware.store/${this.US_CA}/`;

		this.APPLY_CODE_URL = `${BASE_URL}RapidCheckout-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}COBilling-RemoveCoupon`;
		this.UPDATE_REQUEST_URL = CART_URL;
		this.REMOVE_CODE_PARAM_NAME = 'couponCode';

		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams.set('format', 'ajax');
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this.applyCode(code).then(applyCodeResponse => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					freeGifts: 0,
				};

				const successState = this._getValueFromJSON(applyCodeResponse);

				if (
					(successState !== null && successState !== this.APPLY_RESPONSE_COMPARE_SUCCESS_VALUE) ||
					applyCodeResponse.message.includes('cannot currently be added')
				) {
					// Code was not applied
					result.isValid = false;
					// need remove not valid codes
					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				}

				return this.getOrderSummary().then(orderSummary => {
					result.finalPrice = this.parseTotal(orderSummary);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this.parseItemsCount(orderSummary) - originalItemsCount;

					if (
						result.finalPrice < originalPrice ||
						// Free gifts code is acceptable if it won't increase total
						(result.finalPrice === originalPrice && result.freeGifts > 0)
					) {
						workingDiscounts.push(result);
					}

					return this.removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.REMOVE_CODE_SELECTOR)].map(el => el.dataset.couponCode);
		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Sonos',
	domain: 'sonos.com',
	stackable: true,
	completionAction: completionActions.revisit,
	Driver,
	isOnCartUrl(href) {
		return (
			href.includes(`/cart`) &&
			(window.location.href.includes('/en-ca/') || window.location.href.includes('/en-us/'))
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('.cart-empty'));
	},
	showDifferentDiscountMessage: true,
};
