import DemandwareAJAXDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;

const DOMAIN = 'https://www.clubmonaco.com';
const BASE_URL = `${DOMAIN}/on/demandware.store/Sites-ClubMonaco_US-Site/en_US`;
const CHECKOUT_PAGE_URL = `${DOMAIN}/en/billing`;
const REVIEW_PAGE_URL = `${BASE_URL}/COBilling-Billing`;
const isOnReview = () => window.location.href.includes(REVIEW_PAGE_URL);
const CART_PAGE_URL = `${DOMAIN}/en/cart`;
const isOnCart = window.location.href.includes(CART_PAGE_URL);

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: yes
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Replace code: yes
+ Manually apply code: no
+ Pages, where extension works: 
	- Cart
	- Checkout(shipping and payment steps)
+ Is SPA: yes
+ Gather codes on site pages: no
*/

const Driver = class Driver extends DemandwareAJAXDriver {
	constructor() {
		super();
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.APPLIED_CODES_SELECTOR = '#success-coupon-code strong';
		this.PRODUCT_ITEM_SELECTOR = isOnReview() ? '.cart-row' : '.mini-cart-product';
		this.APPLY_CODE_URL = `${BASE_URL}/Cart-AddCouponJson`;
		this.REMOVE_CODE_URL = `${BASE_URL}/Cart-DeleteCouponCode`;
		this.UPDATE_REQUEST_URL = `${BASE_URL}/COBilling-UpdateSummary`;
		this.APPLY_CODE_PARAM_NAME = 'couponCode';
		this.REMOVE_CODE_PARAM_NAME = 'couponcode';
		this.applyQueryParams.set('format', 'ajax');
		this.removeQueryParams = this.applyQueryParams;
		this.SHIPPING_SELECTOR = '.order-shipping > td:last-child';
		this.TAX_SELECTOR = '.order-sales-tax > td:last-child';
		this.SHIPPING_DISCOUNT_SELECTOR = '.order-shipping-discount > td:last-child';
		this.SPINNER_SELECTOR = '.loader-indicator';
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightUrl = isOnCart || href.includes(CHECKOUT_PAGE_URL) || href.includes(REVIEW_PAGE_URL);
		const isSpinner = document.querySelector(this.SPINNER_SELECTOR);
		const isItemInCart = document.querySelector(this.PRODUCT_ITEM_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		return isOnRightUrl && !isSpinner && isItemInCart && isTotal;
	}

	removeCode(code) {
		if (isOnCart) {
			return this.removeCodeOnCart(code);
		}
		this.removeQueryParams.set(this.REMOVE_CODE_PARAM_NAME, code);
		return this._sendRequestGET(this.REMOVE_CODE_URL, this.REMOVE_REQUEST_HEADERS, this.removeQueryParams);
	}

	getPreTaxShippingTotal() {
		return this.getOrderSummary().then(response => {
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			const shippingDiscountEl = extractElement(this.SHIPPING_DISCOUNT_SELECTOR, response);
			const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			return this.parseTotal(response) - shipping - tax + shippingDiscount;
		});
	}
};

export default {
	name: 'Club Monaco',
	domain: 'clubmonaco.com',
	Driver,
	isSinglePageApp: true,
};
